
require('components/common/table-data/table-data');
const saveAs = require('src/common/lib/FileSaver');
const pagination = require('components/base/pagination/pagination');
const calendar = require('components/common/data-calendar/data-calendar');

const ajaxHttp = require('appUtil/ajaxHttp');
const { apiPath } = require('appService/api');
const store = require('appVuex/store');

const template = require('./order-list.tpl');

require('./order-list.scss');

const OrderList = {
	name:"order-list",
	template,
	data() {
		return {
			thItems: ["序号","订单号","充电站名称","枪头ID", "订单时长", "订单来源","订单状态","创建时间","操作"],
			displayItems:["idx","orderId", "stationName","showId", "duration", "channelId","status","createTime","operate"],
			trItems:[],
			url: apiPath.order.orderList,
			exportUrl: apiPath.order.exportCsv,
			pageItems:{},
			pageInitCt:1,

			ajaxArgs:"",
			argObj:{
				size: 20,
				current: 1
			},
			baseArgObj:{
				size:20,
				current:1
			},

			statusList: {
				"200": "充电中",
				"300":"充电完成",
				"800": "充电结束",
				"-40": "电桩上报超时",
				"-20": "连接超时",
				"-10": "打开电闸超时",
				"0": "订单未激活",
				"2000": "钱已到账",
				"-500": "硬件故障",
				"100": "电闸打开等待连接"
			},
			channeList: {
				"0":"微信",
				"1": "APP",
				"3": "充电卡",
				"4": "桩主"
			},
			searchTypeText: "按订单号",
			searchVal: "",
			searchName: "orderId",
			beginTime: "",
			endTime: "",
			channelId:"",
			status: "",
			calendar:{
                show:false,
                x:0,
                y:0,
                picker:"",
                type:"date",
                value:"",
                sep:"-",
                range:false,
                end:'',
                begin:'',
                items:{
                    beginTime:{
                        type:"date",
                        value:"",
                        end:new Date().getFullYear()+'-'+(1+new Date().getMonth())+'-'+new Date().getDate(),
               			begin:'',
                        sep:"-"
                    },
                    endTime:{
                        type:"date",
                        value:"",
                        end:new Date().getFullYear()+'-'+(1+new Date().getMonth())+'-'+new Date().getDate(),
                		begin:'',
                        sep:"-"
                    }
                }
            }
		}
	},
	ready(){

      	let paramsObj = this.$route.query, _obj={};
      	for(let k in paramsObj){
      		this.searchName = k;
      		this.searchVal = paramsObj[k];


      	}
      	let _initArg = Object.assign({},this.baseArgObj,paramsObj);

		this.ajaxArgs = this.serializeArg(_initArg);
	},
	route: {
	    data: (transition) => {
	      document.title = '欢迎您使用充电网私有云服务系统！'
	      transition.next()
	    }
	},
	watch:{
		'ajaxArgs': function(val,oldVal){
			this.getDataList(val);
		},
		'argObj': function(val, oldVal){
			this.ajaxArgs = this.serializeArg(val);
		},
		'status':function (val, oldVal) {
				this.argObj = Object.assign({}, this.baseArgObj, this.argObj,{'status': val } );
		},
		'channelId':function (val, oldVal){
				this.argObj = Object.assign({}, this.baseArgObj,this.argObj, {'channelId': val } );
		},
    'calendar.value': function (value) {
        this.calendar.items[this.calendar.picker].value=value;
    }

	},
	components:{
		pagination,
		calendar
	},
	methods: {
		searchType: function(e){
			this.searchTypeText = $(e.target).text();
			this.searchName = $(e.target).attr("searchName");
		},

		searchData: function(e){
			let errTips = this.searchTypeText.substring(1);
			/*if(this.searchVal === ''){
				alert(`请输入${errTips}进行搜索`);
				return;
			}*/

			let _searchName = {};
			_searchName[this.searchName] = this.searchVal;
			//_searchName[]
			this.argObj = Object.assign({}, this.baseArgObj, _searchName,{
				'beginTime': this.calendar.items["beginTime"].value ? this.calendar.items["beginTime"].value : this.beginTime,
				'endTime': this.calendar.items["endTime"].value ? this.calendar.items["endTime"].value: this.endTime,
				 'channelId': this.channelId,
				 'status': this.status
			} );
		},

		getDataList:function(args =''){

		        ajaxHttp(this.url, {
		       	    type : "get",
		       	    data : args

		        }).then((res) => {

		       	    let rData = res.data;

		          	let recordAr = rData.records;
		          	for(let i=0; i< recordAr.length; i++){
		          		let  currentObj = recordAr[i],
		          			currentPage = rData["current"];
		          		currentObj["idx"] = currentPage < 2? (i+1) : (currentPage-1) * 20 + (i+1);
		          		currentObj["createTime"] = this.formatTime(currentObj["createTime"]);
		          		currentObj["status"] = this.statusList[currentObj["status"]];
		          		currentObj["channelId"] = this.channeList[currentObj["channelId"]];
		          		currentObj["operate"] = `<a href='#!/page/order/orderlists/orderdetail?orderid=${currentObj["orderId"]}'>查看</a>`;
		          	}

		          	this.trItems = recordAr;
		          	if(this.pageInitCt==1){
		          		this.pageInitCt = 0;
		          		this.pageItems = rData;
		          	}
		       	}).catch((rtn) => {
		       	    console.warn('获取失败');
		       	})
		},
		formatTime: function(time){
			let dTime = new Date(time),
				dMinutes = dTime.getMinutes();
			dMinutes = dMinutes< 10 ? "0"+ dMinutes : dMinutes;
			return `${dTime.getFullYear()}-${dTime.getMonth()+1}-${dTime.getDate()}  ${dTime.getHours()}:${dMinutes}`;
		},
		serializeArg: function(args){
			let serialStr = '';
			for(let i in args){
				serialStr += `&${i}=${args[i]}`;
			}
			return serialStr.substring(1);
		},
		exportCsv:function(e){

			e.target.setAttribute("href",`${this.exportUrl}?${this.ajaxArgs}&token=${window.localStorage.TOKEN}`);
			return false;
		},
		exportCsv2:function(){

			let csvData = [];
			//列的标头
			let titleArr = this.thItems.slice(0);
				titleArr.pop();
				titleArr.shift();

			//索引值用来遍历对象
			let keyFor = this.displayItems.slice(0);
				keyFor.pop();
				keyFor.shift();

			csvData.push(titleArr);

			for(let i=0; i<this.trItems.length; i++){
				let _arr = [];
				for(let j=0; j< keyFor.length; j++){
					_arr.push(this.trItems[i][keyFor[j]]);
				}
				csvData.push(_arr);
			}

			let emtAr = [];
			for(let k=0; k<csvData.length; k++){
				emtAr.push(csvData[k].join(",") + "\n");
			}

		   let exportContent = "\uFEFF";
	       let blob = new Blob([exportContent+emtAr.join("")],{type: "text/plain;charset=utf-8"});
	       let fileName = "订单列表("+ this.formatTime(new Date().getTime()).replace(/:/g,'：') +").csv";
	       saveAs(blob, fileName);

		},
		open(e,type) {
            // 设置类型
            this.calendar.picker=type
            this.calendar.type=this.calendar.items[type].type
            this.calendar.value=this.calendar.items[type].value
            // 可不用写
            this.calendar.sep=this.calendar.items[type].sep

            this.calendar.show=true
            this.calendar.x=e.target.offsetLeft
            this.calendar.y=e.target.offsetTop+e.target.offsetHeight+8;
            if(type==="beginTime"){
            	this.calendar.end=this.calendar.items.endTime.value || this.calendar.items.endTime.end;;
            	this.calendar.begin="";
            }else{
            	this.calendar.begin=this.calendar.items.beginTime.value;
            	this.calendar.end=this.calendar.items.endTime.end;
            }

            this.$refs.dropmenu.fold(); //收起下拉框
            this.$refs.dropmenu1.fold(); //收起下拉框

        }

	},
	events:{
		'switchPage': function(page){
			this.argObj = Object.assign({}, this.argObj, {'current': page } );
		},
		dropMenuChange: function(val){
			this.calendar.show = false; //隐藏日历

			if (val.dispatcher.queryName == "type"){
				if (val.optionItem){
					let status = val.optionItem.value;

					this.status = (status == "-1") ? "" : status;

				}
			}
			else{
				if (val.optionItem){

					let channelId = val.optionItem.value;
					if ( channelId == "-1" ){
						this.channelId = "";
					} else{
						this.channelId = val.optionItem.value;
					}
					this.searchData();
				}
			}
		}
	},
	components:{
		calendar
	},
	store

}

module.exports = OrderList;


