require('components/common/table-data/table-data');

const { apiPath } = require('appService/api');

require('./order-list.scss');
const template = require('./order-list.tpl');

const orderList = {
	name: "order-list",

	template,


	data() {
    return {
    	thItems: ["序号", "订单号", "业务", "订单来源", "支付方式", "应付金额（元）",
        "支付时间", "支付状态"],
      url: apiPath.client.orderList,
      queryOpt: {
	    	uid: this.$route.query.uid,
	    	_index: 1,
	    	size: 10
	    },
	    thLength: 8
    }
	},
	ready() {

	},

	methods: {

	}
}

module.exports = orderList;



