const store = require('appVuex/store');
const { apiPath } = require('appService/api');
const { chargingTemplate } = require('appVuex/action/marketing');

require('./charging-template-detail.scss');
const template = require('./charging-template-detail.tpl');

const ChargingTemplateDetail = {
  name: "charging-template-detail",

  template,

  data() {
    return {
      baseId: ''
    }
  },

  route: {
    data(transition) {
      this.getDetail(transition.to.params);
      document.title = '欢迎您使用充电网私有云服务系统！';
      let params = transition.to.params;
      this.baseId = params.id;
      transition.next()
    }
  },

  vuex: {
    getters: {
      detail: (state) => state.marketing.chargingTemplate.detail
    },
    actions: {
      getDetail: chargingTemplate.detail.get
    }
  },

  store,

  methods: {
    _init() {
      let name = this.$route.query.name;
      var arr = [{
        name: name,
        router: `/page/marketing/charging-template/version/${ this.baseId }?name=${ name }`
      }, {
        name: "计费模板详情"
      }];

      COMMON.componentHub.breadCrumb = arr;
    }
  },

  ready() {
      this._init();
  },
  filters: {
    timeShareType(val) {
      return ['', '尖峰', '高峰', '平峰', '低谷'][val];
    },
    billWay(val) {
      return val == 2 ? '免费' : '收费';
    }
  }
}

module.exports = ChargingTemplateDetail;



