const store = require('appVuex/store');
const ajax = require('appUtil/ajax');
const { apiPath } = require('appService/api');
const { setBreadCrumb } = require('src/common/vuex/action/common');
const { chargingTemplate } = require('appVuex/action/marketing');

require('./charging-template-version.scss');
const template = require('./charging-template-version.tpl');

const ChargingTemplateVersion = {
  name: "charging-template-version",

  template,

  data() {
    return {
      thItems: ["模板名称", "版本号", "充电费", "服务费", "生效日期", "失效日期", "是否有效", "创建日期"],
      trItems: [],
      baseId: '',
      queryOpt: {
        _size: 20
      }
    }
  },

  route: {
    data(transition) {
      document.title = '欢迎您使用充电网私有云服务系统！';
      let params = transition.to.params;
      this.baseId = params.id;
      this.getVersions(params);
      transition.next()
    }
  },

  vuex: {
    actions: {
      setBreadCrumb,
      getVersions({ dispatch, state }, queryOpt) {
        ajax('get', apiPath.marketing.chargingTemplateVersionList).
          query(queryOpt).
          end((err, res) => {
            if (res.body.code === 0) {
              this.trItems = res.body.data;
            } else {
              return dispatch('ERROR_INFO', res.body)
            }
          });
      }
    }
  },

  store,

  methods: {
    _init() {
      var arr = [{
        name: this.$route.query.name
      }];
      // this.setBreadCrumb(arr);
      COMMON.componentHub.breadCrumb = arr;
    }
  },

  ready() {
      this._init();
    //this.tableDataAction(this.$route.query)
  }
}

module.exports = ChargingTemplateVersion;



