const template = require('./monitor.tpl');
require('./monitor.scss');
require('appUtil/ajaxSetting');
const { apiPath } = require('appService/api');
const calendar = require('components/common/data-calendar/data-calendar');
const Pagination = require('components/base/pagination/pagination');
const Monitor = {
	template,
	data(){
		return{
			datas:{},
			baseUrl:apiPath.site.monitor,
			jump:null,
			codeId:'370639906524897297',
//			siteUrl:'http://192.168.60.34:8080/chargerlink-site-web',
			calendar:{
                show:false,
                x:0,
                y:0,
                picker:"",
                type:"date",
                value:"",
                sep:"-",
                range:false,
                items:{
                    picker1:{
                        type:"date",
                        value:'',
                    }
                }
            }
		}
	},
	ready(){
		this.postAjax({chargerCode:this.codeId});
	},
	methods:{
		postAjax(a){
			$.ajax({
				type:'post',
				url:this.baseUrl.history,
				dataType:'json',
				data:a,
				success:(data)=>{
					this.datas = data.data
					console.log(data)
				}
			})
		},
		jumpCurrent(){
			this.postAjax({current:this.jump,chargerCode:this.codeId});
		},
		Status(a){
			if (a==10) {
				return `<p style="color: #90ee90;">空闲</p>`;
			}else if(a>12){
				return `<p style="color: #000;">占用</p>`;
			}else if(a==0){
				return `<p style="color: #ff0000;">离线</p>`;
			}else if (a==-300||a==-200) {
				return`<p style="color: #ff0000;">故障</p>`;
			}
		},
		open(e,type) {
            // 设置类型
            this.calendar.picker=type
            this.calendar.type=this.calendar.items[type].type
            this.calendar.range=this.calendar.items[type].range
            this.calendar.begin=this.calendar.items[type].begin
            this.calendar.end=this.calendar.items[type].end
            this.calendar.value=this.calendar.items[type].value
            // 可不用写
            this.calendar.sep=this.calendar.items[type].sep
            this.calendar.weeks=this.calendar.items[type].weeks
            this.calendar.months=this.calendar.items[type].months

            this.calendar.show=true
            this.calendar.x=e.target.offsetLeft
            this.calendar.y=e.target.offsetTop+e.target.offsetHeight+8
        }
	},
	components: {
		Pagination,
		calendar
	},
	watch:{
		'calendar.value': function (value) {
            this.calendar.items[this.calendar.picker].value=value;
        }
	}
}
module.exports = Monitor;


