

const calendar = require('components/common/data-calendar/data-calendar');
const tip = require('components/base/pop/tip');

const { setBreadCrumb } = require('src/common/vuex/action/common');
const store = require('appVuex/store');
const ajaxHttp = require('appUtil/ajaxHttp');
const { apiPath } = require('appService/api');

const echarts = require('echarts');
const template = require('./order-survey.tpl');

require('./order-survey.scss');

const OrderSurvey = {
	name:"order-survey",
	template,
	data() {
		return {
			surveyDetail: apiPath.order.surveyDetail,
			surveyGui: apiPath.order.surveyGui,
			ajaxArgs:{
				endTime:this.formatDay(0)
			},
			echartObj:{},
			statistics:{},
			voltage:{},
			chartDataList:{
			    tooltip : {
			        trigger: 'axis',
			        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
			            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
			        }
			    },
			    grid: {
			        left: '3%',
			        right: '4%',
			        bottom: '3%',
			        containLabel: true
			    },
			    xAxis : [
			        {
			            type : 'category',
			            data : ['全部订单', '已支付订单', '待支付订单', '异常订单'],
			            axisTick: {
			                alignWithLabel: true
			            }
			        }
			    ],
			    yAxis : [
			        {
			            type : 'value'
			        }
			    ],
			    series : [
			        {
			            name:'订单条数',
			            type:'bar',
			            barWidth: '40%',
			            data:[
				            {value:200,itemStyle:{normal:{color:'#4e68b1'}}},
				            {value:100,itemStyle:{normal:{color:'#00c5de'}}},
				            {value:50,itemStyle:{normal:{color:'#00c5de'}}},
				            {value:40,itemStyle:{normal:{color:'#00c5de'}}}
			            ]
			        }
			    ]
			},
			chartOptions:{
				barValueSpacing : 20
			},
			cls:"",
			calendar:{
                show:false,
                x:0,
                y:0,
                picker:"",
                type:"date",
                value:"",
                sep:"-",
                range:false,
                end:'',
                begin:'',
                items:{
                    beginTime:{
                        type:"date",
                        value:"",
                        end:new Date().getFullYear()+'-'+(1+new Date().getMonth())+'-'+new Date().getDate(),
               			begin:'',
                        sep:"-"
                    },
                    endTime:{
                        type:"date",
                        value:"",
                        end:new Date().getFullYear()+'-'+(1+new Date().getMonth())+'-'+new Date().getDate(),
                		begin:'',
                        sep:"-"
                    }
                }
            }

		}
	},
	ready(){

	  this.echartObj = echarts.init(document.getElementById('echart'));
    this.getOrderSruvey();

	},
	watch:{
		'calendar.value': function (value) {
			this.calendar.items[this.calendar.picker].value=value;

			if(this.calendar.items.endTime.value !=="" && this.calendar.items.beginTime.value !==""){
				this.ajaxArgs = Object.assign({}, this.ajaxArgs,{
					beginTime:this.calendar.items.beginTime.value,
					endTime:this.calendar.items.endTime.value
				});
			}
     },
     'ajaxArgs': function(val, oldVal){
     	 this.getSurveyGui();
     }

	},
	components:{
		calendar
	},
	methods: {

		getOrderSruvey:function(args =''){
			let that = this;
			ajaxHttp(this.surveyDetail,{
				type:'get',
				data:args
			})
			.then( res => {
				let rData = res.data;
		    that.statistics = Object.assign({},that.statistics, rData.statistics);
				that.initSurveyGui(rData.chart);
			})
			.catch(err => {
				tip(err.message);
			})

		},
		getSurveyGui: function(){
			let that = this;
			ajaxHttp(this.surveyGui,{
				type:'get',
				data:this.ajaxArgs
			})
			.then( res => {
				that.initSurveyGui(res.data);
				if(that.cls!==""){
					that.voltage[that.cls] = res.data;
				}
			})
			.catch( err =>{
				tip(err.message);
			})

		},
		initSurveyGui: function(dataList){

			let _list = dataList;
			let _xAr = [];
			let  _series = [];
			for(let item of _list){
				_xAr.push(item['name']);
				_series.push(item['value']);
			}
			this.chartDataList.xAxis[0].data = _xAr;

			for(let i in _series){
				this.chartDataList.series[0].data[i].value = _series[i];
			}

			//this.voltage[cls] = this.chartDataList; //保存起来
			this.echartObj.setOption(this.chartDataList);


		},
		//天数日期转换
		formatDay: function(t,ac='-'){
			let t1 = new Date(new Date().getTime() - t*24*60*60*1000);
			return `${t1.getFullYear()}${ac}${(1+t1.getMonth())}${ac}${t1.getDate()}`;
		},

		//按月获取
		formatDayByMonth: function(t,ac='-'){
			let tgDate = new Date(),
					tgMonth = tgDate.getMonth()+1,
					tgYear = tgDate.getFullYear(),
					tgDay = tgDate.getDate();
			let t1 = Math.floor(t/12),
				  t2 = t%12,
				  t3 = tgMonth-t2;

			tgYear = tgYear - t1;
			if(t3 <= 0){
				tgYear = tgYear -1;
				tgMonth =  12 + t3;
			}else{
				tgMonth = t3;
			}
			return `${tgYear}${ac}${tgMonth}${ac}${tgDay}`;


		},
		tabNav:function(it){
			this.cls = it;
			if(this.voltage[it]){
				this.initSurveyGui(this.voltage[it]);
				return;
			}
			let sT='';
			if(it==='oM'){
				sT = this.formatDayByMonth(1);
			}else{
				sT = this.formatDay(it)
			}
			this.ajaxArgs = Object.assign({}, this.ajaxArgs, {
				beginTime:sT
			});

			//this.ajaxArgs = `beginTime=${this.formatDay(it)}&endTime=${this.formatDay(0)}`;

		},
		open(e,type) {
            // 设置类型
            this.calendar.picker=type
            this.calendar.type=this.calendar.items[type].type
            this.calendar.value=this.calendar.items[type].value
            // 可不用写
            this.calendar.sep=this.calendar.items[type].sep

            this.calendar.show=true
            this.calendar.x=e.target.offsetLeft
            this.calendar.y=e.target.offsetTop+e.target.offsetHeight+8;
            if(type==="beginTime"){
            	this.calendar.end=this.calendar.items.endTime.value || this.calendar.items.endTime.end;;
            	this.calendar.begin="";
            }else{
            	this.calendar.begin=this.calendar.items.beginTime.value;
            	this.calendar.end=this.calendar.items.endTime.end;
            }

            //this.$refs.dropmenu.fold(); //收起下拉框
            //this.$refs.dropmenu1.fold(); //收起下拉框
            // this.$refs.dropmenu1.text = "最近时间"; //初始化
        }

	},
	route: {
	    data: (transition) => {
	      document.title = '欢迎您使用充电网私有云服务系统！'
	      transition.next()
	    }
	},
	vuex: {
	    actions: {
	      setBreadCrumb
	    }
	},
	
	store

}

module.exports = OrderSurvey;


