const saasFront = require('src/saasFront/app/app');
const saasFrontRouter = require('src/saasFront/router/router');
const router = require('./router.js');
const spinner = require('src/common/utils/spinner');

router.map(saasFrontRouter);

router.beforeEach(function (transition) {
	spinner.start();

  //清空 jquery 和 bootstrap 的插件
  $('.datetimepicker').remove();

  let token = window.localStorage.getItem('TOKEN');
  if(!token && transition.to.matched[0].handler.path == '/page'){
    router.go({
      path: '/login'
    })
  }

  transition.next();
});

router.afterEach(function(transition) {
  document.title = transition.to.title ? transition.to.title : '欢迎您使用充电网私有云服务系统！';

	spinner.end();
});

router.start(saasFront, '#app');


