/**
 * pop 组件
 *
 * @props theme - 主题
 * @props type - 弹窗类型
 * @props headername - 弹窗头部名字
 * @props message - alert信息
 * @props okbtnname - 确定按钮名字
 * @props nobtnname - 取消按钮名字
 * @props okcb - 确定按钮回调函数
 * @props nocb - 取消按钮回调函数
 * @props noBtnDispaly - 取消按钮回调函数
 * @props headerNoBtnDisplay - 弹窗头部X是否显示
 * @props headerDisplay - 是否显示弹窗头部
 * @props footerDisplay - 是否显示弹窗底部
 *
 * @slot body - confirm弹窗的主体内容
 *
 */
const Vue = require('vue');
require('components/base/btn/btn')

const TYPE_ALERT = 'alert';
const TYPE_CONFIRM = 'confirm';
const TYPE_TIP = 'tip';

const baseMixin = require('components/mixin/base');

var tipShowTime = 3000;

require('./pop.scss');
require('./m.pop.scss');
var template = require('./pop.tpl');

var Pop = Vue.extend({
  name: "Pop",

  template,

  mixins: [baseMixin],

  props: {
    theme: {
      type: String,
      default: "primary"
    },
    type: {
      type: String,
      default: TYPE_CONFIRM
    },
    headername: {
      type: String,
      default: ""
    },
    okbtnname: {
      type: String,
      default: '确定'
    },
    nobtnname: {
      type: String,
      default: '取消'
    },
    message: {
      type: String,
      default: ""
    },
    headerDisplay: {
      type: Boolean,
      default: true
    },
    headerNoBtnDisplay: {
      type: Boolean,
      default: true
    },
    noBtnDisplay: {
      type: Boolean,
      default: true
    },
    footerDisplay: {
      type: Boolean,
      default: true
    },
    okcb: {
      validator: (value) => {
        return value instanceof Function;
      },
      default: function () {
        return function () {
          this.hide();
        }
      }
    },
    nocb: {
      validator: (value) => {
        return value instanceof Function;
      },
      default: () => {
        return function () {
          this.hide();
        }
      }
    }
  },

  data: () => {
    return {
      pointStart: {
        x: 0,
        y: 0
      },
      isMousedown: false,
      popDisplay: false
    }
  },

  methods: {
    /**
     * 显示pop
     *
     * @param {Number} - 当前页码
     * @return {Object}
     */
    show() {
      if (this.isTip) {
        this.popDisplay = true;
        setTimeout(() => {
          this.popDisplay = false;
        }, tipShowTime);
      } else {
        this.popDisplay = true;
      }
      return this;
    },

    /**
     * 隐藏pop
     *
     * @return {Object}
     */
    hide() {
      if (this.isTip) {
        return this;
      }
      this.popDisplay = false;

      return this;
    },

    /**
     * 鼠标mouseDown 弹窗头部触发的事件
     *
     * @return {Object}
     */
    mouseDown(event) {
      this.isMousedown = true;
      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      };

      return this;
    },

    /**
     * 鼠标mouseMove 弹窗头部触发的事件
     *
     * @return {Object, Boolean}
     */
    mouseMove(event) {
      var $this = $(this.$el);
      if (!this.isMousedown) {
        return false;
      }
      var top = parseInt($this.css('top'), 10);
      var left = parseInt($this.css('left'), 10);

      $this.css({
        'top': top + event.clientY - this.pointStart.y,
        'left': left + event.clientX - this.pointStart.x
      });
      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      };

      return this;
    },

    /**
     * 鼠标mouseUp 弹窗头部触发的事件
     *
     * @return {Object, Boolean}
     */
    mouseUp() {
      if (!this.isMousedown) {
        return false;
      }
      this.isMousedown = false;

      return this;
    },

    /**
     * 弹窗点击确定触发的函数
     *
     * @return {Object}
     */
    ok() {
      this.okcb && this.okcb();

      return this;
    },

    /**
     * 弹窗点击取消触发的函数
     *
     * @return {Object}
     */
    cancel() {
      this.nocb && this.nocb();

      return this;
    },

    /**
     * 返回弹窗的title名
     *
     * @return {Object, Boolean}
     */
    title(text) {
      if (text === '' || text) {
        this.headername = text;
        return this;
      }

      return this.headername;
    },

    /**
     * alert 弹窗的信息
     *
     * @param {String} - 需要设置的值
     * @return {Object, String}
     */
    alertInfo(text) {
      if (text === '' || text) {
        this.message = text;
        return this;
      }
      return this.message;
    },

    /**
     * alert 设置弹窗的确定按钮的回调函数
     *
     * @param {Function}
     * @return {Object}
     */
    alertOk(cb) {
      if (cb) {
        this.okcb = cb;
      }

      return this;
    }
  },

  computed: {
    isAlert() {
      return this.type === TYPE_ALERT;
    },
    isTip() {
      return this.type === TYPE_TIP;
    }
  }
});

module.exports = Vue.component('pop', Pop);


