const template = require('./update-site.tpl');
const FileUpload = require('vue-upload-component');
require('./update-site.scss');
require('components/common/AMap/AMap')
const store = require('appVuex/store');
const ajaxHttp = require('appUtil/ajaxHttp');
require('src/common/lib/jquery.timepicker/index.css');
require('src/common/lib/jquery.timepicker/index.js');
require('components/common/select-tree/select-tree');
require('components/common/select-city/select-city');
const {
  apiPath
} = require('appService/api');
const {
  setBreadCrumb
} = require('src/common/vuex/action/common');
require('appUtil/ajaxSetting');
const tip = require('components/base/pop/tip');
const UpdateSite = {
  template,
  store,
  data() {
    return {
      idNo:'',
      estateTypes: [],
      images: [],
      brands: null,
      brandIds: [],
      provincesArr: null,
      citiesArr: '',
      districtsArr: '',
      AjaxPro: '',
      AjaxCit: '',
      AjaxDis: '',
      province: '',
      city: '',
      district: '',
      beginPhone: '',
      endPhone: '',
      beginWork: '',
      endWork: '',
      beginHoliday: '',
      endHoliday: '',
      baseUrl: apiPath.site.updateSite,
      longitude: "",
      longitude1:"",
      latitude: "",
      latitude1:"",
      name:'',
      type:null,
      address:'',
      scope:null,
      specilic:[],
      park:'',
      parkFee:'',
      remark:'',
      status:'',
      phone:[],
      operateName:'',
      operateId:'',
      pic: {
        name: 'file',
        url: apiPath.site.addSite.uploadImg,
        format: /\.(png|jpg)$/i,
        size: 1024 * 1024 * 5,
        timeout: 36000,
        multiple: false,
        accept: "image/png,image/jpg"
      },
      saveBol: true,
      warnNameBol:false,
      phoneObj:[
      ],
      chargeTpyeArr:[
      {
        text:'公共',
        value:1
      },
      {
        text:'个人',
        value:2
      },{
        text:'营运',
        value:3
      }
      ],
      parkCostArr:[
      {
        text:'免费',
        value:3
      },{
        text:'未知',
        value:1
      },{
        text:'收费',
        value:2
      }
      ],
      useRangeArr:[
      {
        text:'对外开放',
        value:1
      },{
        text:'内部使用',
        value:2
      }
      ],
      appPay:null,
      appPayArr:[
      {
        text:'app支付',
        value:-1
      },{
        text:'充电网APP',
        value:1001
      },{
        text:'奥能APP',
        value:1002
      }
      ],
      wxPay:null,
      wxPayArr:[
      {
        text:'微信公众号',
        value:-1
      },{
        text:'充电网微信公众号',
        value:2001
      },{
        text:'奥能微信公众号',
        value:2002
      },{
        text:'e充网公众账号',
        value:2003
      }
      ],
      cardPay:null,
      cardPayArr:[
      {
        text:'刷卡支付',
        value:-1,
      },{
        text:'上海地铁公交卡',
        value:3001
      },{
        text:'普天充电卡',
        value:3002
      },{
        text:'充电网充电卡',
        value:3003
      },{
        text:'招行闪付卡',
        value:3004
      }
      ],
      treeData: {
      },
      pcdObj:{},
      feeDescription:"",
      feeMax:"",
      feeMin:"",
      notice:'',
      noticeBol:false,
      appointArr:[
      {
        text:'不需预约',
        value:2,
      },{
        text:'需提前预约',
        value:1,
       },{
        text:'未知',
        value:3
      }
      ],
      appoint:"",
    }
  },
  vuex: {
    actions: {
      setBreadCrumb,
    }
  },
  route: {
    data: function(transition) {
      document.title = '欢迎您使用充电网私有云服务系统！'
      transition.next();
    },
    waitForData: true
  },
  computed: {
  },
  created(){
    this.updateAjax();
  
  },
  ready() {
    this._init();
  },

  methods: {
    initData(res){
      $('#cbktime').timepicker({
      'timeFormat': 'H:i',
      'step': 1
      });
      $('#cbktime2').timepicker({
        'timeFormat': 'H:i',
        'step': 1
      });
      $('#cbktime3').timepicker({
        'timeFormat': 'H:i',
        'step': 1
      });
      $('#cbktime4').timepicker({
        'timeFormat': 'H:i',
        'step': 1
      });
      let arr = ["idNo","name","type","address","scope","specilic","park","parkFee","remark","status","operateName","operateId","brandIds","phone","province","city","area","feeDescription","feeMin","feeMax","appPay","wxPay","cardPay","appoint","notice"];
      for(let i in arr){
        this[arr[i]] = res[arr[i]];
      }
    },
    initMap(map){
    },
    checkName(){
      ajaxHttp(this.baseUrl.checkName,{
        data:{
          name:this.name,
          token:window.SAAS.TOKEN,
          id:this.$route.query.id
        }
      }).then((res)=>{
        if(res.code==0){
          if (res.data.hasRepeat==false) {
              this.warnNameBol = true;
          }else{
            this.warnNameBol = false;
          }
        }
      }).catch(res=>{
        tip(res.message);
      })
    },
    cancel() {
      window.history.back();
    },
    _init() {
      let arr = [{
        name: `${this.$route.query.head}`,
        router: `${this.handlerUrl(this.$route.path.split('?')[0])}?id=${this.$route.query.id}&head=${this.$route.query.head}`
      }, {
        name: "编辑"
      }];
      COMMON.componentHub.breadCrumb = arr;
    },
    handlerUrl(url) {
      return url.replace('/updatesite', '');
    },
    selectBrands(a) {
        for(let i in this.brandIds) {
          if(this.brandIds[i] == a) {
            return true;
          }
        }
    },
    deleteImg(a) {
      this.images.splice(a, 1);
    },
    updateAjax() {
      ajaxHttp(this.baseUrl.selectBrand,{
      }).then((res)=>{
        if(res.code==0) this.brands = res.data;
      }).catch(res=>{
        tip(res.message);
      })
      ajaxHttp(this.baseUrl.findOne,{
        data:{
          id: this.$route.query.id
        }
      }).then((res)=>{
        if(res.code==0){
          let resData = res.data;
          this.initData(resData);
          ajaxHttp(apiPath.site.updateSite.getCommList,{
            data:{
              groupid:1}
            }).then((res)=>{
            if(res.code==0){
              this.treeData = res.data;
              this.operateId = this.treeData.commId;
              this.$refs.selecttree.txt = this.operateName;
            }
          }).catch(res=>{
            tip(res.message);
          })
          for(let i in resData.estateTypes) {
            this.estateTypes.push(resData.estateTypes[i].toString());
          }
          this.ifyPhone()
          this.changeAppInit("appPayArr","appPay");
          this.changeAppInit("wxPayArr","wxPay");
          this.changeAppInit("cardPayArr","cardPay");
          this.changeAppInit("appointArr","appoint");
          this.images = resData.images.split(",");
          this.changeMenuText('chargeTpyeArr','type');
          this.changeMenuText('useRangeArr','scope')
          this.changeMenuText('parkCostArr','park')
          this.longitude = resData.longitude;
          this.longitude1 = this.longitude.toFixed(6);
          this.latitude = resData.latitude.toFixed(6);
          this.latitude1 = this.latitude;
          this.beginWork = resData.serviceWorkdayTime.split("-")[0];
          this.endWork = resData.serviceWorkdayTime.split("-")[1];
          this.beginHoliday = resData.serviceHolidayTime.split("-")[0];
          this.endHoliday = resData.serviceHolidayTime.split("-")[1];
          ajaxHttp(this.baseUrl.selectPcd,{
          }).then((res)=>{
            if(res.code == 0)  this.provincesArr = res.data;
            this.transfromCity(this.province, this.city, this.area);
            let temp = this.provinceText + this.cityText + this.districtText + this.address;
            let _self = this;
            setTimeout(()=>{
              _self.$refs.select.$refs.pro.text = _self.provinceText;
              _self.$refs.select.$refs.pro.value = _self.province;
              _self.$refs.select.$refs.cit.text = _self.cityText;
              _self.$refs.select.$refs.cit.value = _self.city;
              _self.$refs.select.$refs.dis.text = _self.districtText;
              _self.$refs.select.$refs.dis.value = _self.area;
              this.$refs.updateSiteMap.getloction(temp);
            },10)
          }).catch(res=>{
            tip(res.message);
          })
        }
      }).catch(res=>{
        tip(res.message);
      })
    },
    changeMenuText(arr,key){
      for(let i in this[arr]){
        if(this[arr][i].value == this[key]){
         this.$refs[key].text = this[arr][i].text;
         this.$refs[key].value = this[arr][i].value;
         return
        }
      }
    },
    addSiteLatAndLng() {
      let temp = this.pcdObj.provinceText + this.pcdObj.cityText + this.pcdObj.districtText + this.address;
      this.$refs.updateSiteMap.getloction(temp);
    },
    changeApp(obj,key){
      for(let i in obj){
        if(obj[i].value == key){
          return obj[i].text;
        }
      }
    },
    changeAppInit(arr,obj){
      let temp = obj.toLowerCase();
      this.$refs[temp].text = this.changeApp(this[arr],this[obj]);
      this.$refs[temp].value = this[obj];
    },
    ifyPhone(){
      let phoneTemp = this.phone.split(",");
      for(let i in phoneTemp){
        let phone = {};
        let temp =  phoneTemp[i].split("-");
        phone.beginPhone = temp[0];
        phone.endPhone = temp[1];
        this.phoneObj.push(phone);
      }
    },
    transfromCity(a, b, c) {
      let tempPro = this.provincesArr;
      this.AjaxPro = a;
      this.AjaxCit = b;
      this.AjaxDis = c;
      let Proindex = 0;
      let Citindex = 0;
      for(let value of tempPro) {
        if(value.id == a) {
          this.provinceText = value.pname;
          let tempCit = value.citys;
          this.citiesArr = this.provincesArr[Proindex]['citys'];
          for(let valu of tempCit) {
            if(valu.id == b) {
              this.cityText = valu.cname;
              let tempDis = valu.districts;
              this.districtsArr = this.citiesArr[Citindex]['districts'];
              for(let val of tempDis) {
                if(val.id == c) {
                  this.districtText = val.dname;
                  return
                }
              }
            }
            Citindex++
          }
        }
        Proindex++;
      }
    },
    conditionsIf() {
      if(this.warnNameBol==true){
          tip("充电站名称重复");
          return this.saveBol = false;
      }
      let objIf = {
        "name": {
          maxlength: 30,
          info: '请填写充电站名称',
          infoLen: '站点名称过长,请修改'
        },
        'estateTypes': {
          minlength: 1,
          infoLen: '请选择物业类型'
        },
        'brandIds': {
          minlength: 1,
          infoLen: '请选择支持品牌'
        },
        'AjaxPro': {
          info: '请选择省份'
        },
        'AjaxCit': {
          info: '请选择城市'
        },
        'AjaxDis': {
          info: '请选择区县'
        },
        'address': {
          info: '请填写小区街道名称'
        },
        'longitude': {
          info: '请获取经度'
        },
        'latitude': {
          info: '请获取纬度'
        },
        'images': {
          minlength: 1,
          infoLen: '请上传充电站图片'
        },
        'time': {
          beginWork: '00:00',
          endWork: '24:00',
          beginHoliday: '00:00',
          endHoliday: '24:00'
        },
        'feeDescription':{
          maxlength: 500,
          infoLen: '收费说明限制500字符'
        },
        'notice':{
          maxlength: 40,
          infoLen: '站点公告限制40字符'
        }
      }
      let timeReg = /^([01]\d|2[01234]):([0-5]\d|60)$/;
      this.saveBol = true;
      for(let key in objIf) {
        let value = objIf[key];
        if(value.info && !this[key]) {
          tip(value.info);
          this.saveBol = false;
          return;
        }
        if(value.maxlength && this[key]&&this[key].length >= value.maxlength) {
          tip(value.infoLen);
          this.saveBol = false;
          return;
        }
        if(value.minlength && this[key].length < value.minlength) {
          tip(value.infoLen);
          this.saveBol = false;
          return;
        }
        for(let i in this.phoneObj){
          let phone = this.phoneObj[i];
          if(!phone.beginPhone){
            tip('请填写服务电话');
            return this.saveBol = false;
          }
        }
        if(key == 'time') {
          for(let key1 in value) {
            let value1 = value[key1];
            if(!this[key1]) {
              this[key1] = value1;
            } else if(this[key1] && !timeReg.test(this[key1])) {
              tip('请输入正确时间格式');
              this.saveBol = false;
              return;
            }
          }
          if(this.beginWork > this.endWork || this.beginHoliday > this.endHoliday) {
            tip('起始时间不能大于结束时间');
            this.saveBol = false;
            return;
          }
        }
        if(this.park == 2 && !this.parkFee) {
          tip('请填写停车费用');
          this.saveBol = false;
          return;
        }
        if(Number(this.feeMin)>Number(this.feeMax)){
          tip('最高费用不得小于最低费用');
          return this.saveBol = false;
        }
      }
    },
    ChangeNotice(){
      if (this.notice.length>40) this.noticeBol=true;
      else this.noticeBol=false;
    },
    ChangeCharge(){
      if(this.feeDescription.length>500) this.chargeinstBol = true;
      else this.chargeinstBol = false;
    },
    save(e) {
      let saveUrl = this.baseUrl.update;
      $(".brand-true").each((i, n) => {
        this.brandIds.push(parseInt(n.getAttribute('brandid')))
      })
      this.conditionsIf();
      if(this.saveBol) {
        this.phone=[];
        for(let i in this.phoneObj){
            let temp = `${this.phoneObj[i].beginPhone}-${this.phoneObj[i].endPhone}`;
            this.phone.push(temp);
          }
        if(this.parkFee) {
          let tempparkFee = this.parkFee.toString();
          this.parkFee = Number(tempparkFee.split("元/小时")[0]);
        } else {
          this.parkFee = null;
        }
        let tempData = {
          'id': this.$route.query.id,
          'idNo': this.idNo,
          'name': this.name,
          'type': this.type,
          'estateTypes': this.estateTypes,
          'brandIds': this.brandIds,
          'province': this.AjaxPro,
          'city': this.AjaxCit,
          'area': this.AjaxDis,
          'address': this.address,
          'longitude': this.longitude,
          'latitude': this.latitude,
          'images': this.images.join(","),
          'phone': this.phone.join(","),
          'serviceWorkdayTime': this.beginWork + "-" + this.endWork,
          'serviceHolidayTime': this.beginHoliday + '-' + this.endHoliday,
          'scope': parseInt(this.scope),
          'specilic': this.specilic,
          'park': parseInt(this.park),
          'parkFee': this.parkFee,
          'remark': this.remark,
          'status':this.status,
          'operateId':this.operateId,
          'operateName':this.operateName,
          'appPay':this.appPay,
          'wxPay':this.wxPay,
          'cardPay':this.cardPay,
          'feeDescription':this.feeDescription,
          'feeMin':this.feeMin,
          'feeMax':this.feeMax,
          'appoint':this.appoint,
          'notice':this.notice
        };
        ajaxHttp(saveUrl,{
          type: 'post',
          dataType: 'json',
          data: JSON.stringify(tempData),
          contentType: "application/json",
        }).then((res)=>{
          if(res.code == 0) {
              tip('站点修改成功');
              setTimeout(() => {
                this.$route.router.go({
                  path:`${this.$route.path.replace('/updatesite','').split("?")[0]}`,
                  query:{
                    head:this.$route.query.head,
                    id:this.$route.query.id,
                    list:this.$route.query.list
                  }
                });
              }, 200)
            }
        }).catch(res=>{
            tip(res.message);
          })
      }
    },
    addbrand(e) {
      let _self = $(e.target),
        bol = _self.hasClass('brand-true');
      if(!bol) {
        _self.addClass('brand-true')
      } else {
        _self.removeClass('brand-true');
      }
    },
    addPhone(){
      this.phoneObj.push({'beginPhone':'','endPhone':''});
    },
    deletePhone(a){
      this.phoneObj.splice(a,1);
    }
  },
  components: {
    FileUpload: FileUpload
  },
  events: {
    addFileUpload(file, component) {
      if(file.size > 5 * 1024 * 1024) {
        tip('图片大小不超过5MB');
        component.active = false;
      } else {
        component.active = true;
      }
      if(this.images.length > 49) {
        tip('上传图片不超过50张');
        component.active = false;
      } else {
        component.active = true;
      }
      let patt = new RegExp("\.(png|jpg)$");
      if(!patt.test(file.name)) {
        tip('请上传png,jpg格式图片')
      }
    },
    fileUploadProgress(file, component) {},
    afterFileUpload(file, component) {
      let resIndex = this.$refs.upload.files.length - 1;
      let result = this.$refs.upload.files[resIndex].data;
      if(result.code == 0) {
        this.images.push(result.data[0].fileUrl);
      } else {
        tip('图片格式上传错误')
      }
    },
    beforeFileUpload(file, component) {},
    dropMenuChange(val){
      if(val.queryName){
        this[val.queryName] = val.value;
      }
    },
    selectTree(val){
      this.operateId = val.value;
      this.operateName = val.text;
    },
    selectCitys(val) {
      this.pcdObj = val;
      if(this.pcdObj.provinceValue){
        this.AjaxPro = this.pcdObj.provinceValue;
        this.AjaxCit = this.pcdObj.cityValue;
        this.AjaxDis = this.pcdObj.districtValue;
      }
    },
    latlnginfo(val){
      this.longitude = val.lng;
      this.latitude = val.lat;
      this.longitude1 = val.lng.toFixed(6);
      this.latitude1 = val.lat.toFixed(6);
    }
  },
  watch: {
    'endWork': {
      handler(val, oldVal) {
        if(val == '00:00') {
          this.endWork = "24:00"
        }
      }
    },
    'endHoliday': {
      handler(val, oldVal) {
        if(val == '00:00') {
          this.endWork = "24:00"
        }
      }
    },
  }
}
module.exports = UpdateSite;


