require('components/common/table-data/table-data');
require('components/base/pop/pop');

const store = require('appVuex/store');
const tip = require('components/base/pop/tip');
const { apiPath } = require('appService/api');
const { dropMenu: dropMenuEvent } = require('components/config/event.json');

require('./client-list.scss');
const template = require('./client-list.tpl');

const ClientList = {
	name: "client-list",

	template,

	data() {
    return {
      form: {},
      rechargeMoney: '',
    	thItems: ["序号", "用户ID", "用户名", "用户余额（元）","应用来源",
        "最近登录时间","注册时间","用户状态","操作"],
      url: apiPath.client.clientList,
      sourceId: {
        "1": "APP",
        "2": "微信",
        "3": "平台"
      },
      doRecharge() {},
      goRecharge() {}
    }
	},

	methods: {
    qurryClientList() {
      let queryParams = {
        userParams: this.$refs.userDropmenu.val(),
        keyWord: this.$refs.userInputbox.val(),
        sourceId: this.$refs.sourceDropmenu.val(),
        status: this.$refs.statusDropmenu.val()
      };
    	this.$refs.tableData.query(queryParams).fetch();
    },
    getRechargeDetail(item) {
      let url = apiPath.client.gotoRecharge;
      let queryOpt = {
        uid: item.uid,
        commId: item.commId
      };
      let _this = this;
      $.get(url,queryOpt,(rtn) => {
        if(rtn.code === 0) {
          this.form = rtn.data;
        }
      })
    },
    recharge(trItem) {
      this.$refs.rechargePop.show();
      this.$refs.rechargeMoney.val("");
      this.getRechargeDetail(trItem);

      this.doRecharge = () => {
        if( !this.$refs.rechargeMoney.val() ) {
          tip("请输入充值金额");
        } else if (isNaN(this.$refs.rechargeMoney.val())) {
          tip("金额格式错误");
        } else if( (this.$refs.rechargeMoney.val()*1).toFixed(2) <= 0 ) {
          tip("充值金额精确到小数点两位后须大于0");
        } else {
          this.$refs.gorechargePop.show();
          this.rechargeMoney = (this.$refs.rechargeMoney.val()*1).toFixed(2);
        }
      }
      this.goRecharge = () => {
        let _this = this;
        let queryOpt = {
          uid: trItem.uid,
          commId: trItem.commId,
          tradingAmount: this.$refs.rechargeMoney.val()*100
        };
        $.ajax({
          url: apiPath.client.recharge,
          data: JSON.stringify(queryOpt),
          contentType: 'application/json',
          type: "post",
          success(rtn) {
            if(rtn.code === 0) {
              tip("充值成功！");
              _this.$refs.gorechargePop.hide();
              _this.$refs.rechargePop.hide();
              _this.$refs.tableData.fetch();
            }else {
              tip(rtn.message);
            }
          }
        })
      }
    }
	},

	store,

	events: {
		[dropMenuEvent.menu.change](){
			if ( this.$refs.userDropmenu.val() === 0) {
				this.placeItem = "输入用户名"
			} else if ( this.$refs.userDropmenu.val() === 1 ) {
				this.placeItem = "输入用户ID"
			}
		}
	},
  filters: {
    changeSourse(value) {
      return this.sourceId[value];
    },
    changeMoney(value) {
      return (value/100).toFixed(2);
    }
  }
}

module.exports = ClientList;



