require('components/common/data-picker/data-picker');
const store = require('appVuex/store');

const { apiPath } = require('appService/api');
require('./account-detail.scss');
const template = require('./account-detail.tpl');

const AccountDetail = {
	name: "account-detail",
	template,
  store,
	data() {
    return {
    	thItems: ["序号", "时间", "类型", "金额（元）", "来源"],
    	url: apiPath.client.accountDetail,
    	queryOpt: {
    		commId: this.$route.query.commId,
    		uid: this.$route.query.uid
    	},
      sourceId: {
        "1": "app",
        "2": "微信",
        "3": "公有云",
        "4": "刷卡"
      },
      transType: {
        "1": "充值",
        "2": "支出",
        "3": "提现"
      },
      queryTime: {
        beginTime: '',
        endTime: '',
      }
    }
	},
	methods: {
    queryAccountDetail() {
      let queryParams = {
        commId: this.$route.query.commId,
        uid: this.$route.query.uid,
        startTime: this.queryTime.beginTime,
        endTime: this.queryTime.endTime,
        sourceId: this.$refs.sourseDropmenu.val(),
        transType: this.$refs.typeDropmenu.val()
      };
      this.$refs.tableData.query(queryParams).fetch();
    }
	},
  filters: {
    changeTranstype(value) {
      return this.transType[value];
    },
    changeSourse(value) {
      return this.sourceId[value];
    },
    changeMoney(value) {
      return (value/100).toFixed(2);
    }
  }
}

module.exports = AccountDetail;



