
const ajaxHttp = require('appUtil/ajaxHttp');
const tip = require('components/base/pop/tip');
const store = require('appVuex/store');
const { setBreadCrumb } = require('src/common/vuex/action/common');
const { apiPath } = require('appService/api');
require('./trade-detail.scss');
const template = require('./trade-detail.tpl');

const TradeDetail = {
	name: "trade-detail",

	template,

	data() {
		return {
			id: this.$route.query.id,
			from: this.$route.query.from,
			detail: null,
			payStatus: [{
				"value": 1,
				"text": "待支付"
			},{
				"value": 2,
				"text": "已支付"
			}, {
				"value": 3,
				"text": "支付失败"
			}],
			payModes: [{
				"value": 1,
				"text": "微信"
			},{
				"value": 2,
				"text": "支付宝"
			},{
				"value": 3,
				"text": "银联"
			}],
			channelId: [{
				"value": 0,
				"text": "微信渠道"
			}, {
				"value": 1,
				"text": "APP在线发起充电"
			},{
				"value": 2,
				"text": "APP蓝牙发起充电"
			},{
				"value": 3,
				"text": "刷卡充电"
			},{
				"value": 4,
				"text": "桩上报离线数据"
			}],
			businessType: [{
				"value": 1,
				"text": "充电"
			},{
				"value": 2,
				"text": "租车"
			}]
		}
	},

	ready() {
		this.init();
		this.getDetail();
		this.getTxt('businessType')
	},

	methods: {
		init() {
			var arr = [{
        name: "财务"
      }, {
        name: "交易列表"
      }, {
        name: "交易详情"
      }];

      // this.setBreadCrumb(arr);
		},

		getDetail() {
			let url = '';
			let data = {};
			if (this.from === 'trade') {
				url = apiPath.financial.tradeRcordDetail;
				data = {
					"id" : this.id
				};
			}

			if (this.from === 'statement') {
				url = apiPath.financial.billDetail;
				data = {
					"billId" : this.id
				};
			}
			
			ajaxHttp(url, {
				data: data
			}).then((res)=> {
				this.detail = res.data;
			}).catch((res)=> {
				tip(res.message);
			})
		},

		forGet(arr, key, returnKey, value) {
			for (let i = 0; i < arr.length; i++) {
				let o = arr[i];

				if (o[key] == value) {
					return o[returnKey];
				}
			}

			return '';
		},

		getTxt(arrName, value) {
			let arr = this[arrName];
			let txt =  this.forGet(arr, 'value', 'text', value);

			return txt ? txt : '-----';
		}
	},

	vuex: {
    getters: {
    },
    actions: {
      setBreadCrumb
    }
  },

  store,

  route: {
		data: function (transition) {
			document.title = '欢迎您使用充电网公有云服务系统！';
			transition.next();
		}
	}
}

module.exports = TradeDetail;


