require('components/common/img-upload/img-upload');
const AddSite = {
	template,
	store,
	vuex: {
		actions: {
			setBreadCrumb,
		}
	},
	data() {
		return {
			provincesArr: null,
			citiesArr:[],
			districtsArr:[],
			name: '',
			type: '',
			latitude: '',
			latitude1: '',
			longitude: '',
			longitude1: '',
			address: '',
			maps: {},
			phoneObj:[
			 {
			   'beginPhone':'',
         'endPhone':''
			 }
			],
			beginPhone: '',
			endPhone: '',
			beginWork: null,
			endWork: null,
			beginHoliday: null,
			endHoliday: null,
			scope: '',
			specilic:[],
			park: '',
			parkFee: '',
			remark: '',
			brands: null,
			baseUrl: apiPath.site.addSite,
			filesUploadUrl: apiPath.site.addSite.uploadImg,
			images: [

			],
			okmessage: "保存成功",
			MapBol: false,
			pic: {
				name: 'file',
				url: apiPath.site.addSite.uploadImg,
				format: /\.(png|jpg)$/i,
				size: 1024 * 1024 * 5,
				timeout: 36000,
				multiple: false,
				accept: "image/png,image/jpg"
			},
			saveBol: true,
			markers:null,
			fileBol:false,
			warnNameBol:false,
			pcdObj:{},
			AMapBol:false,
			operateId:null,
			operateName:'',
			chargeTpyeArr:[
			{
			  text:'个人',
			  value:2
			},{
			  text:'公共',
			  value:1
			},{
			  text:'营运',
			  value:3
			}
			],
			parkCostArr:[
			{
			  text:'免费',
			  value:3
			},{
			  text:'未知',
			  value:1
			},{
			  text:'收费',
			  value:2
			}
			],
			useRangeArr:[
			{
			  text:'对外开放',
			  value:1
			},{
			  text:'内部使用',
			  value:2
			}
			],
			appPay:null,
			appPayArr:[
			{
			  text:'app支付',
			  value:-1
			},{
			  text:'充电网APP',
			  value:1001
			},{
			  text:'奥能APP',
			  value:1002
			}
			],
			wxPay:null,
			wxPayArr:[
			{
			  text:'微信公众号',
			  value:-1
			},{
			  text:'充电网微信公众号',
			  value:2001
			},{
			  text:'奥能微信公众号',
			  value:2002
			},{
			  text:'e充网公众账号',
			  value:2003
			}
			],
			cardPay:null,
			cardPayArr:[
			{
			  text:'刷卡支付',
			  value:-1,
			},{
			  text:'上海地铁公交卡',
			  value:3001
			},{
			  text:'普天充电卡',
			  value:3002
			},{
			  text:'充电网充电卡',
			  value:3003
			},{
			  text:'招行闪付卡',
			  value:3004
			}
			],
			treeData: {
			  
      },
      feeDescription:"",
      feeMin:"",
      feeMax:"",
      appoint:"",
      notice:'',
      appointArr:[
      {
        text:'不需预约',
        value:2,
      },{
        text:'需提前预约',
        value:1,
       },{
        text:'未知',
        value:3
      }
      ],
      noticeBol:false,
      chargeinstBol:false,
		}
	},

	route: {
		data: function(transition) {
			document.title = '欢迎您使用充电网私有云服务系统！'
			transition.next()
		}
	},

	ready() {
		this.initDropMenu()
	},
	attached() {},
	methods: {
	  initDropMenu(){
	    this.park = 3;
	    this.type = 2;
	    this.scope = 1;
	    this.appoint = 2;
	    $('#cbktime').timepicker({
      'timeFormat': 'H:i',
      'step': 1
      });
      $('#cbktime2').timepicker({
        'timeFormat': 'H:i',
        'step': 1
      });
      $('#cbktime3').timepicker({
        'timeFormat': 'H:i',
        'step': 1
      });
      $('#cbktime4').timepicker({
        'timeFormat': 'H:i',
        'step': 1
      });
      ajaxHttp(apiPath.site.addSite.getCommList,{
        data:{
          groupid:1
        }
      }).then((res)=>{
        if(res.code==0){
          this.treeData = res.data;
          this.$refs.selecttree.txt =this.treeData.shortName;
          this.operateName = this.treeData.shortName;
          this.operateId = this.treeData.commId;
        }
      }).catch(res=>{
        tip(res.message);
      })
      ajaxHttp(this.baseUrl.selectPcd,{
      }).then((res)=>{
        if(res.code == 0) this.provincesArr = res.data;
      }).catch(res=>{
        tip(res.message);
      })
      ajaxHttp(this.baseUrl.selectBrand,{
      }).then((res)=>{
        if(res.code == 0) this.brands = res.data;
      }).catch(res=>{
        tip(res.message);
      })
	  },
	  initMap(map){
       this.AMapBol = true;
    },
	  checkName(){
	    ajaxHttp(this.baseUrl.checkName,{
	    }).then((res)=>{
	      if(res.code==0){
          if (res.data.hasRepeat==false) {
              this.warnNameBol = true;
          }else{
            this.warnNameBol = false;
          }
        }
	    }).catch(res=>{
        tip(res.message);
      })
	  },
		ok() {
			this.$route.router.go({
				path: "sitelists"
			})
		},
		deleteImg(a) {
			this.images.splice(a, 1);
		},
		search(data) {
			placeSearch.search(data);
		},
		addSiteLatAndLng() {
			if(!this.pcdObj.provinceText) {
				return tip('省份不能为空');
			}
			if(!this.pcdObj.cityText) {
				return tip('城市不能为空');
			}
			if(!this.address){
			 return tip('详细地址不能为空');
			}
			else {
				this.MapBol = true;
				let temp = this.pcdObj.provinceText + this.pcdObj.cityText + this.pcdObj.districtText + this.address;
				this.$refs.addSiteMap.getloction(temp);
				this.MapBol = false;
				}
		},
		cancel() {
			window.history.back();
		},
		conditionsIf(){
		  if (this.warnNameBol) {
          tip('充电站名称重复');
          return this.saveBol = false;
        }
			let objIf = {
				name:{maxlength:30,info:'请填写充电站名称',infoLen:'站点名称过长,请修改'},
				estateTypes:{minlength:1,infoLen:'请选择物业类型'},
				brandIds:{minlength:1,infoLen:'请选择支持品牌'},
				AjaxPro:{info:'请选择省份'},
				AjaxCit:{info:'请选择城市'},
				AjaxDis:{info:'请选择区县'},
				address:{info:'请填写小区街道名称'},
				longitude:{info:'请获取经度'},
				latitude:{info:'请获取纬度'},
				images:{minlength:1,infoLen:'请上传充电站图片'},
				time:{beginWork:'00:00',endWork:'24:00',beginHoliday:'00:00',endHoliday:'24:00'},
				feeDescription:{maxlength: 500,infoLen: '收费说明限制500字符'},
        notice:{maxlength: 40,infoLen: '站点公告限制40字符'}
			}
			let timeReg = /^([01]\d|2[01234]):([0-5]\d|60)$/;
			for(let key in objIf){
				this.saveBol = true;
				let value = objIf[key];
				if (value.info&&!this[key]) {
						tip(value.info);
					return this.saveBol = false;
				}if (value.maxlength&&this[key].length>=value.maxlength) {
					tip(value.infoLen);
					return this.saveBol = false;
				}if (value.minlength&&this[key].length<value.minlength) {
					tip(value.infoLen);
					return this.saveBol = false;
				}
				if (key=='time') {
					for (let key1 in value) {
						let value1 = value[key1];
						if (!this[key1]) {
							this[key1] = value1;
						}else if(this[key1]&&!timeReg.test(this[key1])){
						  tip('请输入正确时间格式');
              return this.saveBol = false;
						}
					}
					if(this.beginWork > this.endWork || this.beginHoliday > this.endHoliday) {
						tip('起始时间不能大于结束时间');
						return this.saveBol = false;
					}
				}
			}
			if(this.park==2&&!this.parkFee){
          tip('请填写停车费用');
          return this.saveBol = false;
        }
			if(Number(this.feeMin)>Number(this.feeMax)){
			  tip('最高费用不得小于最低费用');
			  return this.saveBol = false;
			}
		},
		ChangeNotice(){
		  if (this.notice.length>40) this.noticeBol=true;
		  else this.noticeBol=false;
		},
		ChangeCharge(){
		  if(this.feeDescription.length>500) this.chargeinstBol = true;
		  else this.chargeinstBol = false;
		},
		save(e) {
			let saveUrl = this.baseUrl.siteSave;
			this.estateTypes = [];
			this.brandIds = [];
			$("input[name='property-type']:checked").each((i, n) => {
					this.estateTypes.push(parseInt(n.value))
				})
			$(".brand-true").each((i, n) => {
				this.brandIds.push(parseInt(n.getAttribute('brandid')))
			})
			this.conditionsIf();
			if(this.saveBol) {
				  let phone = [];
					for(let i in this.phoneObj){
					  let temp = `${this.phoneObj[i].beginPhone}-${this.phoneObj[i].endPhone}`;
					  phone.push(temp);
					}
					this.phone = phone.join(",");
				let tempData = {
					'name': this.name,
					'type': this.type,
					'estateTypes': this.estateTypes,
					'brandIds': this.brandIds,
					'province': this.AjaxPro,
					'city': this.AjaxCit,
					'area': this.AjaxDis,
					'address': this.address,
					'longitude': this.longitude,
					'latitude': this.latitude,
					'images': this.images.join(','),
					'phone': this.phone,
					'serviceWorkdayTime': this.beginWork + "-" + this.endWork,
					'serviceHolidayTime': this.beginHoliday + '-' + this.endHoliday,
					'scope': this.scope,
					'specilic': this.specilic[0],
					'park': this.park,
					'parkFee': Number(this.parkFee.split("元/小时")[0]),
					'remark': this.remark,
					'operateId':this.operateId,
					'operateName':this.operateName,
					'appPay':this.appPay,
					'wxPay':this.wxPay,
					'cardPay':this.cardPay,
					'feeDescription':this.feeDescription,
					'feeMin':this.feeMin,
					'feeMax':this.feeMax,
					'appoint':this.appoint,
					'notice':this.notice
				};
				ajaxHttp(saveUrl,{
				  type: "post",
          dataType: 'json',
          contentType: "application/json",
          data: JSON.stringify(tempData),
				}).then((res)=>{
				  if(res.code == 0) {
              tip('新增站点成功');
              setTimeout(() => {
                  this.$route.router.go({
                    path: "staylists"
                  });
                }, 200)
            }
				}).catch(res=>{
				  tip('新增站点失败,接口问题');
				}
				)
			}
		},
		addbrand(e) {
			let _self = $(e.target),
				bol = _self.hasClass('brand-true');
			if(!bol) {
				_self.addClass('brand-true');
			} else {
				_self.removeClass('brand-true');
			}
		},
		filedown(e){
//		  let _this = e.target;
//		  $('.file-uploads').css("color","red");
//		  $('.file-uploads').css("borderColor","red");
//		  setTimeout(function(){
//		    $('.file-uploads').css("borderColor","#e6e6e6");
//		    $('.file-uploads').css("color","#999");
//		  },50)
		},
		addPhone(){
		  this.phoneObj.push({'beginPhone':'','endPhone':''});
		},
		deletePhone(a){
		  this.phoneObj.splice(a,1);
		}
	},
	components: {
		FileUpload: FileUpload,
		selectTree
	},
	events: {
		addFileUpload(file, component) {
			if(file.size > 5 * 1024 * 1024) {
				tip('图片大小不超过5MB');
				component.active = false;
			}else{
				component.active = true;
			}
			if (this.images.length>49) {
				tip('上传图片不超过50张');
				component.active = false;
			}else{
				component.active = true;
			}
			let patt = new RegExp("\.(png|jpg)$");
			if (!patt.test(file.name)) {
				tip('请上传png,jpg格式图片')
			};
		},
		fileUploadProgress(file, component) {
		},
		afterFileUpload(file, component) {
			let resIndex = this.$refs.upload.files.length - 1;
			let result = this.$refs.upload.files[resIndex].data;
			if(result.code == 0) {
				this.images.push(result.data[0].fileUrl);
			} else {
				tip('图片上传失败,服务器出错')
			}
		},
		beforeFileUpload(file, component) {
		},
		selectCitys(val) {
      this.pcdObj = val;
      this.AjaxPro = this.pcdObj.provinceValue;
      this.AjaxCit = this.pcdObj.cityValue;
      this.AjaxDis = this.pcdObj.districtValue;
    },
    latlnginfo(val){
        this.longitude = val.lng;
        this.latitude = val.lat;
        this.longitude1 = val.lng.toFixed(6);
        this.latitude1 = val.lat.toFixed(6);
    },
    dropMenuChange(val){
      if(val.queryName){
        this[val.queryName] = val.value;
      }
    },
    selectTree(val){
      this.operateId = val.value;
      this.operateName = val.text;
    }
	},
	watch:{
	  'endWork':{
	    handler(val,oldVal){
	      if (val=='00:00') {
	      	  this.endWork="24:00"
	      }
	    }
	  },
	  'endHoliday':{
	    handler(val,oldVal){
        if (val=='00:00') {
            this.endWork="24:00"
        }
      }
	  }
	}
}

module.exports = AddSite;


