const Pagination = require('components/base/pagination/pagination');
const template = require('./site-lists.tpl');
require('./site-lists.scss');
require('appUtil/ajaxSetting');
require('components/base/drop-menu/drop-menu');
require('components/base/icon/icon');
const ajaxHttp = require('appUtil/ajaxHttp');
const store = require('appVuex/store');
const {
	setBreadCrumb
} = require('src/common/vuex/action/common');
const {
	apiPath
} = require('appService/api');
const SiteLists = {
	name: '',
	template,
	store,
	vuex: {
		actions: {
			setBreadCrumb,
		}
	},
	data() {
		return {
			jump: null,
			disableBol: false,
			page: {},
			pages: {
			  pages:20,
			  current:1,
			  total:50
			},
			currents: null,
			listsData: [],
			type: '',
			networkStatus: '',
			provincesArr: [],
			citiesArr: '',
			address: '',
			baseUrl: apiPath.site.siteLists,
			size:20,
			token:window.SAAS.TOKEN,
			siteTypeArr:[
			{text:'站点类型',value:''},
			{text:'公共',value:1},
			{text:'个人',value:2},
			{text:'营运',value:3}
			],
			netTypeArr:[
			{text:'联网状态',value:''},
			{text:'已联网',value:1},
			{text:'未联网',value:2}
			]
		}
	},
	ready() {
	  this.getData();
	},
	methods: {
	  getData(){
	    ajaxHttp(this.baseUrl.selectPcd,{
	    }).then((res)=>{
	      if(res.code==0) this.provincesArr = res.data;
	    })
	    this.baseGet({_size:this.size});
	  },
	  baseGet(arg){
	    ajaxHttp(this.baseUrl.page,{
        url: this.baseUrl.page,
        data:arg,
      }).then((res)=>{
        if(res.code==0){
          let {current,pages,size,total} = res.data;
          this.$set('page', {current,pages,size,total});
          this.listsData = res.data.records;
        }
      })
	  },
		changeTypes(a) {
			switch(a) {
				case 1:
					return "公共";
				case 2:
					return "个人";
				case 3:
					return "营运";
				default:
					break;
			}
		},
		changeNetwork(a) {
			if(a == 1) {
				return '是'
			} else {
				return '否'
			}
		},
		transfromProvince(a) {
			let tempPro = this.provincesArr;
			for(let value of tempPro) {
				if(value.id == a) {
					return value.pname;
				}
			}
		},
		transfromCity(a, b) {
			let tempPro = this.provincesArr;
			for(let value of tempPro) {
				if(value.id == a) {
					let tempCit = value.citys;
					for(let valu of tempCit) {
						if(valu.id == b) {
							return valu.cname;
						}
					}
				}
			}
		},
		changeProvince(e) {
			let id = e.target.value;
			this.districtsArr = null;
			if(id) {
				this.citiesArr = this.provincesArr[id]['citys'];
				this.AjaxPro = this.provincesArr[id]['id'];
			}else{
				this.AjaxPro='';
			}
		},
		changeCities(e) {
			let id = e.target.value;
			if(id) {
				this.districtsArr = this.citiesArr[id]['districts'];
				this.AjaxCit = this.citiesArr[id]['id'];
			} else {
				this.AjaxCit='';
			}
			this.changeType();
		},
		search() {
		  let objTemp = {
		    type: this.type,
        networkStatus: this.networkStatus,
		    keyword: this.address,
		    _size:this.size,
		  };
      let objDate={};
      for (let key in objTemp) {
       let value = objTemp[key]
       if (value) {
       	objDate[key]=value;
       }
      }
      this.baseGet(objDate);
		},
		changeType(){
			if (this.type||this.networkStatus||this.AjaxPro||this.AjaxCit) {
			  let temp = {
          type: this.type,
          networkStatus: this.networkStatus,
          province: this.AjaxPro,
          city: this.AjaxCit,
          _size:this.size
        }
			  this.baseGet(temp);
			}else{
			  this.baseGet({_size:this.size});
			}
		},
		derive() {
			let downUrl = this.baseUrl.exports;
			let AjaxPro = this.AjaxPro;
			let AjaxCit = this.AjaxCit;
			let address = this.address;
			if(!AjaxPro) {
				AjaxPro = '';
			}
			if(!AjaxCit) {
				AjaxCit = '';
			}
			if(!address) {
				address = '';
			}
			let temp = `${downUrl}?type=${this.type}&networkStatus=${this.networkStatus}&province=${AjaxPro}&city=${AjaxCit}&keyword=${address}&token=${this.token}`;
			window.open(temp)
		},
		queryName(a){
		  this[a.queryName] = a.value;
		  this.changeType();
		}
	},
	events:{
	  switchPage(val){
	    let temp = {
	      _index: val,
        type: this.type,
        networkStatus: this.networkStatus,
        province: this.AjaxPro,
        city: this.AjaxCit,
        address: this.address,
        _size:this.size
	    }
	    this.baseGet(temp);
	  },
	  dropMenuChange(val){
	    this.queryName(val)
	  },
	  selectCitys(val){
	    this.AjaxPro = val.provinceValue;
	    this.AjaxCit = val.cityValue;
	    this.changeType();
	  }
	},
	components: {
		Pagination
	},
	watch: {
	}
}
module.exports = SiteLists;


