const { apiPath } = require('appService/api');
const store = require('appVuex/store');

const ajax = require('appUtil/ajax');

require('./basic-data.scss');
const template = require('./basic-data.tpl');

const BasicData = {
	name: "basic-data",

	template,

	data() {
    return {
    	url: apiPath.client.basicData,
    	form: {},
    	queryOpt: {
    		uid: this.$route.query.uid,
        commId: this.$route.query.commId
    	},
      sourceId: {
        "1": "APP",
        "2": "微信",
        "3": "平台"
      }
    }
	},
  store,

	ready() {
		this.fetch();
	},

	methods: {
		fetch(queryOpt) {
		  queryOpt = this.queryOpt;
        ajax('get', this.url)
          .query(queryOpt)
          .end((err, res) => {
            if (res.body.code === 0) {
              this.form = res.body.data;
            } else {
              tip(res.body.message)
            }
          })
    }
	},
  filters: {
    changeSourse(value) {
      return this.sourceId[value];
    },
    changeMoney(value) {
      return (value/100).toFixed(2);
    },
    isEmpty(value) {
      if(!value) {
        return "--"
      } else {
        return value;
      }
    }
  }
}

module.exports = BasicData;



