const welcome = require('appComp/page/welcome/welcome');
const routerView = require('components/layout/router-view/router-view');

const login = require('appComp/page/login/login');
const defaultPage = require('appComp/page/default/default');
const pageLayout = require('appComp/layout/page-layout/page-layout');
const siteRouter = require('./site');
const orderRouter = require('./order');
const marketing = require('./marketing');
const maintainRouter = require('./maintain');
const clientRouter = require('./client');
const accountRouter = require('./account');
const financialRouter = require('./financial');
const survey = require('./survey');
const settingRouter = require('./setting');

module.exports = {
  "*": {
    "title": "404",
    "component": defaultPage
  },
  "/": {
    "title": "欢迎您使用登陆",
    "component": login
  },
  "/login": {
    "title": "欢迎您使用登陆",
    "component": login
  },
  "/page": {
    "component": pageLayout,
    "subRoutes": {
      "/site": siteRouter,
      "/order": orderRouter,
      "/client": clientRouter,
      "/maintain": maintainRouter,
      "/marketing": marketing,
      "/account": accountRouter,
      "/financial": financialRouter,
      "/setting": settingRouter,
      "/survey":survey
    }
  }
}


