const template = require('./survey.tpl');
require('./survey.scss');
require('components/common/AMap/AMap')
const store = require('appVuex/store');
require('components/base/pop/pop');
const tip = require('components/base/pop/tip');
require('components/base/icon/icon');
const ajaxHttp = require('appUtil/ajaxHttp');
const echarts = require('echarts');
const {
  apiPath
} = require('appService/api');
require('appUtil/ajaxSetting');
const {
  setBreadCrumb
} = require('src/common/vuex/action/common');
const Survey = {
  template,
  store,
  vuex: {
    actions: {
      setBreadCrumb,
    }
  },
  data() {
    return {
      data: {

      },
      address: '',
      searchAddress: '',
      province: '',
      city: '',
      district: '',
      setBol: false,
      menu: 2,
      okmessage: '',
      provincesArr: [],
      citiesArr: [],
      siteId: this.$route.query.id,
      baseUrl: apiPath.site.survey,
      head: this.$route.query.head,
      networkStatus: null,
      maps: {},
      updateNum: '',
      monitorObj:{},
      warnObj:{},
      optionmonitor: {
        color:['#62A9B8', '#536477','#EF6657','#A4ADB6'],
        tooltip: {
          trigger: 'item',
          formatter: "{a}<br/>{b} : {c} ({d}%)"
        },
        series: [
          {
          name: '车位监控',
          type: 'pie',
          radius: ['50%','70%'],
          center: ['50%', '60%'],
          data: [{
            value: 335,
            name: '占用',
          }, {
            value: 310,
            name: '空闲'
          }, {
            value: 234,
            name: '故障'
          }, {
            value: 135,
            name: '离线'
          }],
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            normal:{ 
                label:{ 
                   show: true, 
                   formatter: '{b} : {c}' ,
                   textStyle:{
                      fontSize:'16'
                    }
                }, 
                labelLine :{show:true}
            } 
          }
        },
        {
            type:'pie',
            radius:['40%','40%'],
            selectedMode: 'single',
            center:['50%', '65%'],
            label: {
                normal: {
                    show: true,
                    position: 'center',
                    textStyle:{
                      fontSize:'20'
                    }
                },
            },
            data:[
            {
              name:'0',
              itemStyle:{
                  normal:{
                    color:'#000',
                    }
              },
              tooltip: {
                  show: false
              },
            },
            {
              name:'总监控',
              itemStyle:{
                  normal:{color:'#999'}
              },
              tooltip: {
                  show: false
              },
            }
            ],
            itemStyle:{ 
            normal:{ 
                  label:{ 
                    show: true, 
                    formatter: '{b}' 
                  }, 
                  labelLine :{show:true} 
                } 
            } 
          },
        ]
      },
      optionwarn: {
        color:["#EF6657","#FC9356","#FAB85A"],
        tooltip: {
          trigger: 'item',
          formatter: "{a}<br/>{b} : {c} ({d}%)"
        },
        series: [
          {
          name: '正在告警',
          type: 'pie',
          radius: ['50%','70%'],
          center: ['50%', '50%'],
          data: [{
            value: 335,
            name: '一级',
          }, {
            value: 310,
            name: '二级'
          }, {
            value: 234,
            name: '三级'
          }],
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            normal:{ 
                label:{ 
                   show: true, 
                   formatter: '{b} : {c}',
                   textStyle:{
                      fontSize:'16'
                    }
                }, 
                labelLine :{show:true}
            } 
          }
        },
        {
            type:'pie',
            radius:['40%','40%'],
            selectedMode: 'single', //单一选中模式 
            center:['50%', '50%'],
            label: {
                normal: {
                    show: true,
                    position: 'center',
                    textStyle:{
                      fontSize:'20'
                    }
                },
            },
            data:[
            {
              name:'0',
              itemStyle:{
                  normal:{
                    color:'#000',
                    }
              },
              tooltip: {
                  show: false
              },
            },
            {
              name:'总告警',
              itemStyle:{
                  normal:{color:'#999'}
              },
              tooltip: {
                  show: false
              },
            }
            ],
            itemStyle:{ 
            normal:{ 
                  label:{ 
                    show: true, 
                    formatter: '{b}' 
                  }, 
                  labelLine :{show:true} 
                } 
            } 
          },
        ]
      },
    }
  },

  route: {
    data: function(transition) {
      document.title = '欢迎您使用充电网私有云服务系统！'
      this.getAjax();
      transition.next()
    },
    waitForData: true
  },

  computed: {
    types() {
      let temp = this.data.info.type;
      switch(temp) {
        case 1:
          return '公共站点';
          break;
        case 2:
          return '个人站点'
          break;
        case 3:
          return '营运站点'
          break;
        default:
          return '未定义站点'
      }
    },
    scopes() {
      let temp = this.data.info.scope;
      switch(temp) {
        case 1:
          return '对外开放';
          break;
        case 2:
          return '内部使用'
          break;
        default:
          return '未定义使用范围'
      }
    },
    status() {
      let temp = this.data.info.status;
      switch(temp) {
        case 0:
          return '已删除';
        case 1:
          return '待上线';
        case 2:
          return '已上线';
        case 3:
          return '已隐藏';
        case 4:
          return '维护中';
      }
    },
    parkFees() {
      let temp = this.data.info.parkFee;
      if(temp) {
        return temp + "元/小时";
      } else {
        return '免费停车';
      }
    },
    computer() {
      let temp = this.data.info.status;
      switch(temp) {
        case 1:
          return 1;
          break;
        case 2:
          return 2;
          break;
        case 3:
          return 3;
          break;
        case 4:
          return 4;
          break;
      }
    },
    updateSiteLink() {
      return {
        path:`${this.$route.path.split('?')[0]}/updatesite`,
        query:{
          id:  this.$route.query.id,
          head: this.data.info.name,
          list: this.$route.query.list
        }
      }
    },
    parkingInfoLink(){
      return {
        path: `${this.$route.path.split('?')[0]}/parkinginfo`,
        query: {
          id: this.$route.query.id,
          siteName: this.data.info.name
        }
      }
    },
    carControlLink() {
      return {
        path: `${this.$route.path.split('?')[0]}/carcontrol`,
        query: {
          id: this.siteId,
          head: this.head
        }
      }
    }
  },

  ready() {
    this._init();
  },
  methods: {
    initMap(map){
    },
    getAjax(){
      $.get(this.baseUrl.query,{
      siteId: this.siteId
    },(res)=>{
      if(res.code==0){
        let _self = this;
        this.data = res.data;
        this.networkStatus = this.data.info.networkStatus;
        let address = res.data.info;
        ajaxHttp(this.baseUrl.selectPcd,{
          }).then((res)=>{
            if(res.code == 0){
              this.provincesArr = res.data;
              this.transfromCity(address.province, address.city, address.area);
              this.address = this.province + " " + this.city + " " + this.district + " " + address.address;
              this.$refs.surveyMap.getloction(this.address);
            }
          })
        setTimeout(() => {
          let parking = [
          {key:'unavailable',text:'占用'},
          {key:'available',text:'空闲'},
          {key:'fault',text:'故障'},
          {key:'offline',text:'离线'},
          ];
          let optionData =[];
          if(_self.data.statsParking.total||_self.data.statsParking.total===0){
            for (let key in parking) {
              optionData[key] = {};
              optionData[key].name = parking[key].text;
              optionData[key].value = _self.data.statsParking[parking[key].key];
            }
            _self.optionmonitor.series[0].data = optionData;
            _self.optionmonitor.series[1].data[0].name =_self.data.statsParking.total.toString() ;
            _self.monitorObj = echarts.init(document.getElementById('monitorEcharts'));
            _self.monitorObj.setOption(_self.optionmonitor);
          }
          let warning =[
          {key:'levelOne',text:'一级'},
          {key:'levelTwo',text:'二级'},
          {key:'levelThree',text:'三级'}
          ];
          let warnData = [];
          if(_self.data.currentWarn.count===0||_self.data.currentWarn.count){
            for (let key in warning) {
              warnData[key] = {};
              warnData[key].name = warning[key].text;
              warnData[key].value = _self.data.currentWarn[warning[key].key];
            }
            _self.optionwarn.series[0].data = warnData;
            _self.optionwarn.series[1].data[0].name =_self.data.currentWarn.count.toString();
            _self.warnObj = echarts.init(document.getElementById('warnEcharts'));
            _self.warnObj.setOption(_self.optionwarn);
          }
      }, 200);
      }
    })
    },
    ok() {
      this.updateStatusAjax(this.updateNum);
      this.$refs.pops.hide();
    },
    _init() {
      var arr = [ {
        name: `${this.$route.query.head}`
      }];
      
      COMMON.componentHub.breadCrumb = arr;
    },
    setUp() {
      this.setBol = !this.setBol;
    },
    setUpFalse() {
      this.setBol = false;
    },
    statusOk(id) {
      switch(id) {
        case 0:
          return "站点是否删除?"
        case 1:
          return "站点是否显示"
        case 2:
          return "站点是否上线"
        case 3:
          return "站点是否隐藏"
        case 4:
          return "站点是否维护"
        default:
          return "站点状态修改失败,无状态值";
      }
    },
    statusOkAgain(id) {
      switch(id) {
        case 0:
          return "站点删除成功"
        case 1:
          return "站点显示成功"
        case 2:
          return "站点上线成功"
        case 3:
          return "站点隐藏成功"
        case 4:
          return "站点设置维护成功"
        default:
          return "站点状态修改失败,无状态值";
      }
    },
    updateStatus(id) {
      this.updateNum = id;
      this.okmessage = this.statusOk(id);
      this.$refs.pops.show();
    },
    updateStatusAjax(id) {
            $.ajax({
              type: "post",
              url: this.baseUrl.updateStatus,
              data: {
                status: id,
                id: this.$route.query.id
              },
              success: (data) => {
                if(data.code == 0) {
                  this.setBol = false;
                  tip(this.statusOkAgain(id));
                  setTimeout(()=>{
                    this.$route.router.go({
                    path: `/page/site/${this.$route.query.list}`
                  })
                  },100)
                }else{
                  tip(data.message);
                }
              }
            });
    },
    transfromCity(a, b, c) {
      let tempPro = this.provincesArr;
      let Proindex = 0;
      let Citindex = 0;
      for(let value of tempPro) {
        if(value.id == a) {
          this.province = value.pname;
          let tempCit = value.citys;
          this.citiesArr = tempPro[Proindex]['citys'];
          for(let valu of tempCit) {
            if(valu.id == b) {
              this.city = valu.cname;
              let tempDis = valu.districts;
              this.districtsArr = this.citiesArr[Citindex]['districts'];
              for(let val of tempDis) {
                if(val.id == c) {
                  this.district = val.dname;
                  break;
                }
              }
            }
            Citindex++
          }
        }
        Proindex++;
      }
    }
  },

  components: {}
}
module.exports = Survey;


