require('components/common/table-data/table-data');
require('components/base/pagination/pagination');
require('components/common/search-menu-input/search-menu-input');
require('components/common/data-picker/data-picker');

const store = require('appVuex/store');
const ajaxHttp = require('appUtil/ajaxHttp');
const tip = require('components/base/pop/tip');
const {
  apiPath
} = require('serviceDir/saasFront/api');
const {
  dropMenu: dropMenuEvent
} = require('components/config/event.json');
const {
  time
} = require('src/common/utils/date');


require('./trade-record.scss');
const template = require('./trade-record.tpl');

const TradeRecod = {
  name: "trade-record",

  template,

  data() {
    return {
      thItems: ["序号", "交易号", "业务", "应用来源", "支付方式", "应付金额（元）", "实付金额（元）",
        "支付时间", "支付状态", "操作"
      ],
      url: apiPath.financial.tradeRcord,
      placeItem: "输入交易号、用户ID、用户名",
      searchParams: {
        _size: 20
      },
      menuData: [{
        text: '交易号',
        value: 'orderId'
      }, {
        text: '用户名',
        value: 'customerName'
      }, {
        text: '用户id',
        value: 'customerId'
      }],
      placeholder: '订单号、用户id、用户名',
      paramsRule: [], //参数检验
      list: [],
      queryTime: {
        beginTime: '',
        endTime: '',
      },
      now: time(new Date().getTime()),
      token: window.SAAS.TOKEN,
      exportUrl: apiPath.financial.tradeRcordExport
    }
  },

  methods: {

    openUrl() {
      let params = {
        payStatus: this.$refs.paystatus.val(), //支付状态, （1:待支付; 2:已支付;3: 支付失败）
        beginTime: this.queryTime.beginTime,
        endTime: this.queryTime.endTime,
        size: 20,
        current: this.$refs.tableData.pageItems.current,
        orderId: '',
        customerName: '',
        customerId: ''
      }

      let userInfo = this.$refs.searchinfo.getVal();
      params[userInfo.key] = userInfo.value;

      window.open(`${this.exportUrl}?token=${this.token}&size=${params.size}&current=${params.current}&payStatus=${params.payStatus}&beginTime=${params.beginTime}&endTime=${params.endTime}&orderId=${params.orderId}&customerName=${params.customerName}&customerId=${params.customerId}`);
    },

    getParams() {
      let params = {
        payStatus: this.$refs.paystatus.val(), //支付状态, （1:待支付; 2:已支付;3: 支付失败）
        beginTime: this.queryTime.beginTime,
        endTime: this.queryTime.endTime,
        _size: 20,
        orderId: '',
        customerName: '',
        customerId: ''
      }

      let userInfo = this.$refs.searchinfo.getVal();
      params[userInfo.key] = userInfo.value;

      return params;
    },

    search() {
      let params = this.getParams();

      this.searchParams = Object.assign(this.searchParams, params);

      this.$refs.tableData.fetch();
    }
  },

  store
}

module.exports = TradeRecod;


