const marketingLayout = require('appComp/page/marketing/marketing-layout/marketing-layout');
const chargingTemplate = require('appComp/page/marketing/charging-template/charging-template');
const chargingTemplateVersion = require('appComp/page/marketing/charging-template-version/charging-template-version');
const chargingTemplateDetail = require('appComp/page/marketing/charging-template-detail/charging-template-detail');
const chargingTemplateAdd = require('appComp/page/marketing/charging-template-add/charging-template-add');

module.exports = {
  component: marketingLayout,
  subRoutes: {
    '/charging-template': {
      name: 'chargingTemplate',
      component: marketingLayout,
      subRoutes: {
        '/': {
          component: chargingTemplate
        },
        '/detail/:id': {
          name: 'chargingTemplateDetail',
          component: chargingTemplateDetail
        },
        '/add': {
          name: 'chargingTemplateAdd',
          component: chargingTemplateAdd
        },
        '/version/add/:id': {
          name: 'chargingTemplateVersionAdd',
          component: chargingTemplateAdd
        },
        '/version/:id': {
          name: 'chargingTemplateVersion',
          component: chargingTemplateVersion
        }
      }
    }
  }
}



