/*
 * checkbox - 多选框组件
 *
 * @props theme - 主题
 * @props value - 初始化时选中的值，默认为第一项， 是checkbox 則為數組
 * @props inputName - input 的 name
 * @props type - input 的 type(radio | checkbox)
 * @props checkboxItems - 复选框数据
 * @props readOnly - 只读
 * @props required - 是否必选
 * @props queryName - 参数名
 * @props errorMessage - checkbox 没选的时候显示的错误信息
 *
 * @events change - checkbox值改变
 */

const Vue = require('vue');
require('components/base/icon/icon');
require('./check-ele');

require('./checkbox.scss');
const template = require('./checkbox.tpl');

const { checkbox: checkboxEvent } = require('components/config/event.json');
const baseMixin = require('components/mixin/base');
const formMixin = require('components/mixin/form');
const arrayUtil = require('src/common/utils/array');

const TYPE_RADIO = 'radio';
const TYPE_CHECKBOX = 'checkbox';

var CheckboxGroup = Vue.extend({
  name: "Checkbox",
  mixins: [baseMixin, formMixin],
  template,
  props: {
    theme: {
      type: String,
      default: "primary"
    },
    checkboxItems: {
      type: Array,
      default: () => []
    },
    inputName: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: TYPE_RADIO
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    queryName: {
      type: String,
      default: ""
    },
    errorMessage: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: true
    },
    value: [Number, Array]
  },
  data() {
    return {
      currentIndex: 0,
      text: "",
      verified: true,
      dangerTip: ""
    }
  },
  methods: {
    /**
     * 派送checkbox的change事件
     *
     * @return {Function}
     **/
    _dispatchChange() {
      return this.$dispatch(checkboxEvent.change, {
        value: this.value,
        text: this.text,
        queryName: this.queryName,
        dispatcher: this
      });
    },

    /**
     * 初始化checkbox
     *
     * @return {Function}
     **/
    _initCheckbox() {
      this._ininCheckboxItems();

      if (this.isCheckbox) {
        this.text = [];
      }

      if (!this.value && this.value !==0 ) {
        if(this.isRadio){
          this.value = undefined;
          this.verified = false;
        } else {
          this.value = [];
        }
      }
    },

    /**
     * 初始化checkboxItems值
     *
     * @return {Function, Object}
     **/
    _ininCheckboxItems() {
      var $el = $(this.$el);
      var $checkEle = $el.find('.checkbox-items-slot .check-ele');
      var items = [];
      if (arrayUtil.isEmpty(this.checkboxItems) && $checkEle.length !== 0) {
        $checkEle.each((index, el) => {
          var $el = $(el);

          items.push({
            value: parseInt($el.attr('value'), 10),
            text: $el.text()
          });
        });

        return this.$set('checkboxItems', items);
      }

      return this;
    },

    /**
     * 改变了选择框的值
     *
     * @return {Function}
     **/
    changeCheck(item, index) {
      if (this.isRadio) {
        this.value = item.value;
        return this.setCurrentIndex(this.value);
      } else {
        return this.setText();
      }
    },

    /**
     * checkbox的icon的样式
     *
     * @param { String } - checkbox当前值
     * @return { Function, Object }
     **/
    iconName(val) {
      if (this.isRadio) {
        return this.value === val ? 'dot-circle-o' : 'circle-o';
      } else if (this.isCheckbox) {
        return this.value.includes(val) ? 'check-square' : 'square-o';
      }
    },

    /**
     * 设置checkbox的text值
     *
     * @return {Function, String}
     **/
    setText() {
      if (this.isRadio) {
        return this.checkboxItems[this.currentIndex]['text'];
      } else {
        if (!arrayUtil.isArray(this.value) ||
            arrayUtil.isEmpty(this.value)) {
          return false;
        }

        this.text = [];

        return this.value.forEach((item) => {
          this.checkboxItems.forEach((ele) => {
            if (item === ele.value) {
              this.text.push(item);
            }
          });
        });
      }
    },

    /**
     * 设置 currentIndex
     *
     * @return {Function, Object}
     **/
    setCurrentIndex(val) {
      if (this.isRadio) {
        return this.checkboxItems.forEach((item, index) => {
          if(item === this.value)
          this.currentIndex = index;
        });
      }

      return this;
    },

    /**
     * 验证数据格式
     *
     * @return {Object} - this - 组件
     */
    verify() {
      this.dangerTip = `请选择 ${this.errorMessage}!`;

      return this.verified;
    },

    /**
     * 验证数据格式并且弹出错误
     *
     * @return {Object} - this - 组件
     */
    validate() {
      this.verify();

      if (!this.verified) {
        tip(this.dangerTip);

        return false;
      }

      return this;
    }
  },

  computed: {
    isCheckbox() {
      return this.type === TYPE_CHECKBOX;
    },

    isRadio() {
      return this.type === TYPE_RADIO;
    }
  },

  watch: {
    'value'(val, oldVal) {
      if(this.isRadio){
        this.verified = true;
      }

      if (Object.is(val, oldVal) || val === oldVal) {
        return false;
      }

      this._dispatchChange();
    }
  },

  compiled() {
    this._ininCheckboxItems();
    this._initCheckbox();
    this._dispatchChange();
  }
});

module.exports = Vue.component('checkbox', CheckboxGroup);


