/**
 * form-area 组件
 *
 * @props theme - 主题
 * @props action - 提交url
 * @props type - ( Post | Get)
 * @props success - 提交成功的回调函数
 * @props fail - 提交失败的回调函数
 * @props beforeSubmit - 提交之前的钩子函数
 * @props jsonData - 提交之前的钩子函数
 * @slot - 加进来的表单控件
 *
 */

const Vue = require('vue');

const baseMixin = require('components/mixin/base');
const {
  btn: btnEvent,
  checkbox: checkboxEvent,
  dropMenu: dropMenuEvent,
  inputBox: inputBoxEvent } = require('components/config/event.json');

const tip = require('components/base/pop/tip');

const template = require('./form-area.tpl');
require('./form-area.scss');

const INIT_FORM_CONTROL = ['DropMenu', 'InputBox', 'Checkbox'];
const VERIFY_FORM_CONTROL = ['InputBox', 'Checkbox'];
const TYPE_POST = "post";
const TYPE_GET = "get";

const FormArea = {
  name: "FormArea",

  mixins: [baseMixin],

  template,

  props: {
    theme: {
      type: String,
      default: "primary"
    },

    action: {
      type: String,
      default: ""
    },

    type: {
      type: String,
      default: "post"
    },

    success: {
      type: Function,
      default: () => {
        return () => { };
      }
    },

    beforeSubmit: Function,

    jsonData: {
      type: Boolean,
      default: false
    }
  },

  data: function () {
    return {
      queryOpt: {}
    }
  },

  methods: {
    /**
     * 初始化
     * @return {Object}
     */
    _init() {
      this._initFormData();
    },

    /**
     * 初始化表单数据
     * @return {Object}
     */
    _initFormData() {
      this.$children.forEach((comp, index) => {

        INIT_FORM_CONTROL.forEach((controlName) => {
          if (comp.constructor.name === controlName) {
            if (!comp.queryName || (comp.value !== 0 && !comp.value)) {
              return false;
            }

            this.queryOpt[comp.queryName] = comp.value;
          }
       });

      });

      return this;
    },

    /**
     * 验证表单控件里是否有格式不对的
     * @return {Boolean} - 是够验证成功
     */
    verify() {
      var verifitation = true;

      var deepVerify = function (comp) {
        comp.$children.every((comp, index) => {

          if (comp.$children.length > 0) {
            deepVerify(comp);
            if (!verifitation) {
              return false;
            }
          }

          return VERIFY_FORM_CONTROL.every((controlName) => {

            if (comp.constructor.name === controlName) {
              if (!comp.verify()) {
                verifitation = false;
                tip(comp.dangerTip);

                return false;
              }
            }

            return true;
          });
        });
      }

      if (this.$children && this.$children.length !== 0) {
        deepVerify(this);
      }

      return verifitation;
    },

    /**
     * 提交表单
     * @return {Object} this - 组件
     */
    submit(opt) {
      if (!this.verify()) {
        return false;
      }

      this.beforeSubmit && this.beforeSubmit.call(null, this.queryOpt);

      var ajaxConf = {
        type: this.type,
        url: this.action,
        data: this.queryOpt,
        success: this.success
      };

      if (this.jsonData) {
        ajaxConf = {
          type: this.type,
          url: this.action,
          data: JSON.stringify(this.queryOpt),
          success: this.success,
          contentType: 'application/json'
        };
      }

      $.ajax(ajaxConf);

      return this;
    },

    /**
     * 重设表单数据
     * @return {Object}
     */
    reset() {
     this.$children.forEach((comp, index) => {

        INIT_FORM_CONTROL.forEach((controlName) => {
          if (comp.constructor.name === controlName) {
            switch (controlName) {
              case 'DropMenu':
                break;
              case 'Checkbox':
                break;
              case 'InputBox':
                comp.value = '';
                break;
              default:
                break;
            }
          }
       });

      });

      return this;
    }
  },

  events: {
    [btnEvent.click](opt) {
      if (opt.dispatcher.submit) {
        this.submit();
      }

      return false;
    },
    [checkboxEvent.change](opt) {
      this.queryOpt[opt.queryName] = opt.value;

      return true;
    },
    [inputBoxEvent.change](opt) {
      if (!opt.queryName) {
        return false;
      }

      this.queryOpt[opt.queryName] = opt.value;
      return true;
    },
    [dropMenuEvent.menu.change](opt) {
      this.queryOpt[opt.queryName] = opt.value;

      return true;
    }
  }
}

module.exports = Vue.component('form-area', FormArea);


