/**
 * alert 组件
 */

const Vue = require('vue');

const {trim} = require('src/common/utils/string');

/**
 * 弹窗函数
 * @param {Object} - {弹窗的头部名字, 弹窗的信息}
 */

module.exports = ({title = '', message = '', cb} = {}) => {
  return COMMON.router.app.$refs
    .commonComponent
    .$refs
    .alert
    .title(title)
    .alertInfo(message)
    .alertOk(cb)
    .show()
};


