
require('components/base/checkbox/checkbox');

require('./edit-account.scss');
const template = require('./edit-account.tpl');

const store = require('appVuex/store');
const tip = require('components/base/pop/tip');
const { apiPath } = require('appService/api');
const { dropMenu: dropMenuEvent } = require('components/config/event.json');

const EditAccount = {
	name: "edit-account",

	template,

	data() {
    return {
      okMessage: "保存成功！",
    	editAccount: apiPath.account.editAccount,
      checkboxItems: [],
      beforeSubmit() {},
      success(){},
      ok() {
        this.$route.router.go({
          path: "/page/account/account-list"
        })
      }
    }
	},

  ready() {
    this.init();
    this.queryAccount();
    this.getRoles();
  },

	methods: {
		init() {
      this.beforeSubmit = (queryOpt) => {
        queryOpt.merchantId = this.$route.query.merchantId;
        if(queryOpt.userName.toString().indexOf(this.$route.query.merchants) < 0) {
          queryOpt.userName = this.$route.query.merchants + queryOpt.userName;
        }
      }
      this.success = (rtn) => {
        if(rtn.code === 0) {
          this.$refs.accountPop.show();
        } else {
          tip(rtn.message);
        }
      }
    },
    queryAccount() {
      let _this = this;
      $.ajax({
        url: apiPath.account.queryAccount,
        data: { merchantId: this.$route.query.merchantId },
        dataType: "json",
        type: "get",
        success(rtn) {
          let userName = (rtn.data.userName).replace(_this.$route.query.merchants,'');
          _this.$refs.usernameInputbox.val(userName);
          _this.$refs.passwordInputbox.val(rtn.data.password);
          _this.$refs.merchantInputbox.val(rtn.data.merchant);
          _this.$refs.telInputbox.val(rtn.data.phone);
          _this.$refs.emailInputbox.val(rtn.data.email);
          _this.$refs.roleidCheckbox.val(rtn.data.roleIds);
          _this.$refs.statusCheckbox.val(rtn.data.status);
        }
      })
    },
    cancel() {
      this.$route.router.go({
        path: "/page/account/account-list"
      })
    },
    getRoles() {
      let _this= this;
      $.ajax({
        url: apiPath.account.getRoles,
        dataType: "json",
        type: "get",
        success(rtn) {
          if (rtn.data) {
            for(let i = 0; i < rtn.data.length; i++) {
              _this.checkboxItems.push({
                "text": rtn.data[i]["roleName"],
                "value": rtn.data[i]["roleId"]
              });
            }
          }
        }
      })
    }
	},

  store
}

module.exports = EditAccount;


