const financialLayout = require('appComp/page/financial/financial-layout/financial-layout');
const tradeRcord = require('appComp/page/financial/trade-record/trade-record');
const tradeDetail = require('appComp/page/financial/trade-detail/trade-detail');
const statementList = require('appComp/page/financial/statement-list/statement-list');
const statementDetail = require('appComp/page/financial/statement-detail/statement-detail');

module.exports = {
  component: financialLayout,
  subRoutes: {
    "/trade-record": {
      name: "tradeRcord",
      component: financialLayout,
      subRoutes: {
        "/": {
          title: '交易记录列表',
          component: tradeRcord
        },
        "/detail": {
          title: '交易记录详情',
          name: 'financialTradeDetail',
          component: tradeDetail
        }
      }
    },
    "/statement-list": {
      name: 'statementList',
      component: financialLayout,
      subRoutes: {
        "/": {
          title: '对账单列表',
          component: statementList
        },
        "/detail": {
          title: '对账单详情',
           name: 'financialStatementDetail',
           component: tradeDetail
        },
        "/list": {
          name: 'financialStatementList',
          component: statementDetail
        }
      }
    }
  }
}



