const Vue = require('vue');

require('./aside-layout.scss');
require('src/common/lib/jquery.scrollbar-gh-pages/jquery.scrollbar.css')
require('src/common/lib/jquery.scrollbar-gh-pages/jquery.scrollbar.min.js');

const template = require('./aside-layout.tpl');
const tip = require('components/base/pop/tip');
const store = require('appVuex/store');

const { apiPath } = require('appService/api');
const { setLoginInfo } = require('appVuex/action/login');
const {getCookie, addCookie, delCookie} = require('src/common/utils/cookie');
require('components/base/pop/pop');

var AsideLayout = Vue.extend({
  props:{
    menus: {
      require: true,
      type: Array
    }
  },

	data() {
    let pIndex = getCookie('pIndex') ? parseInt(getCookie('pIndex'), 10) : 0;
    let sIndex = getCookie('sIndex') ? parseInt(getCookie('sIndex'), 10) : 0;

		return {
      "menuData": null,
      "active": {
        pIndex: pIndex,
        sIndex: sIndex
      }
		}
  },

	ready() {
    $(window).on('resize', ()=>{
      this.resizeNav();
    });
  },

  template,

  methods: {
    resizeNav() {
      let navH = $('#nav-scroll').height();
      let bodyH = $(window).height() - 80;
      // $('#nav-scroll').css('height', bodyH);
      if (navH >= bodyH) {
        $('#nav-scroll').scrollbar();
      } else {
        $('#nav-scroll').css('max-height','auto');
      }

      $('.scroll-wrapper').css('height', bodyH);
    },

    returnBackImg(url) {
      return `background-image: url(${url})`;
    },

    forbid(status) {
      if (status == 1) {
        return true;
      } else {
        tip('暂无权限访问该页面, 请联系管理员！');
      }
    },

    saveIndex(index, type, url) {
      if (type === 0) {
        this.active.pIndex = index;
        addCookie('pIndex', index);
        this.$router.go({
          path: url
        });
      }
    }
  },

  events: {
    "initScroll": function() {
      this.resizeNav();
    }
  }
});

module.exports = AsideLayout;



