//账户管理
const routerView = require('components/layout/router-view/router-view');

const accountLayout = require('appComp/page/settings/account/account-layout/account-layout');
const accountList = require('appComp/page/settings/account/account-list/account-list');
const addAccount = require('appComp/page/settings/account/add-account/add-account');
const editAccount = require('appComp/page/settings/account/edit-account/edit-account');

const messageCenter = require('appComp/page/message/message-center');
const taskCenter = require('appComp/page/task/task-center');
const taskDetail = require('appComp/page/task/task-detail/task-detail');
const taskView = require('appComp/page/task/task.vue');

module.exports = {
  "component": accountLayout,
  "subRoutes": {
    "/account-list": {
      "title": "账户列表",
      "component": accountLayout,
      "subRoutes": {
        "/": {
          "component": accountList
        },
        "/task": {
          "component": routerView,
          "subRoutes": {
            "/": {
              "title": "任务中心",
              "component": taskCenter,
            },
            "/detail/:id": {
              "title": "任务详情",
              "component": taskDetail,
            }
          }
        },
        "/add-account": {
          "title": "添加子账户",
          "component": addAccount
        },
        "/edit-account": {
          "title": "编辑账户",
          "component": editAccount
        },
        "/message": {
          "title": "消息中心",
          "component": messageCenter
        }
      }
    }
  }
}


