
const ajaxHttp = require('appUtil/ajaxHttp');
const calendar = require('components/common/data-calendar/data-calendar');
const tip = require('components/base/pop/tip');
const { apiPath } = require('appService/api');
//const pop = require('components/base/pop/pop');
const pop = resolve => require(['components/base/pop/pop'], resolve);
const template = require('./order-detail.tpl');

const { setBreadCrumb } = require('src/common/vuex/action/common');
const store = require('appVuex/store');

const echarts = require('echarts');

require('./order-detail.scss');

const OrderDetail = {
	name:"order-detail",
	template,
	components:{
		pop,
		calendar

	},
	data(){
		return{
			getUrl: apiPath.order.orderDetail,
			upUrl: apiPath.order.orderEnd,
			voltageUrl: apiPath.order.chargerRecord,
			updateOrderOffline:apiPath.order.updateOrderOffline,
			updatePrice:apiPath.order.updatePrice,

			showBtn:"",
			coupon:false,
			billingList_custom:"",
			serverPrice_custom:"",
			billingType:'',
			serverType:'',
			pushBtn:false,
			upOrderArgs:"",
			upOrderObj:{
				'orderId': "",
				'status': "",
				'remark': ""
			},
			markIndex:"",
			upResltTips: "状态修改成功",
			orderid:"",
			status: "",
			serverTypeVal: 1, //服务费区分
			allowChangeList:["-500","-40","-35","-30","-29","-20","200","300"],
			statusList: {
				"200": "充电中",
				"300":"充电完成",
				"800": "充电结束",
				"-40": "电桩上报超时",
				"-20": "连接超时",
				"-10": "打开电闸超时",
				"0": "订单未激活",
				"2000": "钱已到账",
				"-500": "硬件故障",
				"100": "电闸打开等待连接"
			},
			channeList: {
					"1": "APP",
					"0":"微信",
					"3": "充电卡",
					"4": "桩主"
			},
			detailData:{},
			modal: false,
			tabNav:'',
			voltage:{
				"A": {
						xAxis :{
							data: ["2016-19-29 14:12:20","2016-19-29 14:12:20",
							"2016-09-29 14:12:20","2016-09-29 14:12:20"]
							},
						datasets : [
							{
								data : [20,40,60,40]
							},
							{
								data : [20,40,80,20]
							}
						]
				}
			},
			echartObj:{},
			tagPopStaus:1, //当前弹框状态1,结束订单,2线下付款3,调整价格
			popTits:["结束订单","支付订单","调整价格"],
			remark: "",
			price:"",
			calendar:{
			  show:false,
			  x:0,
			  y:0,
			  picker:"",
			  type:"datetimeS",
			  value:"",
			  sep:"-",
			  range:false,
			  items:{
			    endTime:{
			      type:"datetimeS",
			      value:"",
			      sep:"-"
			    }
			  }
			},
			//默认line的数据配置
			option : {
			  tooltip: {
			    trigger: 'axis'
			  },
			  legend: {
			    left: 100,
			    data: [{
			      name: "电流",
			      icon: 'pin'
			    },
			    {
			      name: "电压",
			      icon: 'circle'
			    }],
			    x: 'left',
			  },
			  xAxis:[
			  	{
			  		type: 'category',
			  		data: ["1", "2", "3", "4", "5"]
			  	}
			  ],
			  yAxis: [{
			    name: '电流(A)',
			    type: 'value'
			  },
			  {
			  	position: 'right',
			    name: '电压(V)',
			    type: 'value'
			  }],
			  series: [
						{
							name:'电流',
							type:'line',
							showAllSymbol: false,
							data:[123,23,45,22]
						},
						{
							name:'电压',
							type:'line',
							showAllSymbol: false,
							yAxisIndex:1,
							data:[2,3,5,8]
						}
				]
			}
	}
		
	},
	watch: {
		'tabNav':function(val, oldVal){

			//let _obj = Object.assign({},this.option, this.voltage[val]);
			this.option.xAxis = this.voltage[val].xAxis;
			this.option.series[0].data = this.voltage[val].series[0].data;
			this.option.series[1].data = this.voltage[val].series[1].data;
				this.echartObj.setOption(this.option);
		},
		'calendar.value': function (value) {
            this.calendar.items[this.calendar.picker].value=value;
        }
	},
	methods:{
		getOrderInfo:function(args =''){
			ajaxHttp(this.getUrl, {
		        type: "GET",
		        data: `${args}`
		      }).then((res)=> {

		        let currentObj = res.data;
		          		this.showBtn = this.allowChangeList.indexOf(`${currentObj["status"]}`);
		          		this.upOrderObj.status = currentObj["status"];
		          		this.status = currentObj["status"];
		          		//自定义后台取出的服务费与电费serverType
		          		let billingList = currentObj["chargerBillingList"];

		          		currentObj["createTime"] = this.formatTime(currentObj["createTime"]);
		          		currentObj["stopTime"] = this.formatTime(currentObj["stopTime"]);
		          		currentObj["billingList_custom"] = Array.from(billingList).filter(function(item){
		          				return item.type === 0;
		          		})
		          		this.billingList_custom = currentObj["billingList_custom"];

		          		currentObj["serverPrice_custom"] = Array.from(billingList).filter(function(item){
		          				return item.type != 0;
		          		})
		          		this.serverPrice_custom = currentObj["serverPrice_custom"];

		          		if(billingList.length>0){
		          			let _billingType = billingList[0].billingType;
		          			this.billingType = _billingType==0? "分时计费" : _billingType==1 ? "普通收费" : "<span style='color:#ff0000'>免费</span>";
		          		}
		          		if(this.serverPrice_custom.length>0){
		          			let _serverType = this.serverPrice_custom[0].billingType;
		          			let _type = this.serverPrice_custom[0].type;
		          			this.serverTypeVal = _type;
		          			this.serverType = _serverType==2 ? "<span style='color:#ff0000'>免费</span>" : _type==1 ? "按电量" : "按时长";
		          		}

		          	this.detailData = currentObj;

		      }).catch((res)=> {
		        tip(res.message);
		      })
		},
		getVoltage: function(){
				let orderId = this.getQueryString("orderid");
				if(!orderId){ return; }

				ajaxHttp(this.voltageUrl, {
			        type: "GET",
			        data: 'orderId='+ orderId
			      }).then((res)=> {
			      	let currentObj = res.data;


		          		this.voltage["A"] = this.voltageFormat("A", currentObj);
		          		this.voltage["B"] = this.voltageFormat("B", currentObj);
		          		this.voltage["C"] = this.voltageFormat("C", currentObj);

		          		this.tabNav = "A";

			      }).catch((res)=> {
				        tip(res.message);
				  })

		},
		voltageFormat: function(type, obj){

			let _obj = obj, labels = [], datasets = [], v_obj = {}, v_arr=[], c_obj = {}, c_arr=[];
			for(let item of _obj){
					labels.push(item.time);
					v_arr.push(item[`voltage${type}`]/100);
					c_arr.push((item[`current${type}`]/100).toFixed(4));
			}
			v_obj.data = v_arr;
			c_obj.data = c_arr;

			datasets.push(c_obj);
			datasets.push(v_obj);

			return {
				"xAxis":{
					"data": labels
				},
				series:datasets
			}

		},

		orderStatus: function(sta){
			this.tagPopStaus = sta || 1;
			this.remark = "";
			this.price ="";
			this.calendar.items.endTime.value ="";
			this.$refs.popstatus.show();
		},
		showMark: function(idx){
			this.markIndex = idx;
		},
		hideMark: function(){
			this.markIndex = "";
		},
		formatTime: function(time){
			if(!time){
				return "";
			}
			let dTime = new Date(time),
					dMinutes = dTime.getMinutes(),
					dSeconds = dTime.getSeconds();
			dMinutes = dMinutes< 10 ? "0"+ dMinutes : dMinutes;
			dSeconds = dSeconds< 10 ? "0"+ dSeconds : dSeconds;
			return `${dTime.getFullYear()}-${dTime.getMonth()+1}-${dTime.getDate()}  ${dTime.getHours()}:${dMinutes}:${dSeconds}`;
		},
		getQueryString: function (name) {
			let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
			let r = window.location.hash.split("?")[1].match(reg);
			if (r != null) return unescape(r[2]); return null;
		},
		serializeArg: function(args){
			let serialStr = '';
			for(let i in args){
				serialStr += `&${i}=${args[i]}`;
			}
			return serialStr.substring(1);
		},
		"changeNav":function(tag){
			this.tabNav = tag;
		},
		"ok": function(){
			//订单结束流程
			if(this.tagPopStaus === 1){
				if(this.remark === "" || this.calendar.items.endTime.value === ""){
					alert("请写入备注跟结束时间");
					return;
				}
				this.$refs.popstatus.hide();
				this.$refs.secondtrue.show();

			}else if(this.tagPopStaus === 2){//支付订单
				if(this.remark === ""){
					alert("请写入备注");
					return;
				}
				this.paySure();

			}else if(this.tagPopStaus === 3){//调整价格
				if(this.remark === "" || this.price === ""){
					alert("请写入备注与调整后的价格");
					return;
				}
				this.$refs.popstatus.hide();
				this.$refs.secondtrue.show();
			}

		},
		//价格调整
		priceChage: function(){
			let that = this;
			let args = {
				orderId: this.orderid,
				price:this.price,
				remark: this.remark
			}
			ajaxHttp(this.updatePrice,{
				type:"post",
				data:args
			}).then((res) => {
				tip(res.message);
				that.getOrderInfo(`orderId=${that.orderid}`);
				that.cancel();
			})
			.catch((res)=> {
				        tip(res.message);
				  })
			//updatePrice
		},
		//支付订单
		paySure: function(){
			let that = this;
			let args = {
				orderId: this.orderid,
				remark: this.remark
			}
			ajaxHttp(this.updateOrderOffline,{
				type:"post",
				data: args
			})
			.then((res) => {
				tips(res.message);
				that.getOrderInfo(`orderId=${that.orderid}`);
				that.cancel();
			})
			.catch((res)=> {
				        tip(res.message);
				  })

		},
		//结束订单

		//结束订单流程
		upOrderStatus: function(){
			let that = this;
			let args = {
				orderId: this.orderid,
				stopTime: this.calendar.items.endTime.value,
				remark: this.remark
			}
			ajaxHttp(this.upUrl, {
			        type: "post",
			        data: args
			      }).then((res)=> {
		          		/*this.status = 800;
		          		this.$refs.secondtrue.hide();
		          		this.showBtn = -1;*/
		          		tip(res.message);
		          		that.getOrderInfo(`orderId=${that.orderid}`);
		          		that.cancel();

			      })
			      .catch((res)=> {
				        tip(res.message);
				  })

		},
		"trueSecond":function(){
			this.tagPopStaus === 1? (this.upOrderStatus()) : (this.priceChage());
		},
		"cancel": function(){
			this.remark = '';
			this.price = "";
			this.calendar.items.endTime.value = "";
			this.$refs.popstatus.hide();
			this.$refs.secondtrue.hide();
		},
		open(e,type) {
            // 设置类型
            this.calendar.picker=type
            this.calendar.type=this.calendar.items[type].type
            this.calendar.value=this.calendar.items[type].value
            // 可不用写
            this.calendar.sep=this.calendar.items[type].sep

            this.calendar.show=true
            this.calendar.x=e.target.offsetLeft
            this.calendar.y=e.target.offsetTop+e.target.offsetHeight+8;

        }

	},
	ready(){

     this.echartObj = echarts.init(document.getElementById('echart'));
		let orderId = this.getQueryString("orderid");
		this.orderid = orderId;
		this.upOrderObj.orderId = orderId;
		this.getOrderInfo(`orderId=${orderId}`);
		this.getVoltage();
	},
	route: {
	    data: (transition) => {
	      document.title = '欢迎您使用充电网私有云服务系统！'
	      transition.next()
	    }
	},
	vuex: {
	    actions: {
	      setBreadCrumb
	    }
	},
	store
}

module.exports = OrderDetail;


