const template = require('./car-details.tpl');
require('./car-details.scss');

const store = require('appVuex/store');
require('appUtil/ajaxSetting');
const {
  apiPath
} = require('appService/api');
const tip = require('components/base/pop/tip');
const {
  setBreadCrumb
} = require('src/common/vuex/action/common');
require('components/base/pop/pop');
require('components/base/input-box/input-box');
const ajaxHttp = require('appUtil/ajaxHttp');

const CarDetails = {
  template,
  store,
  vuex: {
    actions: {
      setBreadCrumb,
    }
  },
  data() {

    return {
      gundatas: null,
      datas: {
        chargerDataVo: {},
        boxVo: {},
        lockVo: {},
        sensorVos: {}
      },
      handle: true,
      orderBol: false,
      gunBol: false,
      inductorBol: false,
      lockBol: false,
      controlBol: false,
      acBol: false,
      warnIndex: null,
      statusBol: false,
      baseUrl: apiPath.site.carDetails,
      carCode: '',
      thirdpartyOrderId: null,
      stallCode: '',
      siteId: null,
      chargringBol: false,
      noteItem: {},
      noticeList: [{
        name: "管理员:（Edward yang | edward@chargerlink.com）",
        a: "站内信 -- 已通知",
        b: "邮件 -- 已通知",
        c: "短信 -- 已通知"
      }],
      gunstatus:null,
      introduceBol:false
    }
  },
  route: {
    data: function(transition) {
      document.title = '欢迎您使用充电网私有云服务系统！'
      transition.next()
    }
  },
  ready() {
    this.carCode = this.$route.query.carCode;
    this.stallCode = this.$route.query.stallCode;
    this.getAjax({
      gunCode: this.carCode
    });
    this._init();
  },
  methods: {
    getAjax(a) {
      $.ajax({
        type: 'get',
        url: this.baseUrl.details,
        dataType: 'json',
        data: a,
        success: (data) => {
          if(data.code == 0) {
            this.datas = data.data
            this.siteId = this.datas.siteId;
            if(this.datas.orderVos[0]) {
              this.thirdpartyOrderId = this.datas.orderVos[0].thirdpartyOrderId;
            }
          }
        }
      })
    },
    openType(arg) {
      switch(arg) {
        case 0:
          return "微信渠道";
        case 1:
          return "APP在线发起充电";
        case 2:
          return "APP蓝牙发起充电";
        case 3:
          return "刷卡充电";
        case 4:
          return "桩上报离线数据";
      }
    },
    stopCharge() {
      let a = {
        orderId: this.datas.orderVos[0].thirdpartyOrderId
      }
      let temp = JSON.stringify(a);
      $.ajax({
        type: 'post',
        url: this.baseUrl.stopCharge,
        data:{
          orderId:this.datas.orderVos[0].thirdpartyOrderId
        },
        success: (data) => {
          if(data.code == 0) {
            tip("停止充电成功");
            setTimeout(() => {
              window.location.reload(location.href)
            }, 400)
          }
        }
      })
    },
    startCharge() {

    },
    changeLock(arg) {
      let lockNum, lockMes;
      if(arg == 1) {
        lockNum = "Lock";
        lockMes = '地锁升起成功';
      } else {
        lockNum = "Unlock";
        lockMes = "地锁落下成功";
      }
      $.ajax({
        type: 'post',
        url: this.baseUrl.changeLock,
        dataType: 'json',
        data: {
          id: this.datas.lockVo.lockCode,
          action: lockNum
        },
        success: (data) => {
          if(data.code == 0) {
            tip(lockMes);
            setTimeout(() => {
              window.location.reload(location.href);
            }, 400)
          }
        }
      })
    },
    changeStatus(id) {
      let temp = id;
      if(!this.chargringBol) {
        $.ajax({
          type: 'post',
          url: this.baseUrl.changeStatus,
          data: {
            regionId: this.$route.query.regionId,
            stallCode: this.$route.query.stallCode,
            status: id
          },
          success: (data) => {
            if(data.code == 0) {
              tip("状态修改成功");
              setTimeout(() => {
                window.location.reload(location.href);
              }, 400)
            }
          }
        })
      } else {
        tip('正在充电,不可维护');
      }
    },
    resetBol(bol) {
      this.statusBol = false;
      this.orderBol = false;
      this.gunBol = false;
      this.inductorBol = false;
      this.lockBol = false;
      this.controlBol = false;
      this.acBol = false;
      this.warnIndex = null;
      if(bol) {
        this.$set(bol, true);
      }
    },
    warnIndexs(a, b) {
      this.warnIndex = a;
      this.noteItem = b;
    },
    note() {
      this.$refs.warningNotePop.show();
      this.resetBol();
    },
    saveNote() {
      if(!this.noteItem.remark) {
        tip("备注不能为空!")
        return false;
      }
      $.post(apiPath.maintain.updateWarningNote, {
        recordId: this.noteItem.recordId,
        note: this.noteItem.remark
      }, (rtn) => {
        if(rtn.code === 0) {
          tip("保存成功！");
        } else {
          tip("保存失败！");
        }
      });
      this.$refs.warningNotePop.hide();
      this.resetBol();
    },
    stopWarn() {
      this.resetBol();
      this.$refs.warningOverPop.show();
      this.overWarn();
    },
    sendNotice(index) {
      this.noteItem = this.noticeList[index];
      $.get(apiPath.maintain.warning.notice, {
        recordId: this.noteItem.recordId,
        userId: this.noteItem.userId
      }, (rtn) => {
        if(rtn.code === 0) {
          this.warningNotice();
          tip("通知成功！");
        } else {
          tip("通知失败！");
        }
      });
    },
    warningNotice() {
      this.resetBol();
      ajaxHttp(apiPath.maintain.warning.noticeDetail, {
        data: {
          recordId: this.noteItem.recordId
        }
      }).then((rtn) => {
        this.noticeList = rtn.data;
        this.$refs.warningNoticePop.show();
      }).catch(() => {})
    },
    overWarn() {
      if(!this.noteItem.remark) {
        tip("备注不能为空!")
        return false;
      }
      $.post(apiPath.maintain.updateEndWarning, {
        recordId: this.noteItem.recordId,
        note: this.noteItem.remark,
        code: this.noteItem.code
      }, (rtn) => {
        if(rtn.code === 0) {
          tip("结束成功！");
        } else {
          tip("结束失败！");
        }
      });
      this.$refs.warningOverPop.hide();
    },
    orderStatus(a) {
      switch(a) {
        case -500:
          return "硬件故障"
        case -40:
          return "上报超时"
        case -35:
          return "断连超时"
        case -30:
          return "关电超时"
        case -29:
          return "结束充电失败"
        case -20:
          return "链接超时"
        case -10:
          return "打开电闸超时"
        case -7:
          return "开启充电失败"
        case -5:
          return "系统繁忙"
        case 0:
          return "订单未激活"
        case 100:
          return "电闸打开"
        case 200:
          this.chargringBol = true;
          return "充电中"
        case 250:
          return "关电闸,充电停止"
        case 300:
          return "充电完成"
        case 800:
          return "订单结束"
        case 1000:
          return "用户已支付"
        case 2000:
          return "钱已到账"
        default:
          break;
      }
    },
    gunStatus() {
      let temp = this.datas.chargerDataVo.status;
      this.gunstatus = temp;
      if(temp > 12) {
        return `<span style="color:#9AC88E">占用</span>`
      } else if(temp == -300 || temp == -200) {
        return `<span style="color:#ff0000">故障</span>`
      } else if(temp == 10) {
        return `<span style="color:#848484">空闲</span>`
      } else if(temp == 0) {
        return `<span style="color:#ff0000">离线</span>`
      }
    },
    sensorVosStatus(a) {
      if(a == 70) {
        return `<span class="takeUp">占用(有车)</span>`
      } else if(a == 10) {
        return `<span class="free">空闲</span>`
      } else if(a == 0) {
        return `<span class="offline">离线</span>`
      } else if(a == -200) {
        return `<span class="offline">故障</span>`
      }
    },
    lockStatus() {
      let a = this.datas.lockVo.status;
      if(a == 10) {
        return `<span class="takeUp">解锁</span>`
      } else if(a == 70) {
        return `<span class="free">锁定</span>`
      } else if(a == 0) {
        return `<span class="offline">离线</span>`
      } else if(a == -200) {
        return `<span class="offline">故障</span>`
      }
    },
    sensorVosType(a) {
      switch(a) {
        case 1:
          return '地磁'
        case 2:
          return '红外线'
        case 3:
          return '超声波'
      }
    },
    boxVoStatus() {
      switch(this.datas.boxVo.status) {
        case 100:
          return `<span style="color:#848484">网络正常</span>`
        case 0:
          return `<span style="color:#ff0000">离线</span>`
        case -200:
          return `<span style="color:#ff0000">故障</span>`
        default:
          break;
      }
    },
    _init() {
//    let bread = JSON.parse(window.localStorage.getItem('bread'));

      let arr = [{
        name: `${this.$route.query.head}`,
        router: `${this.handlerUrl(this.$route.path.split('?')[0],'/carcontrol/cardetails')}?id=${this.$route.query.id}&head=${this.$route.query.head}`
      }, {
        name:'实时监控',
        router: `${this.handlerUrl(this.$route.path.split('?')[0],'/cardetails')}?id=${this.$route.query.id}&head=${this.$route.query.head}`
      },
      {
        name: '车位详情'
      }];
      COMMON.componentHub.breadCrumb = arr;
//    this.setBreadCrumb(bread.concat(arr));
    },
    handlerUrl(url,b) {
      return url.replace(b, '');
    },
    warnDetail(a) {
      switch(a) {
        case 1:
          return `warn`;
        case 2:
          return `warn1`;
        case 3:
          return `warn2`
      }
    },
    boxType(){
      if(this.datas.boxVo.type==0){
        return "内置"
      }else{
        return "外置"
      }
    }
  },
  computed: {
    currentType() {
      if(this.datas.chargerDataVo.currentType == 0) {
        return '交流'
      } else {
        return "直流"
      }
    },
    status() {
      switch(this.datas.status) {
        case 1:
          return '运营中'
        case 2:
          return '维护中'
        default:
          break;
      }
    }
  },
  components: {

  }
}
module.exports = CarDetails;


