const { deepReplaceVal } = require('src/common/utils/object');
const appConfig = require('rootPath/config/app/saasFront.json');
const apiData = require('./config/api.json');

//release环境的uri
var prodUri = appConfig.prodUri;

//测试环境的uri
var devUri = appConfig.devUri;

var docking = true;

const apiPath = deepReplaceVal({
  obj: apiData,
  cb (val) {
    return `${docking ? prodUri : devUri}${val}`;
  }
});

module.exports = {
  apiPath
}



