require('components/base/drop-menu/drop-menu')
require('components/base/loading/loading')

require('components/common/tab-panel/tab-panel')

const store = require('appVuex/store');
const ajaxHttp = require('appUtil/ajaxHttp');
const { apiPath } = require('appService/api');
const { dropMenu: dropMenuEvent } = require('components/config/event.json');
const { setBreadCrumb } = require('src/common/vuex/action/common');

require('./message-setting.scss');
const template = require('./message-setting.tpl');

const MessageSetting = {
  name: "message-setting",

  template,

  data() {
    return {
      warningItems: [],
      otherMessageItems: [],
      roleOption: [],
      tabItems: [{
      	text: "告警消息设置"
      }, {
        text: "其他消息设置"
      }]
    }
  },

  vuex: {
    actions: {
      setBreadCrumb
    }
  },

  store,

  methods: {
    _init() {
      let arr = [{
        name: `设置`
      }, {
        name: `消息设置`
      }];

      this.setBreadCrumb(arr);
      this.settingData();
    },
    settingData(){
      this.$refs.loading.show();

      $.get(apiPath.maintain.warningSetting.detail, (rtn) => {
        this.warningItems = rtn.data;

        $.get(apiPath.setting.message.other, (rtn) => {
          this.otherMessageItems = rtn.data;

          this.$refs.loading.hide();
        })
      })
    },
    strToArr(str, num) {
      var arrTmp = str.split(',');

      if(!num) {
        return arrTmp;
      }

      for(let i = 0; i < arrTmp.length; i++) {
        arrTmp[i] = isNaN(arrTmp[i] ) ? arrTmp[i]  : Number(arrTmp[i] );
      }

      return arrTmp;
    },
    roleValue(arr) {
      var arrTmp = [];

      for(let i = 0; i < arr.length; i++) {
        let item = isNaN(arr[i].value) ? arr[i].value : Number(arr[i].value);
        arrTmp.push(item);
      }

      return arrTmp;
    },

    updateMessageSetting(item) {
      ajaxHttp(item.url, {
        data: item.updateOpt,
        type: 'post',
        dataType: 'json'
      }).then((rtn) => {
        item.cb && item.cb();
      }).catch((res) => {
        return false;
      })
    }
  },

  events: {
    [dropMenuEvent.menu.change](opt) {
      var dispatcher = opt.dispatcher;
      var updateSettingOpt = {};

      if (dispatcher.queryName === 'roleId'
          || dispatcher.queryName === 'noticeType') {
        updateSettingOpt = {
          updateOpt: {
            code: this.warningItems[opt.dispatcher.store.index].code,
            [dispatcher.queryName]: opt.value && opt.value.join(',')
          },
          url: apiPath.maintain.warningSetting.save
        }
      }

      if (dispatcher.queryName === 'notifyRoleId'
          || dispatcher.queryName === 'notifyMethod') {
        updateSettingOpt = {
          updateOpt: {
            triggerCode: this.otherMessageItems[opt.dispatcher.store.index].triggerCode,
            [dispatcher.queryName]: opt.value && opt.value.join(',')
          },
          url: apiPath.setting.message.updateOtherMessageSetting
        }
      }

      this.updateMessageSetting(updateSettingOpt);
    }
  },

  created() {
    $.get(apiPath.maintain.warning.noticeRole, (rtn) => {
      this.roleOption = rtn.data;
    })
  },

  ready() {
    this._init();
  }
}

module.exports = MessageSetting;


