require('components/common/table-data/table-data');
require('components/base/tab/tab');

const store = require('appVuex/store');

const { tab: tabEvent } = require('components/config/event.json');
const { apiPath } = require('appService/api');
const { setBreadCrumb } = require('src/common/vuex/action/common');

require('./message-center.scss');
const template = require('./message-center.tpl');

const MessageCenter = {
  name: "message-center",

  template,

  data() {
    return {
      thItems: ["消息类别", "消息", "时间"],
      url: apiPath.message.list,
      queryOpt: {},
      currentItem: {},
      tabItems: [{
        text: "全部消息"
      },{
        text: "告警通知"
      },{
        text: "系统通知"
      },{
        text: "站点动态"
      }]
    }
  },

  vuex: {
    actions: {
      setBreadCrumb
    }
  },

  store,

  events: {
    [tabEvent.change]({item}) {
      var messageType = item.text === '全部消息' ? '' : item.text;

      this.queryOpt = Object.assign(this.queryOpt, {
        messageType: messageType
      })

      this.$refs.tableData.fetch();
    }
  },

  methods: {
    _init() {
      let arr = [{
        name: `消息中心`
      }];

      this.setBreadCrumb(arr);
    }
  },

  ready() {
    this._init();
  }
}

module.exports = MessageCenter;


