
require('components/base/pagination/pagination');
require('components/common/empty/empty');
require('components/base/icon/icon');
const store = require('appVuex/store');
const { dropMenu: dropMenuEvent } = require('components/config/event.json');
const tip = require('components/base/pop/tip');
const ajaxHttp = require('appUtil/ajaxHttp');
const { apiPath } = require('appService/api');
require('./statement-list.scss');
const template = require('./statement-list.tpl');

const StatementList = {
	name: "statement-list",

	template,

	data() {
		return {
			years: [{
				"text": '年份',
				"value": -1
			}],
			months: [{
				"text": '月份',
				"value": -1
			}],
			params: {
				"_size": 20,
				"type": -1,
				"year": -1,
				"month": -1
			},
			list: null,
			"pageItems": {
        "pages": 10,
        "current": 1,
        "page": 0
      },
		}
	},

	ready() {
		let tmpYears = this.getYears(2013);
		this.years = this.years.concat(tmpYears);
		let tmpMonths = this.getMonths();
		this.months = this.months.concat(tmpMonths);

		this.fetch();
	},

	methods: {

		getYears(start) {
			if (isNaN(start)) {
				return false;
			}

			let years = [];
			let date = new Date();
			let curYear = date.getFullYear();

			let difNums = curYear - start;

			for (let i = 0; i < difNums+1; i++) {
				let tmp = {
					value: start + i,
					text: (start + i) + '年'
				}
				years.push(tmp)
			}

			return years;
		},

		getMonths() {
			let months = [];

			for (let i = 1; i < 13; i++) {
				let tmp = {
					text: i + '月',
					value: i
				}
				months.push(tmp);
			}

			return months;
		},

		getParams(obj) {
			let params = {};
			for (let k in obj) {
				if (obj[k] != -1) {
					params[k] = obj[k];
				}
			}

			return params;
		},

		fetch() {
			let params = this.getParams(this.params);

			ajaxHttp(apiPath.financial.statementList, {
				data: params
			}).then((res)=> {
				this.list = res.data.records;
				this.pageItems.pages = res.data.pages;
        this.pageItems.page = res.data.current;
			}).catch((res)=> {
				tip(res.message);
			});

		},

		setType(type) {
			if (this.params.type == -1) {
				this.params.type = type;
			} else {
				this.params.type = -1;
			}

			this.fetch();
		},

		setYears() {
			console.log(2)
		}
	},

  store,

  events: {
  	[dropMenuEvent.menu.change](opt) {
  		if (opt.queryName == 'years' && opt.value != -1) {
  			this.params.year = opt.value;
  			this.fetch();
  		}

  		if (opt.queryName == 'months' && opt.value != -1) {
  			this.params.month = opt.value;
  			this.fetch();
  		}
  	},

    switchPage(page) {
      this.pageItems.page = page;
      this.getStallList();
    }
  }
}

module.exports = StatementList;


