require('components/base/checkbox/checkbox');
require('components/base/input-box/input-box');
require('components/common/table-data/table-data');

const store = require('appVuex/store');
const tip = require('components/base/pop/tip');
const ajaxHttp = require('appUtil/ajaxHttp.js');

const { apiPath } = require('appService/api');
const { setBreadCrumb } = require('src/common/vuex/action/common');

require('./task-center.scss');
const template = require('./task-center.tpl');

const TaskCenter = {
  name: "task-center",

  template,

  data() {
    return {
      thItems: ["标题", "时间", "内容", "状态"],
      url: apiPath.task.list,
      queryOpt: {
        checkFailTask: []
      },
      trItems: [],
      pageItems: {},
      failTaskCount: 0
    }
  },

  vuex: {
    actions: {
      setBreadCrumb
    }
  },

  store,

  filters: {
    taskStatus(statu) {
      if (statu === -1) {
        return '失败';
      } else if(statu === 0) {
        return '进行中';
      } else {
        return '成功';
      }
    },
    taskColor(statu) {
      if (statu === -1) {
        return 'danger-color';
      } else if(statu === 0) {
        return 'success-color';
      } else if(statu === 1) {
        return '';
      } else {
        return 'default-color';
      }
    }
  },

  watch: {
    "queryOpt": {
      handler: function (val, oldVal) {
        if (this.queryOpt.checkFailTask.length > 0) {
          this.queryOpt.exStatus = -1;
        } else {
          this.queryOpt.exStatus = 0;
        }

        this.tableData();
      },
      deep: true
    }
  },

  methods: {
    _init() {
      let arr = [{
        name: `任务中心`
      }, {
        name: `任务列表`
      }];

      this.setBreadCrumb(arr);
      this.tableData();
    },

    tableData() {
      this.$refs.tableData.showLoading();

      ajaxHttp(this.url, {
        data: this.queryOpt
      }).then((rtn) => {
        this.failTaskCount = this.countFailTask(rtn.data.records);

        this.trItems = rtn.data.records;
        this.pageItems = rtn.data;

        this.$refs.tableData.hideLoading();
      }).catch((rtn) => {
        tip(rtn.message);

        this.$refs.tableData.hideLoading();
      })
    },

    countFailTask(list) {
      var count = 0;

      list.forEach((item) => {
        if (item.exStatus === -1) {
          count++;
        }
      })

      return count;
    }
  },

  ready() {
    this._init();
  }
}

module.exports = TaskCenter;


