require('components/base/icon/icon');
require('components/base/bubble/bubble');

const { apiPath } = require('appService/api');
const { setLoginInfo } = require('appVuex/action/login');
const { setTaskTip, setMessageTip } = require('appVuex/action/headerLayout');

const store = require('appVuex/store');
const alert = require('components/base/pop/alert');
const tip = require('components/base/pop/tip');
const ajaxHttp = require('appUtil/ajaxHttp');
const spinner = require('components/base/loading/spinner');

require('./header-layout.scss');
const template = require('./header-layout.tpl');

const SET_INTERVAL_TIME = 10000;

module.exports = {
  template,

  props:{
    breadCrumbData: Object
  },

  data: function() {
    return {
      loginInfo: {},
      messageTip: "",
      taskTip: "",
      setTimeoutForTaskTip: () => { },
      setTimeoutForMessageTip: () => { },
      setIntervalForFetchTip: () => {}
    }
  },

  vuex: {
    getters: {
      taskBubbleTxt: state => state.headerLayout.taskTip,
      messageBubbleTxt: state => state.headerLayout.messageTip
    },
    actions: {
      setLoginInfo,
      setTaskTip,
      setMessageTip
    }
  },

  store,

  methods: {
    _init() {
      this.loadUserInfo();

      this.setIntervalForFetchTip = setInterval(() => {
        ajaxHttp(apiPath.message.tip).then((res) => {
          if(res.data){
            this.setMessageTip(res.data);
            this.messageTip = res.data;
          }
        }).catch((res)=>{
          tip(res.message);
        });

        ajaxHttp(apiPath.task.tip).then((res) => {
          var data = res.data;

          if(!data || data.length === 0){
            return false;
          }

          var taskTip = '';
          var failureTaskNum = 0;

          data.forEach((item, index)=>{
            taskTip += '任务完成：'
            taskTip += data.successful !==0 ? `${item.successful}个车位设置成功，` : '';
            taskTip += data.failure !==0 ? `${item.failure}个车位设置失败` : '';
            taskTip += '\n';

            if(item.failure > 0){
              failureTaskNum++;
            }
          })

          this.setTaskTip($.trim(taskTip));
          this.taskTip = `任务中心：您有 ${failureTaskNum} 个失败任务`;
        }).catch((res)=>{
          tip(res.message);
        });
      }, SET_INTERVAL_TIME)
    },

  	loadUserInfo() {
      ajaxHttp(apiPath.login.userInfo, {}).then((res)=>{
        this.loginInfo = res.data;
        this.setLoginInfo(this.loginInfo);
      }).catch((res)=>{
        tip(res.message);
      });
    },

    logout() {
      this.$refs.confirmLogoutPop.show();
    },

    showAvatarMenu() {
      this.$refs.avatarMenu.show(this.$els.avatar)
    },

  	okcb() {
      ajaxHttp(apiPath.login.logout, {}).then((res) => {

        this.$refs.confirmLogoutPop.hide()
        clearInterval(this.setIntervalForFetchTip);
        tip('退出成功！');

        window.localStorage.removeItem('loginInfo');
        window.localStorage.removeItem('TOKEN');
        window.SAAS.TOKEN = '';

        setTimeout(()=>{
          this.$router.go({
            path: '/login'
          });
        }, 3000);

      }).catch((res)=> {
        tip(res.message);
      })
  	},

    _initBreadCrumbData({data, path} = {}) {
      if (!data || !path) {
        return false;
      }

      let breadCrumbs = [];
      let indexs = [];

      let search = function(list, index) {
        for(let i = 0, length = list.length; i < length; i++) {
          let e = list[i];
          let link = e.url;
          let sub = e.sub;

          indexs[index] = i;
          breadCrumbs[index] = {
            name: e.description,
            router: link
          };

          if(link && path.indexOf(link) > -1) {
            breadCrumbs[index] = {
              name: e.description,
              router: link
            }

            if (!sub || sub.length === 0) {
              return true;
            } else if(sub && sub.length !== 0 && search(sub, index + 1)) {
              return true;
            }
          }

          if(sub && sub.length !== 0 && search(sub, index + 1)) {
            return true;
          }
        }

        return false;
      };

      if (search(data, 0)) {console.log
        this.$refs.breadCrumb.setPartBreadCrumb(breadCrumbs);
      }

      return false;
    }
  },

  watch: {
    breadCrumbData(val) {
      if (val) {
        this._initBreadCrumbData(val);
      }
    },
    taskBubbleTxt(val){
      if (val) {
        clearTimeout(this.setTimeoutForTaskTip);

        this.$refs.taskTipBubble.show(this.$els.taskTipEl);

        this.setTimeoutForTaskTip = setTimeout(() => {
          this.$refs.taskTipBubble.hide();
        }, 5000)
      }
    },
    messageBubbleTxt(val){
      if (val) {
        clearTimeout(this.setTimeoutForMessageTip);

        this.$refs.messageTipBubble.show(this.$els.messageTipEl);

        this.setTimeoutForMessageTip = setTimeout(() => {
          this.$refs.messageTipBubble.hide();
        }, 5000)
      }
    }
  },

  computed: {
    isMessagePage() {
      return this.$route.path === '/page/account/account-list/message';
    },
    isTaskPage() {
      return /page\/account\/account-list\/task/.test(this.$route.path);
    }
  },

  ready() {
    this._init();
  }
}


