require('components/common/table-data/table-data');


require('./account-list.scss');
const template = require('./account-list.tpl');

const store = require('appVuex/store');
const { apiPath } = require('appService/api');
const { dropMenu: dropMenuEvent } = require('components/config/event.json');

const AccountList = {
	name: "account-list",

	template,

	data() {
    return {
      merchants: '',
    	thItems: ["序号","账户","姓名","电话","邮箱","状态","最后登录时间","操作"],
      url: apiPath.account.accountList,
      statusItems: [{
        value: -1,
        text: "状态"
      },{
        value: 0,
        text: "禁用"
      },{
        value: 1,
        text: "启用"
      }]
    }
	},

  ready() {
    this.getMerchants();
  },

	methods: {
    getMerchants() {
      var self = this;
      $.ajax({
        type: "get",
        url: apiPath.account.getMerchants,
        success(rtn) {
          if(rtn.code === 0) {
            self.merchants = rtn.data.merchants
          }
        }
      });
    },
    qurryAccountList() {
      let queryParams = {
        keyWord: this.$refs.keywordInputbox.val(),
        status: this.$refs.statusDropmenu.val()
      };
      this.$refs.tableData.query(queryParams).fetch();
    }
	},

  store
}

module.exports = AccountList;


