require('components/base/input-box/input-box');
require('components/base/checkbox/checkbox');
require('components/base/drop-menu/drop-menu');
require('components/base/btn/btn');
require('components/base/form-area/form-area');

const dataPicker = require('components/common/data-picker/data-picker');
const tip = require('components/base/pop/tip');
const store = require('appVuex/store');
const ajaxHttp = require('appUtil/ajaxHttp');

const { dropMenu: dropMenuEvent } = require('components/config/event.json');
const { apiPath } = require('appService/api');
const { setBreadCrumb } = require('src/common/vuex/action/common');
const { chargingTemplate } = require('appVuex/action/marketing');
const { time } = require('src/common/utils/date');

const template = require('./charging-template-add.tpl');
const timeShareFormTpl = require('./time-share-form.tpl');
require('./charging-template-add.scss');

module.exports = {
  name: "charging-template-add",

  template,

  components: {
    'data-picker': dataPicker,
    "time-share-form": {
      props: {
        params: Object
      },
      template: timeShareFormTpl
    }
  },

  data() {
    return {
      beginTime: '',
      endTime: '',
      now: time(new Date().getTime()),
      params: {
        billWay: 0,
        isTimeSharing: 0,
        serviceFeeType: 1,
        serviceFee: 0,
        unitPrice: 0,
        shareTimeList:[]
      },
      timeArray: [],
      chargeTabs: [{
        text: '尖峰',
        value: 1,
        price: 0
      }, {
        text: '高峰',
        value: 2,
        price: 0
      }, {
        text: '平峰',
        value: 3,
        price: 0
      }, {
        text: '低谷',
        value: 4,
        price: 0
      }],
      shareTimeList: [{
        timeShareType: 1,
        startTime: '00:00',
        endTime: '24:00',
        unitPrice: 0,
        endTimeArray: []
      }],
      baseId: this.$route.params.id,
      operatorListUrl: apiPath.common.operatorList,
      processDropItems(data){
        var arr = [];

        arr.push({
          text: data.shortName,
          value: data.commId
        })

        data.childs.forEach((item)=>{
          arr.push({
            text: item.shortName,
            value: item.commId
          })
        })

        return arr;
      }
    };
  },

  vuex: {
    actions: {
      setBreadCrumb,
      add({ dispatch, state }, queryOpt) {
        ajaxHttp(apiPath.marketing.addChargingTemplate, {
          type: 'post',
          data: JSON.stringify(queryOpt),
          contentType: 'application/json'
        }).then((res) => {
          this.$router.go({
            name: 'chargingTemplate'
          });
        }).catch(() => {
          return false;
        })
      }
    }
  },

  store,

  methods: {
    _init() {
      let name = this.$route.query.name;
      let arr = [{
        name: "新增计费模板"
      }];

      if (name) {
        arr = [{
          name: name,
          router: `/page/marketing/charging-template/version/${this.$route.params.id}?name=${name}`
        }, {
          name: "新增版本"
        }]
      }
      

      COMMON.componentHub.breadCrumb = arr;

      this._initDatatime(this.shareTimeList[0]);
    },

    _initDatatime(endTimeSelectHost) {
      var timeArr = [];
      var startTime = endTimeSelectHost.startTime;
      var endTimeArray = endTimeSelectHost.endTimeArray;
      var hour = parseInt(startTime.split(':')[0], 10);
      var min = parseInt(startTime.split(':')[1], 10);

      var i = 1;
      i += hour*2;
      if (min === 30) {
        i++;
      }

      endTimeArray.splice(0, endTimeArray.length);

      for (; i < 49; i++) {
        let h = Math.floor(i/2);
        let hStr = h > 9 ? h : '0' + h;
        let mStr = i % 2 === 0 ? '00' : '30';
        let time = hStr + ':' + mStr;

        endTimeArray.push({
          text: time,
          value: time
        });
      }
    },

    addItem(index) {
      let time = '';

      if(index || index === 0) {
        time = this.shareTimeList[index].endTime;
      }

      var shareTimeListLength = this.shareTimeList.push({
        timeShareType: 1,
        startTime: time,
        endTime: '24:00',
        unitPrice: 0,
        endTimeArray:[]
      });

      this._initDatatime(this.shareTimeList[--shareTimeListLength]);
    },

    deleteItem(item, index) {
      this.shareTimeList.splice(index, 1);

      if(this.shareTimeList.length === 0) {
        this.addItem();
      }

      this.shareTimeList[0].startTime = '00:00';
      this.checkTime(0);
    },

    changeTime(item, index) {
      if(!item.endTime) {
        return;
      }
      if(item.startTime && !this.isValidTime(item.startTime, item.endTime)) {
        tip('请输入正确的时间且开始时间不能大于结束时间');
        item.endTime = '24:00';
        return;
      }
      this.checkTime(0);
    },

    checkTime(index) {
      let item = this.shareTimeList[index];
      if(index < 4) {
        let nextItem = this.shareTimeList[index+1];
        if(nextItem) {
          nextItem.startTime = item.endTime;
          if(nextItem.startTime == nextItem.endTime) {
            this.deleteItem(nextItem, index+1);
          }
          if(nextItem.endTime && !this.isValidTime(nextItem.startTime, nextItem.endTime)) {
            nextItem.endTime = '';
          }
        }
        this.checkTime(index+1);
      }
    },

    isValidTime(...time) {
      if(!time[0] || !time[1]) {
        return false;
      }
      let t1 = time[0].split(':');
      let h1 = parseInt(t1[0]);
      let s1 = parseInt(t1[1]);
      let t2 = time[1].split(':');
      let h2 = parseInt(t2[0]);
      let s2 = parseInt(t2[1]);
      if(h1 > h2) {
        return false;
      } else if(h1 === h2 && s1 > s2) {
        return false;
      }
      return true;
    },

    showAdd(item, val) {
      return item.endTime != '24:00' && val === this.shareTimeList.length-1;
    },

    submit() {
      if (!this.$refs.formArea.verify()) {
        return false;
      }

      this.params.shareTimeList = this.shareTimeList;

      this.params.shareTimeList.forEach((item) => {
        item.unitPrice = this.timeShareTypePrice(item.timeShareType);
      });

      if(this.validate(this.params)) {
        this.$set('params', this.params);
        this.$refs.confirm.show();
      }
    },

    handleOk() {
      this.params.serviceFee = parseFloat(this.params.serviceFee, 10) * 100;
      this.params.unitPrice = parseFloat(this.params.unitPrice, 10) * 100;

      this.params.shareTimeList.forEach((item) => {
        item.unitPrice = parseFloat(item.unitPrice, 10) * 100;
      });

      this.add(this.params);
    },

    validate() {
      let value = this.shareTimeList;

      if(value.length > 0 && value[value.length - 1].endTime != '24:00') {
        tip('分时计费时间段设置没有覆盖24小时！');

        return false;
      }

      return true;
    },

    timeShareTypePrice(val) {
      var price = 0;

      this.chargeTabs.every((item) => {
        if (item.value === val) {
          price = item.price;

          return false;
        }

        return true;
      })

      return price;
    }
  },

  ready() {
    this._init();
  },

  filters: {
    timeShareType(val) {
      return ['', '尖峰', '高峰', '平峰', '低谷'][val];
    }
  }
};



