// 设置路由配置
const routerView = require('components/layout/router-view/router-view');
const messageSetting = require('appComp/page/settings/message/message-setting');

module.exports = {
  "component": routerView,
  "subRoutes": {
    "/message": {
      "title": "消息设置",
      "component": messageSetting,
    }
  }
}


