//客户详情
const clientLayout = require('appComp/page/client/client-layout/client-layout');
const clientList = require('appComp/page/client/client-list/client-list');
const clientDetail = require('appComp/page/client/client-detail/client-detail');

//卡片列表
const cardList = require('appComp/page/client/card/card-list/card-list');

module.exports = {
  "component": clientLayout,
  "subRoutes": {
    "/client-list": {
      "title": "客户列表",
      "component": clientLayout,
      "subRoutes": {
        "/": {
          "component": clientList
        },
        "/detail": {
          "title": "客户详情",
          "component": clientDetail
        }
      }
    },
    "/card-list": {
      "title": "卡片列表",
      "component": clientLayout,
      "subRoutes": {
        "/": {
          "component": cardList
        }
      }
    }
  }
}




