require('components/common/table-data/table-data');
require('components/base/pagination/pagination');
require('components/common/data-picker/data-picker');
const store = require('appVuex/store');
const ajaxHttp = require('appUtil/ajaxHttp');
const { time } = require('src/common/utils/date');
const { apiPath } = require('appService/api');
const { setBreadCrumb } = require('src/common/vuex/action/common');
const { dropMenu: dropMenuEvent } = require('components/config/event.json');

require('./statement-detail.scss');
const template = require('./statement-detail.tpl');

const StatementDetail = {
	name: "statement-detail",

	template,

	data() {
    let beginTime = (this.$route.query.beginTime).replace(/年|月/g, '-').replace('日', '');
    let endTime = (this.$route.query.endTime).replace(/年|月/g, '-').replace('日', '');

		return {
			thItems: ["编号", "类型", "交易流水（元）","时间","备注", "操作"],
      url: apiPath.financial.statementDetail,
      searchParams: {
    		"_size": 20,
    		"beginTime": beginTime,
      	"endTime": endTime
    	},
      now: time(new Date().getTime()),
      types: [{
				text: '全部',
				value: 0
			},{
      	"value": 1,
      	"text": "订单入账"
      }, {
      	"value": 2,
      	"text": "套餐购买"
      }, {
      	"value": 3,
      	"text": "提现"
      }, {
      	"value": 4,
      	"text": "充值"
      }],
      params: {
      	"beginTime": beginTime,
      	"endTime": endTime,
      	"_size": 20,
      	"type": -1,
      	"orderId": ""
      },
      token: window.SAAS.TOKEN,
      exportUrl: apiPath.financial.statementDetailExport
		}
	},

	ready() {
		this.init();

	},

	methods: {
		init() {
      let month = this.fetchTime(this.$route.query.beginTime, 'M');
			let arr = [{
        name: `${month}月对账单`
      }];

     COMMON.componentHub.breadCrumb = arr;
		},

    fetchTime(str, timeKind){
      let tStr = str.replace(/年|月/g, '/').replace('日', '');
      let date = new Date(tStr);

      if (timeKind == 'Y') {
        return date.getFullYear();
      }

      if (timeKind == 'M') {
        return date.getMonth() + 1;
      }

      if (timeKind == 'D') {
        return date.getDate();
      }

      return '时间格式请传入Y|M|D';
    },

		getParams(obj) {
			let params = {};
			for (let k in obj) {
				if (obj[k] != -1 && obj[k] != '') {
					params[k] = obj[k];
				}
			}

			return params;
		},

		search() {

			this.searchParams = Object.assign(this.searchParams, this.params);

    	this.$refs.tableData.fetch();
		},

		exportsUrl() {
			let current = this.$refs.tableData.pageItems.current;
			let url = `${ this.exportUrl }?orderId=${ this.params.orderId }&beginTime=${ this.params.beginTime }&endTime=${ this.params.endTime }&current=${ current }&size=20&token=${ this.token }`;
			window.open(url);
		}
	},

	vuex: {
    getters: {
    },
    actions: {
      setBreadCrumb
    }
  },

  store,

  watch: {
  	"params.beginTime": function(val, oldval) {
  		if (val) {
  			this.params.beginTime = $.trim(val);
  		}
  	},
  	"params.endTime": function(val, oldval) {
  		if (val) {
  			this.params.endTime = $.trim(val);
  		}
  	},
  	"params.orderId": function(val, oldval) {
  		if (val) {
  			this.params.orderId = $.trim(val);
  		}
  	}
  }
}

module.exports = StatementDetail;


