require('components/base/btn/btn');
require('components/common/list/list');

const store = require('appVuex/store');
const tip = require('components/base/pop/tip');
const ajaxHttp = require('appUtil/ajaxHttp.js');

const { apiPath } = require('appService/api');
const { setBreadCrumb } = require('src/common/vuex/action/common');

require('./task-detail.scss');
const template = require('./task-detail.tpl');

const TaskDetail = {
  name: "TaskDetail",

  template,

  data() {
    return {
      thItems: ["标题", "时间", "内容", "状态"],
      url: apiPath.task.detail,
      queryOpt: {
        tdLogId: ""
      },
      listData: {
        subLoglist: [],
        detail: {}
      },
      failTaskNum: 0,
      taskInProgressNum: 0
    }
  },

  vuex: {
    actions: {
      setBreadCrumb
    }
  },

  store,

  filters: {
    subTaskStatu(statu) {
      if (statu === -1) {
        return '失败';
      } else if(statu === 0) {
        return '进行中';
      } else {
        return '成功';
      }
    },
    subTaskColor(statu) {
      if (statu === -1) {
        return 'danger-color';
      } else if(statu === 0) {
        return 'success-color';
      } else if(statu === 1) {
        return '';
      } else {
        return 'default-color';
      }
    }
  },

  methods: {
    _init() {
      this.queryOpt.tdLogId = this.$route.params.id;
      this.tableData()
    },

    tableData() {
      this.taskInProgressNum = 0;
      this.failTaskNum = 0;

      ajaxHttp(this.url, {
        data: this.queryOpt
      }).then((rtn) => {
        rtn.data.subLoglist.forEach((item, index) => {
          item.forEach((subItem) => {
            subItem.exContent = JSON.parse(subItem.exContent);

            if (subItem.exSubStatus === 0) {
              this.taskInProgressNum++;
            } else if (subItem.exSubStatus === -1) {
              this.failTaskNum++;
            }
          })
        });

        this.listData.subLoglist = rtn.data.subLoglist;
        this.listData.detail = rtn.data.tdCarStationLog;
      }).catch((rtn) => {
        tip(rtn.message);
      })
    },

    reTryTask() {
      ajaxHttp(apiPath.task.reTryTask, {
        data: {
          tdLogId: this.listData.detail.tdLogId
        }
      }).then((rtn) => {
        tip('重试成功')
        this.tableData();
      }).catch((rtn) => {
        tip(rtn.message);
      })
    }
  },

  ready() {
    this._init();
  }
}

module.exports = TaskDetail;


