require('components/common/table-data/table-data');
require('components/common/search-tool/search-tool');
require('components/base/drop-menu/drop-menu');
require('components/base/input-box/input-box');
require('components/base/bubble/bubble');
require('components/base/btn/btn');
require('components/base/loading/loading');

const store = require('appVuex/store');
const tip = require('components/base/pop/tip');
const alert = require('components/base/pop/alert');
const { apiPath } = require('appService/api');
const { common: commonEvent } = require('components/config/event.json');

require('./net-controller.scss');
const template = require('./net-controller.tpl');

const NetController = {
  name: "net-controller",

  template,

  data() {
    return {
      thItems: ["控制器ID", "类型", "硬件版本", "软件版本", "所属站点", "运营商", "枪头数", "最近心跳时间", ""],
      url: apiPath.maintain.netController.list,
      updateUrl: apiPath.maintain.netController.update,
      optionItems: [{
        value: -1,
        text: "类型"
      }, {
        value: 0,
        text: "内置"
      }, {
        value: 1,
        text: "外置"
      }],
      queryOpt: {},
      fetchQuery: ({ queryOpt }) => {
        this.$refs.tableData.query({
          "boxChargerType": queryOpt['boxChargerType'] ? queryOpt['boxChargerType'].value : null,
          "boxOutFactoryCode": queryOpt['boxOutFactoryCode'],
          "boxVersion": queryOpt['boxVersion'] ? queryOpt['boxVersion'].value : null,
          "telVersion": queryOpt['telVersion'] ? queryOpt['telVersion'].value : null,
          "searchKey": queryOpt['searchKey'],
        }).fetch();
      },
      currentItem: {},
      versionDropMenuUrl: apiPath.maintain.netController.version
    }
  },

  store,

  methods: {
    netControllerDetail(trItem) {
      this.currentItem = trItem;

      this.$router.go({
        path: "/page/maintain/netController/detail",
        query: {
          deviceId: this.currentItem.boxCode
        }
      })
    },

    update(item) {
      this.currentItem = item;

      $.get(apiPath.maintain.netController.updateStatus, {
        boxRawId: this.currentItem.boxOutFactoryCode
      }, (rtn) => {
        if (rtn.data === 0 || rtn.data === 100 || rtn.data === -200) {
          this.$refs.updateControllerPop.show();
          return false;
        }

        tip(`${ rtn.data === -1 ? '盒子不存在' : '维护(升级中)' }`);
      });
    },

    okUpdateController() {
      this.$refs.updateControllerForm.submit();
    },

    updateNetControllerConfirm() {
      if (!this.$refs.updateControllerForm.verify()) {
        return false;
      }

      this.$refs.updateControllerConfirm.show();
      this.$refs.updateControllerPop.hide();
    },

    updateFormSuccess(rtn) {
      this.$refs.updateControllerConfirm.hide();

      if (rtn.code === 0) {
        this.$refs.updateControllerWaiting.show();
        this.checkUpdateStatusLoop();

        return false;
      }

      alert({
        message: rtn.message
      });
    },

    checkUpdateStatusLoop() {
      var timeInterval = setInterval(() => {

        $.get(apiPath.maintain.netController.updateStatus, {
          boxRawId: this.currentItem.boxOutFactoryCode
        }, (rtn) => {
          if (rtn.code === 0 && rtn.data === 100) {
            this.$refs.updateControllerWaiting.hide();
            alert({
              message: "升级成功"
            });

            clearInterval(timeInterval);

            return false;
          }

          tip(rtn.message);
        });

      }, 60000);

      setTimeout(() => {
        this.$refs.updateControllerWaiting.hide();
        alert({
          message: "升级失败，请重新升级"
        });

        clearInterval(timeInterval);
      }, 300000);

    }
  },

  filters: {
    boxVersion(val) {
      return `${val.boxVersion ? `G-${val.boxVersion}/` : ''}
        ${val.clientVersion ? `M-${val.clientVersion}/` : ''}
        ${val.firmwareVersion ? `C-${val.firmwareVersion}` : ''}`
    }
  }
}

module.exports = NetController;


