//设置任务的tip
const setTaskTip = ({ dispatch, state }, tip) => {
  return dispatch('SET_HEADER_TASK_TIP', tip);
}

//设置消息的tip
const setMessageTip = ({ dispatch, state }, tip) => {
  return dispatch('SET_HEADER_MESSAGE_TIP', tip);
}

module.exports = {
  setTaskTip,
  setMessageTip
}



