/**
 * btn 组件
 *
 * @props theme - 主题
 * @props kind - 按钮种类
 * @props type - 按钮类型 (input | button | link)
 * @props size - 按钮大小
 * @props value - 按钮名字
 * @props submit - 提交按钮
 *
 * @events click - 点击btn事件
 */

const Vue = require('vue');

const baseMixin = require('components/mixin/base');
const formMixin = require('components/mixin/form');
const { btn: btnEvent } = require('components/config/event.json');

const template = require('./btn.tpl');
require('./btn.scss');

const BTN_TYPE_LINK = 'link';
const BTN_TYPE_BUTTON = 'button';
const BTN_TYPE_INPUT = 'input';

const SIZE_S = "S";
const SIZE_M = "M";
const SIZE_L = "L";

const Btn = {
  name: "Btn",

  mixins: [baseMixin, formMixin],

  template,

  props: {
    theme: {
      type: String,
      default: "primary"
    },

    kind: {
      type: String,
      default: "primary"
    },

    type: {
      type: String,
      default: BTN_TYPE_LINK
    },

    value: {
      type: String,
      require: true
    },

    size: {
      type: String,
      default: SIZE_S
    },

    submit: {
      type: Boolean,
      require: false
    }
  },

  methods: {
    /**
     * 点击按钮
     * @return {Object} this - 组件
     */
    click() {
      this.$dispatch(btnEvent.click, {
        dispatcher: this
      });

      return this;
    }
  },

  computed: {
    btnClass() {
      return `btn-${this.kind}`;
    },

    sizeClass() {
      return this.size.toLowerCase();
    },

    isLink() {
      return this.type === BTN_TYPE_LINK;
    },

    isButton() {
      return this.type === BTN_TYPE_BUTTON;
    },

    isInput() {
      return this.type === BTN_TYPE_INPUT;
    }
  }
}

module.exports = Vue.component('btn', Btn);


