
require('components/base/input-box/input-box');
require('components/base/drop-menu/drop-menu');
require('components/common/tab-panel/tab-panel');
require('components/base/checkbox/checkbox');

const store = require('appVuex/store');
const ajax = require('appUtil/ajax');
const { apiPath } = require('appService/api');

require('./net-controller-detail.scss');
var template = require('./net-controller-detail.tpl');
const deviceInfoTpl = require('./device-info.tpl');
const paramSettingTpl = require('./param-setting.tpl');

const NetController = {
  name: "net-controller-detail",

  template,

  components: {
    'param-setting': {
      props: {
        detailMessage: Object
      },
      template: paramSettingTpl
    },
    'device-info': {
      props: {
        chargeApiTh: Array,
        carLockerTh: Array,
        sensorTh: Array,
        detailMessage: Object
      },
      template: deviceInfoTpl
    }
  },

  data() {
    return {
      tabItems: [{
        text: '参数配置',
        name: 'setting'
      }, {
        text: '设备信息',
        name: 'message'
      }],
      detailMessage: {},
      chargeApiTh: ["充电接口ID", "电流（A）", "电压（V）", "电量（kWh）", "最近心跳时间"],
      carLockerTh: ["车位锁ID", "电量（%）", "最近心跳时间"],
      sensorTh: ["感应器ID", "类型", "最近心跳时间"]
    }
  },

  store,

  methods: {
    _init() {

      ajax('get', apiPath.maintain.netControllerDetail).
        query({
          boxCode: this.$route.query.deviceId
        }).
        end((err, res) => {
          if (res.body.code === 0) {
            this.detailMessage = res.body.data;
          } else {
            console.warn('获取失败');
          }
        });
    }
  },
  ready() {
    this._init();
  }
}

module.exports = NetController;


