require('components/base/btn/btn')

const store = require('appVuex/store');
const { apiPath } = require('appService/api');

require('./charging-template.scss');
const template = require('./charging-template.tpl');

const ChargingTemplate = {
  name: "charging-template",

  template,

  data() {
    return {
      thItems: ["模板名称", "版本号", "充电费", "服务费", "运营商", "生效日期", "失效日期", "创建日期"],
      url: apiPath.marketing.chargingTemplateList,
      queryOpt: {
        _size: 20
      },
      fetchQuery: ({queryValue}) => {
        this.$refs.tableData.query(queryValue).fetch();
      }
    }
  },

  route: {
    data: (transition) => {
      document.title = '欢迎您使用充电网私有云服务系统！'
      transition.next()
    }
  },

  store
}

module.exports = ChargingTemplate;



