require('components/common/table-data/table-data');
require('components/base/input-box/input-box');
require('components/base/switching/switching');
require('components/base/checkbox/checkbox');
require('components/base/form-area/form-area');
require('components/base/drop-menu/drop-menu');
require('components/common/select-tree/select-tree');
require('./net-controller-create.scss');

const tip = require('components/base/pop/tip');
const template = require('./net-controller-create.tpl');
const store = require('appVuex/store');

const { apiPath } = require('appService/api');
const {
  checkbox: checkboxEvent,
  inputBox: inputEvent } = require('components/config/event.json');

const registerNetControllerTpl = require('./register-net-controller.tpl');
const settingNetControllerTpl = require('./setting-net-controller.tpl');
const successSaveControllerTpl = require('./success-save-controller.tpl');

const NetControllerCreate = {
  name: "net-controller-create",

  template,

  components: {
    'register-net-controller': {
      template: registerNetControllerTpl,
      props: {
        register: Function,
        registerUrl: String
      }
    },
    'setting-net-controller': {
      template: settingNetControllerTpl,
      props: {
        save: Function,
        external: Boolean
      }
    },
    'success-save-controller': {
      template: successSaveControllerTpl
    }
  },

  data() {
    return {
      external: true,
      registerUrl: apiPath.maintain.addNetController,
      operatorListUrl: apiPath.common.operatorList,
      processDropItems(data){
        var arr = [];

        arr.push({
          text: data.shortName,
          value: data.commId
        })

        data.childs.forEach((item)=>{
          arr.push({
            text: item.shortName,
            value: item.commId
          })
        })

        return arr;
      },
      treeData: {
        shortName: "充电网",
        commId: null,
        childs: [{
          shortName: "充电网商户1",
          commId: 160,
        },{
          shortName: "充电网商户2",
          commId: 161,
        }]
      }
    }
  },

  store,

  methods: {
    switchNext() {
      this.$refs.switching.next();
    },

    registerNetController(rtn) {
      if (rtn.code === 0) {
        tip('提交成功');
        this.$router.go({
          path: "/page/maintain/netController/detail",
          query: {
            deviceId: rtn.data.boxCode,
            tab: "message"
          }
        })
      } else {
        tip(rtn.message);
      }
    }
  },

  events: {
    [checkboxEvent.change](value, text) {
      this.external = value === 1 ? false : true;
    },
    [inputEvent.change](opt) {
      this.registerId = opt.value;
    }
  }
}

module.exports = NetControllerCreate;


