//运维业务组件
const routerView = require('components/layout/router-view/router-view');
const maintainLayout = require('appComp/page/maintain/maintain-layout/maintain-layout');
const netControllerDetail = require('appComp/page/maintain/net-controller/net-controller-detail/net-controller-detail');
const netControllerCreate = require('appComp/page/maintain/net-controller/net-controller-create/net-controller-create');
const netController = require('appComp/page/maintain/net-controller/net-controller');
const warningRecord = require('appComp/page/maintain/warning-record/warning-record');
const chargingInterface = require('appComp/page/maintain/charging-interface/charging-interface');

module.exports = {
  component: maintainLayout,
  subRoutes: {
    "/netController": {
      component: routerView,
      subRoutes: {
        "/": {
          title: "网络控制器",
          component: netController,
        },
        "/detail/": {
          title: "控制器详情",
          component: netControllerDetail
        },
        "/create": {
          title: "创建控制器",
          component: netControllerCreate
        }
      }
    },
    "/warningRecord": {
      title: "告警记录",
      component: warningRecord
    },
    "/chargingInterface": {
      title: "充电接口",
      component: chargingInterface
    }
  }
}


