/**
 * tip 组件
 */

const Vue = require('vue');

module.exports = (message) => {
  message = message || '后台无返回提示';

  return COMMON.router.app.$refs
    .commonComponent
    .$refs
    .tip
    .alertInfo(message)
    .show()
};


