//网站业务组件
const site = require('appComp/page/site/site.vue')
const siteLists = require('appComp/page/site/site-lists/site-lists');
const stayLists = require('appComp/page/site/stay-lists/stay-lists')
const parkingInfo = require('appComp/page/site/parking-info/parking-info');
const addSite = require('appComp/page/site/add-site/add-site');
const updateSite = require('appComp/page/site/update-site/update-site');
const survey = require('appComp/page/site/survey/survey');
const carControl = require('appComp/page/site/car-control/car-control');
const carDetails = require('appComp/page/site/car-details/car-details');
const monitor = require('appComp/page/site/monitor/monitor');
const logging = require('appComp/page/site/logging/logging')
const allSurvey = require('appComp/page/site/all-survey/all-survey');
const siteImg = require('appComp/page/site/site-img/site-img');

module.exports = {
  "component": site,
  "subRoutes": {
    "/addsite": {
      "name": "addsite",
      "title": "新增站点",
      "component": addSite
    },
    "/allsurvey":{
      "name":"allsurvey",
      "title":"概况",
      "component":allSurvey
    },
    "/sitelists": {
      "name": "SiteLists",
      "title": "站点列表",
      "component": site,
      'subRoutes':{
        "/":{
          "component":siteLists
        },
        "detail":{
          "component":site,
          "title":"车位概况",
          "subRoutes":{
            "/":{
              "component":survey
            },
            "/updatesite":{
              "component":updateSite
            },
            "/parkinginfo": {
              "title": "车位信息",
              "component":parkingInfo
            },
            "carcontrol":{
              "title":'车位监控',
              "component":site,
              "subRoutes":{
                "/":{
                  "component":carControl,
                },
                '/cardetails':{
                  "title":'车位详情',
                  "component":carDetails
                }
              },
            }
          }
        }
      }
    },
//  "/survey": {
//    "name": "Survey",
//    "title": "站点概况",
//    "component": survey
//  },
//  "/siteimg":{
//    "name":"SiteImg",
//    "title":"图片",
//    "component":siteImg
//  },
//  "/parkinginfo": {
//    "name": "ParkingInfo",
//    "title": "车位信息",
//    "component":parkingInfo
//  },
//  '/updatesite':{
//    name:'UpdateSite',
//    title:'修改站点详情',
//    component:updateSite
//  },
//  '/carcontrol':{
//    name:'CarControl',
//    title:'车位监控',
//    component:carControl
//  },
//  '/cardetails':{
//    name:'carDetails',
//    title:'车位详情',
//    component:carDetails
//  },
//  '/monitor':{
//    name:'monitor',
//    title:'监测数据',
//    component:monitor
//  },
    '/logging':{
      name:'logging',
      title:'日志记录',
      component:logging
    },
    '/staylists':{
      name:"Staylists",
      title:'待上线站点',
      component:site,
      "subRoutes": {
        "/": {
          "component": stayLists
        },
        "/detail": {
          "component": site,
          "title": "车位概况",
          "subRoutes": {
            "/": {
              "component": survey    
            },
            "/updatesite": {
              "component": updateSite
            },
            "/parkinginfo": {
              "title": "车位信息",
              "component":parkingInfo
            },
            '/carcontrol':{
              "title":'车位监控',
              "component":site,
              "subRoutes":{
                "/":{
                  "component":carControl,
                },
                '/cardetails':{
                  "title":'车位详情',
                  "component":carDetails
                }
              }
            },
          }
        }
      }
    },
  }
}


