//订单
const order = require('appComp/page/order/order/order');
const orderlist = require('appComp/page/order/order-list/order-list');
const orderstars = require('appComp/page/order/order-stars/order-stars');
const ordersurvey = require('appComp/page/order/order-survey/order-survey');
const orderdetail = require('appComp/page/order/order-detail/order-detail');

module.exports = {
  component: order,
  subRoutes: {
    "/orderlists": {
      "component": order,
      "title": "订单列表",
      "subRoutes": {
        "/": {
          "title": "订单列表",
          "component": orderlist,
        },
        "/orderstars": {
          "component": orderstars,
          "title": "星标订单"
        },
        "/orderdetail": {
          "component": orderdetail,
          "title": "订单详情"
        }
      }
    },
    "/ordersurvey": {
      "component": order,
      "title": "订单概况",
      "subRoutes": {
        "/": {
          "title": "订单概况",
          "component": ordersurvey
        }
      }
    }
  }
}


