require('./welcome.scss');
const template = require('./welcome.tpl');

module.exports = {
  name: "welcome",
  template,
  route: {
    data: function (transition) {
      document.title='欢迎您使用充电网私有云服务系统！'
      transition.next()
    }
  }
}


