const template = require('./car-control.tpl');

require('components/base/bubble/bubble');
const store = require('appVuex/store');
const Pagination = require('components/base/pagination/pagination');
require('./car-control.scss');
require('appUtil/ajaxSetting');
const { apiPath } = require('appService/api');
const ajaxHttp = require('appUtil/ajaxHttp');
const CarControl = {
  template,
  store,
  data() {
    return {
      dataBody: null,
      pageBol: false,
      introduceBol: false,
      areasItem: 0,
      typesItem: 0,
      areas: [],
      bindNums: '未知',
      searchGun: '',
      searchGuns: [],
      searchGunBol: false,
      baseUrl: apiPath.site.carControl,
      datas: {
        page: {}
      },
      controlTimer:{}
    }
  },

  route: {
    data: function(transition) {
      document.title = '欢迎您使用充电网私有云服务系统！'
      transition.next()
    }
  },

  computed: {
  },

  ready() {
    this._init();
    ajaxHttp(this.baseUrl.findAll,{
      data: {
        siteId: this.$route.query.id
      }
    }).then(res=>{
      if(res.code ==0){
        this.areas = res.data;
        this.areasItem = this.areas[0].id;
        this.dataBody = {
          'status': this.typesItem,
          'regionId': this.areasItem
        };
        this.postAjax(this.dataBody);
      }
    })
//  $.ajax({
//    type: 'get',
//    url: this.baseUrl.findAll,
//    dataType: 'json',
//    data: {
//      siteId: this.$route.query.id
//    },
//    success: (data) => {
//      if(data.code == 0) {
//        this.areas = data.data;
//        this.areasItem = this.areas[0].id;
//        this.dataBody = {
//          'status': this.typesItem,
//          'regionId': this.areasItem
//        };
//        this.postAjax(this.dataBody);
//      }
//    }
//  })
    let _self = this
//		this.controlTimer = setInterval(function() {
    		_self.postAjax({
    			regionId: _self.areasItem,
    			status: _self.typesItem,
//    		deviceId: _self.searchGun
    		})
//  	}, 5000)
  },

  attached() {},
  beforeDestroy(){
    clearInterval(this.controlTimer);
  },
  methods: {
    postAjax(a) {
      ajaxHttp(this.baseUrl.lists,{
        type:'post',
        data:JSON.stringify(a),
        contentType: 'application/json;charset=utf-8',
        dataType: 'json',
      }).then(res=>{
        if(res.code==0){
          this.datas = res.data;
        }
      })
//    $.ajax({
//      type: 'post',
//      url: this.baseUrl.lists,
//      contentType: 'application/json;charset=utf-8',
//      dataType: 'json',
//      data: JSON.stringify(a),
//      success: (data) => {
//        this.datas = data.data;
//      }
//    })
    },
    areasItems(a) {
      this.areasItem = a;
      this.searchGun = null;
      this.postAjax({
        'status': this.typesItem,
        'regionId': this.areasItem
      });
      this.pageBol = true;
    },
    typesItems(a) {
      this.typesItem = a;
      this.searchGun = null;
      this.postAjax({
        'status': this.typesItem,
        'regionId': this.areasItem
      });
      this.pageBol = true;
    },
    query(b) {
      this.searchGun = b.value;
      if(b.text == "枪头") {
        this.postAjax({
          'gunShow': this.searchGun,
          'siId': this.$route.query.id
        });
      } else {
        this.postAjax({
          'boxShow': this.searchGun,
          'siId': this.$route.query.id
        });
      }
      this.searchGunBol = false;
      this.searchGuns = null;
      this.pageBol = false;
    },
    searchLists() {
      let searchGun = this.searchGun.toString();
      ajaxHttp(this.baseUrl.boxCode,{
        type: 'post',
        dataType: 'json',
        data: {
          devcieCode: searchGun,
          siteId: this.$route.query.id
        },
        }).then(res=>{
        if(res.code==0){
          this.searchGuns = res.data;
          this.searchGunBol = true;
        }
      })
//    $.ajax({
//      type: 'post',
//      url: this.baseUrl.boxCode,
//      dataType: 'json',
//      data: {
//        devcieCode: searchGun,
//        siteId: this.$route.query.id
//      },
//      success: (data) => {
//        if(data.code == 0) {
//          this.searchGuns = data.data;
//          this.searchGunBol = true;
//        }
//      }
//    })
    },
    resetBol() {
      this.searchGunBol = false;
      this.searchGuns = null;
    },
    checkGun(a) {
      switch(a) {
        case 1:
          return `<p class="blue" data-toggle="tooltip" data-placement="bottom" title="枪头已插在车上,可能在充电">占用</p>`
        case 4:
          return `<p>空闲</p>`
        case 2:
          return `<p class="red" data-toggle="tooltip" data-placement="bottom" title="设备超过3分钟未上报">离线</p>`
        case 3:
          return `<p class="red" data-toggle="tooltip" data-placement="bottom" title="枪头上报:故障">故障</p>`
        case null:
          return `<p class="disable">--<div class="child2">未安装</div></p>`
      }
    },
    checkInduced(a) {
      switch(a) {
        case '70':
          return `<p class="blue" data-toggle="tooltip" data-placement="bottom" title="感应器检测到车位有车">占用</p>`
        case '10':
          return `<p>空闲</p>`
        case '0':
          return `<p class="red" data-toggle="tooltip" data-placement="bottom" title="设备超过3分钟未上报">离线</p>`
        case '-200':
          return `<p class="red">故障</p>`
        case null:
          return `<p class="disable">--<div class="child2">未安装</div></p>`
      }
    },
    checkLock(a) {
      switch(a) {
        case '10':
          return `<p class="blue" data-toggle="tooltip" data-placement="bottom" title="地锁处于放下的状态">解锁</p>`
        case '70':
          return `<p>锁定</p>`
        case '0':
          return `<p class="red" data-toggle="tooltip" data-placement="bottom" title="设备超过3分钟未上报">离线</p>`
        case '-200':
          return `<p class="red">故障</p>`
        case null:
          return `<p class="disable">--<div class="child2">未安装</div></p>`
      }
    },
    warn(a) {
      if(!a){
        return `<p>--</p>`
      }
      let b = a.split(":");
      switch(b[0]) {
        case '1':
          return `<p class="warn">${b[1]}</p>`
        case '3':
          return `<p class="warn2">${b[1]}</p>`
        case '2':
          return `<p class="warn1">${b[1]}</p>`
      }
    },
    _init() {

      let arr = [{
        name: `${this.$route.query.head}`,
        router: `${this.handlerUrl(this.$route.path.split('?')[0])}?id=${this.$route.query.id}&head=${this.$route.query.head}`
      }, {
        name: '车位监控'
      }];
      COMMON.componentHub.breadCrumb = arr;
    },
    handlerUrl(url) {
      return url.replace('/carcontrol', '');
    },
  },

  components: {
    Pagination
  },
  watch: {}
}
module.exports = CarControl;


