
require('components/common/tab-panel/tab-panel');

require('./client-detail.scss');
const template = require('./client-detail.tpl');

const BasicData = require('./basic-data/basic-data');
const OrderList = require('./order-list/order-list');
const Bond = require('./bond/bond');
const AccountDetail = require('./account-detail/account-detail');
const store = require('appVuex/store');
const { apiPath } = require('appService/api');

const ClientDetail = {
	name: "client-detail",

	template,

	data() {
    return {
      tabItems: [{
      	text: "基本资料",
      	name: "basicData"
      },{
      	text: "订单",
      	name: "orderList"
      },{
      	text: "账户明细",
      	name: "accountDetail"
      }]
    }
	},

	store,

	components: {
		BasicData,
		OrderList,
		Bond,
		AccountDetail
	}
}

module.exports = ClientDetail;



