module.exports = {
  show(cb) {
    return COMMON.router.app.$refs.commonComponent.$refs
      .loading
      .show(cb);
  },
  hide() {
    return COMMON.router.app.$refs.commonComponent.$refs
      .loading
      .hide();
  }
}



