require('./login.scss');
const ajax = require('appUtil/ajax');
const { setLoginInfo } = require('appVuex/action/login');
const template = require('./login.tpl');
const { apiPath } = require('appService/api');
const {delCookie} = require('src/common/utils/cookie');

module.exports = {
  name: "login",
  data() {
    return{
      params:{
        username: "",
        password: "",
        code: ""
      },
      error: "",
      warnText: "",
      hasError: false,
      isSubmiting: false,
      validateRules: {
        username: {
          required: true,
          empty: '请输入账号'
        },
        password: {
          required: true, empty: '请输入密码',
          reg: /^[^(\u4e00-\u9fa5)\s]{1,15}$/,
          error: '请输入8-15位非中文密码'
        },
        code: {
          required: true,
          empty: '请输入验证码'
        }
      },
      imgCode:'',
      codeId:''
    }
  },
  ready() {
    this.resizeHeight();
    this.getCode();
    $(window).on('resize', ()=>{
      this.resizeHeight();
    });
  },

  methods: {
    resizeHeight() {
      let pageHeight = document.documentElement.clientHeight;

      $('#login').css({"min-height": pageHeight});
      $('body').css('overflow', 'hidden');
    }, 

    validate() {
      let params = this.params;
      let rules = this.validateRules;
      for(let key in rules) {
        let rule = rules[key];
        let value = params[key];
        if(rule.required && !$.trim(value)) {
          this.hasError = true
          this.warnText =  rule.empty
          return false;
        }
        if(rule.reg && !rule.reg.test(value)) {
          this.hasError = true
          this.warnText =  rule.error
          return false;
        }
      }
      this.hasError = false
      this.warnText = ""
      return true;
    },

    login(){
      let url =  apiPath.login.login;
      let data = Object.assign({}, this.params, {"codeId": this.codeId})

      if(this.validate()){
        if(this.isSubmiting) return;

        this.isSubmiting = true;

        ajax('post', url, (res)=> {
          window.localStorage.setItem('TOKEN', res.body.data.token);
          SAAS.TOKEN = res.body.data.token;
          $.ajaxSettings.headers = {
            token: res.body.data.token
          }
          delCookie('pIndex');
          $('body').css('overflow', 'auto');
          this.$router.go({
            path: '/page/site/sitelists'
          });
        }, (res)=> {
          this.hasError = true
          this.warnText = res.body.message
          this.$router.go({
            path: '/login'
          });
        }).send(data)
        .end(()=> {
          this.isSubmiting = false;
        });
      }
    },

    getCode() {
      this.imgCode = '';
      let date = new Date();
      clearTimeout(window.SAAS.login.timer);
      this.codeId = date.getTime();

      window.SAAS.login.timer = setTimeout(()=> {
        this.imgCode = `${apiPath.login.code}?codeId=${this.codeId}`;
      }, 10);
    }
  },
  template,
  route: {
    data: function (transition) {
      document.title='欢迎您使用充电网私有云服务系统！'
      transition.next()
    }
  }
}



