require('components/common/table-data/table-data');
require('components/common/search-tool/search-tool');
require('components/base/input-box/input-box');

const tip = require('components/base/pop/tip');
const store = require('appVuex/store');
const { apiPath } = require('appService/api');

require('./charging-interface.scss');
const template = require('./charging-interface.tpl');

const ChargingInterface = {
  name: "charging-interface",

  template,

  data() {
    return {
      thItems: ["枪头ID", "所属站点", "对应控制器", "电流", "电压", "电量", "最近心跳时间", ""],
      url: apiPath.maintain.charger.chargerList,
      setQRCodeUrl: apiPath.maintain.charger.setQRCode,
      queryOpt: {},
      currentItem: {}
    }
  },

  store,

  methods: {
    setQRCode(item) {
      this.currentItem = item;
      if (!this.currentItem.qrCode) {
        this.$refs.qrCodeInput.val('');
      } else {
        this.$refs.qrCodeInput.val(this.currentItem.qrCode);
      }

      this.$refs.qrCodePop.show();
    },

    okcb() {
      $.post(apiPath.maintain.charger.setQRCode, {
        qrCode: this.$refs.qrCodeInput.val(),
        bcCode: this.currentItem.bcCode
      }, (rtn) => {
        if (rtn.code !== 0) {
          tip(rtn.message);
          return false;
        }
        tip('设置成功');
        this.$refs.tableData.fetch();

        this.$refs.setCodeForm.reset();
      });
      this.$refs.qrCodePop.hide();
    },

    fetchQuery({queryValue}) {
      this.$refs.tableData.query(queryValue).fetch();
    },
  }
}

module.exports = ChargingInterface;


