const Pagination = require('components/base/pagination/pagination');
const template = require('./stay-lists.tpl');
require('./stay-lists.scss');

const store = require('appVuex/store');
const { setBreadCrumb } = require('src/common/vuex/action/common');
const { apiPath } = require('appService/api');
const ajaxHttp = require('appUtil/ajaxHttp');
require('appUtil/ajaxSetting');
require('components/base/icon/icon');
const StayLists = {
	name: '',
	template,
	store,
	vuex: {
		actions: {
			setBreadCrumb,
		}
	},
	data() {
		return {
			page: {},
			currents: null,
			listsData: [],
			type: '',
			networkStatus: '',
			provincesArr: [],
			citiesArr: '',
			address: '',
			baseUrl:apiPath.site.stayLists,
			jump: null,
			siteTypeArr:[
      {text:'站点类型',value:''},
      {text:'公共',value:1},
      {text:'个人',value:2},
      {text:'营运',value:3}
      ],
      netTypeArr:[
      {text:'联网状态',value:''},
      {text:'已联网',value:1},
      {text:'未联网',value:2}
      ],
      size:20,
		}
	},

	route: {
		data: function(transition) {
			document.title = '欢迎您使用充电网私有云服务系统！'
			transition.next()
		}
	},

	computed: {

	},

	ready() {
	  this.getData();
	},

	attached() {},

	methods: {
	  getData(){
	    this.baseGet({_size:this.size});
	    ajaxHttp(this.baseUrl.selectPcd,{})
	    .then((res)=>{
	      if(res.code == 0) {
            this.provincesArr = res.data;
          }
	    })
	  },
	  baseGet(arg){
	    ajaxHttp(this.baseUrl.findWait,{
	      data:arg
	    }).then((res)=>{
	      if(res.code == 0) {
          this.listsData = res.data.records;
          let { current, pages, size, total } = res.data;
          this.$set('page', { current, pages, size, total });
        }
	    })
	  },
		changeNetwork(a) {
			if(a == 1) {
				return '已联网'
			} else {
				return '未联网'
			}
		},
		changeType(){
			if (this.type||this.networkStatus||this.AjaxPro||this.AjaxCit) {
			  let temp = {
			    type: this.type,
          networkStatus: this.networkStatus,
          province: this.AjaxPro,
          city: this.AjaxCit,
          _size:this.size
			  }
			  this.baseGet(temp)
			}else{
			  this.baseGet({_size:this.size})
			}
		},
		changeTypes(a) {
			switch(a) {
				case 1:
					return "公共"
				case 2:
					return "个人"
				case 3:
					return "营运"
			}
		},
		changeNetwork(a) {
			if(a==1) {
				return '是'
			} else{
				return '否'
			}
		},
		transfromProvince(a) {
			let tempPro = this.provincesArr;
			for(let value of tempPro) {
				if(value.id == a) {
					return value.pname;
					break;
				}
			}
		},
		transfromCity(a, b) {
			let tempPro = this.provincesArr;
			for(let value of tempPro) {
				if(value.id == a) {
					let tempCit = value.citys;
					for(let valu of tempCit) {
						if(valu.id == b) {
							return valu.cname;
							break
						}
					}
				}
			}
		},
		changeProvince(e) {
			let id = e.target.value;
			this.districtsArr = null;
			if (id) {
				this.citiesArr = this.provincesArr[id]['citys'];
				this.AjaxPro = this.provincesArr[id]['id'];
				this.province = $('.province').find('option:selected').text();
			}else{
				this.AjaxPro='';
			}
		},
		changeCities(e) {
			let id = e.target.value;
			if(id) {
				this.districtsArr = this.citiesArr[id]['districts'];
				this.AjaxCit = this.citiesArr[id]['id'];
				this.city = $('.city').find('option:selected').text();
			} else {
				this.AjaxCit='';
			}
			this.changeType()
		},
		search() {
			this.baseGet({keyword: this.address,_size:this.size});
		},
		queryName(a){
      this[a.queryName] = a.value;
      this.changeType();
    }
	},
	events:{
	  selectCitys(val){
      this.AjaxPro = val.provinceValue;
      this.AjaxCit = val.cityValue;
      this.changeType();
    },
    dropMenuChange(val){
      this.queryName(val)
    },
		switchPage(val){
		  console.log('switchPage')
		},
		jumpPage(val) {
		  this.baseGet({_index: val,_size:this.size});
    }
	},
	watch: {
	},
	components: {
		Pagination: Pagination
	}
}
module.exports = StayLists;


