const template = require('./all-survey.tpl');
require('./all-survey.scss');
require('appUtil/ajaxSetting');
const echarts = require('echarts');
const Data = require('./data.js');
const ajaxHttp = require('appUtil/ajaxHttp');
const {
  apiPath
} = require('appService/api');
const AllSurvey = {
  template,
  data() {
    return {
      optioncharge: {
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: ['', '', '', '', '', ''],
          splitLine: {
            show: false
          },
          axisTick: {
            alignWithLabel: false,
            length: 1
          },
          axisLabel: {
            show: true
          },
          axisLine: {
            lineStyle: {
              color: "#B3B3B3"
            }
          },
        }],
        yAxis: [{
            type: 'value',
            splitLine: {
              show: true
            },
            splitArea: {
              show: false,
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            },
          },

        ],
        series: [{
          name: '充电金额',
          type: 'bar',
          label: {
            normal: {
              formatter: '¥{c}',
              show: true,
              position: 'top',
              textStyle: {
                color: "#333",
                fontSize: 14,
                fontWeight: 'bold'
              }
            }
          },
          itemStyle: {
            normal: {
              color: function(d) {
                return "#00C5DB";
              }
            }
          },
          barWidth: '35%',
          data: [0,0,0,0,0,0],
        }]
      },
      chargeEcharts: {},
      chargeinfoEcharts: {},
      optionchargeinfo: {
        xAxis: [{
          show: false
        }],
        yAxis: [{
          offset:'40',
          type: 'category',
          data: [
          {
            value:'本月',
            textStyle:{
                fontSize:14
            }
          },
          {
            value:'今天',
            textStyle:{
                fontSize:14
            }
            }],
          splitArea: {
            show: false,
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
        }],
        series: [{
          type: 'bar',
          data: [{
            value: 0,
            itemStyle: {
              normal: {
                color: '#536477',
              }
            }
          }, {
            value: 0,
            itemStyle: {
              normal: {
                color: '#62A9B7'
              }
            }
          }],
          label:{
          normal:{
              formatter:'{c} 度',
              show:true,
              position:'right',
              textStyle:{
                  color:"#333",
                  fontSize:16
              },
          },
      },
        }]
      },
      ordersumEcharts:{},
      optionordersum:{
        xAxis: [{
          show: false
        }],
        yAxis: [{
          offset:'40',
          type: 'category',
          data: [
          {
            value:'本月',
            textStyle:{
                fontSize:14
            }
          },
          {
            value:'今天',
            textStyle:{
                fontSize:14
            }
            }],
          splitArea: {
            show: false,
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
        }],
        series: [{
          type: 'bar',
          data: [{
            value: 0,
            itemStyle: {
              normal: {
                color: '#FAB85A',
              }
            }
          }, {
            value: 0,
            itemStyle: {
              normal: {
                color: '#FC9356'
              }
            }
          }],
          label:{
          normal:{
              formatter:'¥ {c}',
              show:true,
              position:'right',
              textStyle:{
                  color:"#333",
                  fontSize:16
              },
          },
      },
        }]
      },
      monitorEcharts:{},
      optionmonitor: {
        color:['#62A9B8', '#536477','#EF6657','#A4ADB6'],
        tooltip: {
          trigger: 'item',
          formatter: "{a}<br/>{b} : {c} ({d}%)"
        },
        series: [
          {
          name: '车位监控',
          type: 'pie',
          radius: ['50%','70%'],
          center: ['50%', '50%'],
          data: [{
            value: 335,
            name: '占用',
          }, {
            value: 310,
            name: '空闲'
          }, {
            value: 234,
            name: '故障'
          }, {
            value: 135,
            name: '离线'
          }],
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            normal:{ 
                label:{ 
                   show: true, 
                   formatter: '{b} : {c}' ,
                   textStyle:{
                      fontSize:'16'
                    }
                }, 
                labelLine :{show:true}
            } 
          }
        },
        {
            type:'pie',
            radius:['40%','40%'],
            selectedMode: 'single',
            center:['50%', '50%'],
            label: {
                normal: {
                    show: true,
                    position: 'center',
                    textStyle:{
                      fontSize:'16'
                    }
                },
            },
            data:[
            {
              name:'0',
              itemStyle:{
                  normal:{
                    color:'#000',
                    }
              },
              tooltip: {
                  show: false
              },
            },
            {
              name:'总监控',
              itemStyle:{
                  normal:{color:'#999'}
              },
              tooltip: {
                  show: false
              },
            }
            ],
            itemStyle:{ 
            normal:{ 
                  label:{ 
                    show: true, 
                    formatter: '{b}' 
                  }, 
                  labelLine :{show:true} 
                } 
            } 
          },
        ]
      },
      warnEcharts:{},
      optionwarn: {
        color:["#EF6657","#FC9356","#FAB85A"],
        tooltip: {
          trigger: 'item',
          formatter: "{a}<br/>{b} : {c} ({d}%)"
        },
        series: [
          {
          name: '正在告警',
          type: 'pie',
          radius: ['50%','70%'],
          center: ['50%', '50%'],
          data: [{
            value: 335,
            name: '一级',
          }, {
            value: 310,
            name: '二级'
          }, {
            value: 234,
            name: '三级'
          }],
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            normal:{ 
                label:{ 
                   show: true, 
                   formatter: '{b} : {c}',
                   textStyle:{
                      fontSize:'16'
                    }
                }, 
                labelLine :{show:true}
            } 
          }
        },
        {
            type:'pie',
            radius:['40%','40%'],
            selectedMode: 'single', //单一选中模式 
            center:['50%', '50%'],
            label: {
                normal: {
                    show: true,
                    position: 'center',
                    textStyle:{
                      fontSize:'16'
                    }
                },
            },
            data:[
            {
              name:'0',
              itemStyle:{
                  normal:{
                    color:'#000',
                    }
              },
              tooltip: {
                  show: false
              },
            },
            {
              name:'总告警',
              itemStyle:{
                  normal:{color:'#999'}
              },
              tooltip: {
                  show: false
              },
            }
            ],
            itemStyle:{ 
            normal:{ 
                  label:{ 
                    show: true, 
                    formatter: '{b}' 
                  }, 
                  labelLine :{show:true} 
                } 
            } 
          },
        ]
      },
      monitorObj:[
          {key:'accuptionNum',text:'占用'},
          {key:'freeNum',text:'空闲'},
          {key:'faultNum',text:'故障'},
          {key:'offLineNum',text:'离线'},
        ],
     warnObj:[
        {key:'levelOneNum',text:'一级'},
        {key:'levelTwoNum',text:'二级'},
        {key:'levelThreeNum',text:'三级'}
     ],
     //充电排名
     chargerRankList:[],
     //电量信息
     stasElectricityList:[],
     //订单金额
     stasPricesList:[],
     //运维告警
     warnimgVo:{},
     //运维概览
     chargersStatusSummaryVo:{},
     siteInfo:{},
     destroyBol:true,
    }
  },
  ready() {
    this.down();
  },
  beforeDestroy(){
    this.destroyBol = false;
  },
  methods: {
    down(){
      ajaxHttp(apiPath.site.survey.applicationSurvey,{
          type:'get',
      }).then((res)=>{
        if(res.code==0){
          let datas = res.data;
          let arrs = ['siteInfo','chargerRankList','stasElectricityList','stasPricesList','chargersStatusSummaryVo','warnimgVo'];
          for (let i in arrs) {
          	 this[arrs[i]] = datas[arrs[i]];
          }
          if(this.destroyBol){
              setTimeout(()=>{
                this.chartgeOption('chargeEcharts', 'optioncharge', this.chargerRankList);
                this.electricOption('chargeinfoEcharts','optionchargeinfo',this.stasElectricityList);
                this.orderOption('ordersumEcharts','optionordersum',this.stasPricesList);
                this.warnOption('monitorEcharts','optionmonitor',this.chargersStatusSummaryVo,this.chargersStatusSummaryVo.totalNum,this.monitorObj);
                this.warnOption('warnEcharts','optionwarn',this.warnimgVo,23,this.warnObj);
            },200)
          }
        }
      }).catch((res)=>{
//      tip(res.message);
      })
    },
    //a=>DOMId和echarts的Obj;
    //b=>echarts的Option;
    //c=>需修改的Data数据;
    //d=>需单独修改的Data数据;
    //e=>需遍历的Obj属性;
    echartInit(a,b){
      this[a] = echarts.init(document.getElementById(a));
      this[a].setOption(this[b]);
    },
    chartgeOption(a, b, c) {
      let temp1 = [];
      let temp2 = [];
      for(let i in c) {
        temp1[i] = c[i].stationName;
        temp2[i] = c[i].sumElectricity/100;
      }
      this[b].xAxis[0].data = temp1;
      this[b].series[0].data = temp2;
      this.echartInit(a,b);
    },
    orderOption(a,b,c){
      let index = 0;
      for (var i=c.length-1;i>=0;i--) {
      	 this[b].series[0].data[i].value = c[index].sumPrice;
      	 index++
      }
       this.echartInit(a,b);
    },
    electricOption(a,b,c){
      let index = 0;
      for (var i=c.length-2;i>=0;i--) {
         this[b].series[0].data[i].value = c[index].sumElectricity;
         index++
      }
      this.echartInit(a,b);
    },
    warnOption(a,b,c,d,e){
      let temp = [];
      for(let i in e){
        temp[i] ={};
        temp[i].name = e[i].text;
        temp[i].value = c[e[i].key];
      }
      this[b].series[0].data = temp;
      this[b].series[1].data[0].name = d;
      this.echartInit(a,b);
    },
  }
}
module.exports = AllSurvey;


