require('components/common/table-data/table-data');
require('components/base/pagination/pagination');
require('components/common/search-tool/search-tool');
require('components/common/data-picker/data-picker');

const ajaxHttp = require('appUtil/ajaxHttp');
const tip = require('components/base/pop/tip');
const alert = require('components/base/pop/alert');
require('components/base/pop/pop');
const {
  apiPath
} = require('serviceDir/saasFront/api');
const {
  dropMenu: dropMenuEvent
} = require('components/config/event.json');
const {
  time
} = require('src/common/utils/date');


require('./card-list.scss');
const template = require('./card-list.tpl');

const CardList = {
  name: "card-list",

  template,

  data() {
    let cardStatus = this.$route.query.cardStatus ? this.$route.query.cardStatus : -1;
    let beginTime = this.$route.query.beginTime ? this.$route.query.beginTime : '';
    let endTime = this.$route.query.endTime ? this.$route.query.endTime : '';
    let keyWord = this.$route.query.keyWord ? this.$route.query.keyWord : '';
    let size = this.$route.query['_size'] ? this.$route.query['_size'] : 20;

  	return {
  		"queryTime": {
        "beginTime": beginTime,
        "endTime": endTime,
      },
      "now": time(new Date().getTime()),
      "menuData": [{
        "text": "卡号",
        "value": ""
      }, {
        "text": "手机号",
        "value": ""
      }],
      "placeholder": "请输入卡号、手机号",
      "thItems": ["序号", "卡号", "激活码", "客户手机号","状态", "创建时间", "操作"],
      "url": apiPath.client.cardList,
      "searchParams": {
        "_size": size
      },
      "queryName": "keyWord",
      "newCard": {
      	"cardNo": {
      		"validate": true,
      		"value": "",
      		"require": true,
      		"error": "请输入卡号",
          "maxLength": 50,
          "maxLengthError": "卡号最大长度为50"
      	},
      	"phone": {
      		"validate": true,
      		"value": "",
      		"require": false,
      		"regx": /^1[34578]\d{9}$/,
      		"regxError": "手机号码不正确"
      	},
      	"activationCode": {
      		"validate": true,
      		"value": "",
      		"require": false,
          "maxLength": 50,
          "maxLengthError": "激活码最大长度为50"
      	}
      },
      "error": {
      	"addMsg": "",
      	"activeMsg": ""
      },
      "userTip": null,
      "btnName": {
      	"add": "保存",
      	"make": "制作"
      },
      "send": {
      	"add": false,
      	"confirmAdd": false,
      	"active": false,
      	"frost": false
      },
      "activeData": {
      	"cardNo": {
      		"validate": false,
      		"value": null
      	},
      	"phone": {
      		"validate": true,
      		"value": "",
      		"require": true,
      		"error": "请输入手机号",
      		"regx": /^1[34578]\d{9}$/,
      		"regxError": "手机号码不正确"
      	}
      },
      "activeBtnGroup": {
      	"btnName": "确定"
      },
      "frostBtnGroup": {
      	"btnName": "确定"
      },
      "frostData": {
      	"cardNo": "",
      	"phone": "",
      	"resMsg": ""
      },
      "cardStatus": cardStatus,
      "keyWord": keyWord
  	}
  },

  route: {
    data(transition) {
      let cardStatus = this.$route.query.cardStatus ? this.$route.query.cardStatus : -1;
      let beginTime = this.$route.query.beginTime ? this.$route.query.beginTime : '';
      let endTime = this.$route.query.endTime ? this.$route.query.endTime : '';
      let keyWord = this.$route.query.keyWord ? this.$route.query.keyWord : '';
      let size = this.$route.query['_size'] ? this.$route.query['_size'] : 20;
      this.queryTime = {
        "beginTime": beginTime,
        "endTime": endTime,
      };

      this.cardStatus = cardStatus;
      this.keyWord = keyWord;

      let params = {
        cardStatus: this.cardStatus, //支付状态, （1:待支付; 2:已支付;3: 支付失败）
        beginTime: this.beginTime,
        endTime: this.endTime,
        _size: size,
        keyWord: keyWord
      }

      this.fetch(params);
      
      transition.next();
    },
    waitForData: true,
    canReuse: false
  },

  methods: {
  	 getParams() {
      let params = {
        cardStatus: this.$refs.cardstatus.val(), //支付状态, （1:待支付; 2:已支付;3: 支付失败）
        beginTime: this.queryTime.beginTime,
        endTime: this.queryTime.endTime,
        _size: 20,
        keyWord: this.$refs.searchTool.queryOpt['keyWord']
      }

      return params;
    },

  	search() {
  		let params = this.getParams();
      this.$router.go({
        path: '/page/client/card-list',
        query: params
      });

  	},

    fetch(params) {
 
      this.searchParams = Object.assign(this.searchParams, params);
      this.$refs.tableData.fetch();

    },

  	empty(val) {
  		return val ? val : '--';

  	},

  	getStatus(code) {
  		//10000未激活，10001已激活，10002已挂失
  		let codeArr = {
  			"10000": {
  				"text": "激活",
  				"link": true
  			},
  			"10001": {
  				"text": "挂失",
  				"link": true
  			},
  			"10002": {
  				"text": "已挂失",
  				"link": false
  			}
  		};

  		return codeArr[code];
  	},

    getStatusTxt(code) {
      let codeArr = {
        "10000": {
          "text": "未激活",
          "link": true
        },
        "10001": {
          "text": "已激活",
          "link": true
        },
        "10002": {
          "text": "已挂失",
          "link": false
        }
      };

      return codeArr[code];
    },

  	op(code, cardNo, phone) {
  		if (code == 10002) {
  			return false;
  		}

  		if (cardNo) {
  			this.activeData.cardNo.value = cardNo;
  			this.frostData.cardNo = cardNo;
  		}

  		if (phone) {
  			this.frostData.phone = phone;
  		}
  		
  		if (code == 10000) {
  			this.showPop('activecardpop');
  		}

  		if (code == 10001) {
  			this.showPop('frostcardpop');
  		}
  		
  	},

  	validate(data) {

  		let res = {
  			bool: true,
  			msg: ''
  		};

  		for (let k in data) {
  			let o = data[k];
  	
  			if (o.validate) {
  				if (o && o.require && o.value.length === 0) {
	  				res.bool = false;
	  				res.msg = o.error;
	  				return res;
	  			}

          if (o && o.value.length > 0 && o.maxLength && o.value.length > o.maxLength) {
            res.bool = false;
            res.msg = o.maxLengthError;
            return res;
          }

	  			if (o && o.value.length > 0 && o.regx && !o.regx.test(o.value)) {
	  				res.bool = false;
	  				res.msg = o.regxError;
	  				return res;
  				}
  			}
  			
  		}

  		return res;

  	},

  	showPop(name) {
      this.$refs[name].show();
    },

    hidePop(name) {
      this.$refs[name].hide();
    },

  	addCardPop() {
  		this.showPop('addcardpop');
  	},

  	makeCardPop() {
  		this.showPop('makecardpop');
  	},

  	confirmAddCard() {
  		if (this.send.confirmAdd) {
  			return false;
  		}

  		let res = this.validate(this.newCard);

  		if (!res.bool) {
  			this.error.addMsg = res.msg;
  			return false;
  		}

  		let data = {
  			"cardNo": this.newCard.cardNo.value,
  			"phone": this.newCard.phone.value,
  			"activationCode": this.newCard.activationCode.value
  		};

  		this.btnName.add = '正在校验...';
  		this.send.confirmAdd = true;

  		ajaxHttp(apiPath.client.cardAddConfirm, {
  			data: data
  		}).then((res)=> {
  			this.send.confirmAdd = false;
  			this.btnName.add = '保存';
  			this.userTip = this.newCard.phone.value ? (res.userId ? `(账户余额：${this.user.balance})` : '(未注册)') : '';
  			this.hidePop('addcardpop');
  			this.showPop('confirmaddcard');
  		}).catch((res)=> {
  			this.send.confirmAdd = false;
  			this.btnName.add = '保存';
  			this.error.addMsg = res.message;
  		});
  	},

  	addCardOk() {
  		if (this.send.add) {
  			return false;
  		}
  		this.btnName.add = "正在添加...";
  		let data = {
  			"cardNo": this.newCard.cardNo.value,
  			"phone": this.newCard.phone.value,
  			"activationCode": this.newCard.activationCode.value
  		};

  		this.send.add = true;

  		ajaxHttp(apiPath.client.cardAdd, {
  			"type": "post",
  			"data": data
  		}).then((res)=> {
  			this.send.add = false;
  			this.hidePop('confirmaddcard');
  			tip('添加卡片成功');
  			this.btnName.add = "保存";
        this.search();
  		}).catch((res)=> {
  			this.send.add = false;
  			this.hidePop('confirmaddcard');
  			tip(res.message);
  			this.btnName.add = "保存";
  		});

  	},

  	confirmMakeCard() {

  	},

  	makeCardOk() {

  	},

  	confirmActiveCard() {
  		let res = this.validate(this.activeData);
  		if (!res.bool) {
  			this.error.activeMsg = res.msg;
  			return false;
  		}

  		this.hidePop('activecardpop');
  		this.showPop('confirmactivecardpop');

  	},

  	activeCardOk() {
  		if (this.send.active) {
  			return false;
  		}

  		let data = {
  			"cardNo": this.activeData.cardNo.value,
  			"mobile": this.activeData.phone.value,
  		}
  		this.activeBtnGroup.btnName = '正在激活...';
  		this.send.active = true;
  
  		ajaxHttp(apiPath.client.cardActive, {
  			type: "post",
  			data: data
  		}).then((res)=> {
  			this.send.active = false;
  			this.hidePop('confirmactivecardpop');
  			tip(res.message);
  			this.activeBtnGroup.btnName = '确定';
        this.search();
  		}).catch((res)=> {
  			this.send.active = false;
  			tip(res.message);
  			this.activeBtnGroup.btnName = '确定';
  		});
  	},

  	frostCardOk() {
  		if (this.send.frost) {
  			return false;
  		}

  		this.send.frost = true;

  		let data = {
  			"cardNo": this.frostData.cardNo
  		};

  		ajaxHttp(apiPath.client.cardFrost, {
  			"type": "post",
  			"data": data
  		}).then((res)=> {
  			this.send.frost = false;
  			this.frostData.resMsg = res.data;
  			this.hidePop('frostcardpop');
  			this.showPop('frosttips');
  			setTimeout(()=> {
  				this.hidePop('frosttips');
  			}, 2500);
        this.search();
  		}).catch((res)=> {
  			this.send.frost = false;
  			this.hidePop('frostcardpop');
  			tip(res.message);
  		});
  	}
  },

  watch: {
  	"newCard": {
  		"handler": function(val, oldval) {
  			if (val) {
  				this.error.addMsg = "";
  			}
  		},
  		"deep": true
  	},
  	"activeData.phone.value": function(val, oldval) {
  		this.error.activeMsg = "";
  	}
  }
}

module.exports = CardList;


