const Vue = require('vue');
const Vuex = require('vuex');

const common = require('src/common/vuex/module/common');
const headerLayout = require('./module/headerLayout');
const marketing = require('./module/marketing');
const login = require('./module/login');

Vue.use(Vuex);

module.exports = new Vuex.Store({
  modules: {
    common,
    headerLayout,
    marketing,
    login
  }
})


